html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  /* 
  scroll-behavior: smooth; 
     , 
      , 
     project-page.html
  */
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border:0; 
  outline:0;
  box-sizing: inherit;
}
*:focus {outline: 0;}

body {
  font-family: "SFUIDisplay", sans-serif;
  font-size: 16px;
  background: #ffffff;
  // overflow: hidden;
  overflow-x: hidden;
  // display: flex;
  // flex-direction: column;
  // justify-content: space-between;
  // min-height: 100vh;
}

header {
  position: relative;
  z-index: 6;
}

img {
  max-width: 100%;
}

button,
input,
label {
  font-family: "SFUIDisplay", sans-serif;
  outline: none;
}
