.section-why-us {
    padding: 90px 0;

    &__wrap {
        max-width: 1220px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;

        .promo-gallery,
        .example,
        .your-budget,
        .save,
        .stages {
            border-bottom: 1px solid var(--gray-300);
        }
    }

    &__text {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto 40px auto;
        padding: 0 40px;
    }

    &__title {
        @include font(144, 500);
        text-transform: uppercase;
        margin-bottom: 12px;
    }

    &__subtitle {
        @include font(20, 500);
    }

    .promo-gallery {
        margin-bottom: 0;
        padding: 90px 0 120px;

        &__head {
            max-width: 1060px;
            width: 100%;
            margin-left: auto;
            margin-bottom: 60px;
        }

        &__title {
            max-width: 820px;
            width: 100%;
            @include font(62, 500);
            margin-bottom: 20px;
        }

        &__text {
            max-width: 700px;
            width: 100%;
            @include font(20, 500);
            color: var(--gray-700);
        }

        .promo-slide {
            &__picture {
                img {
                    width: 980px;
                    height: 620px;
                    object-fit: cover;
                    object-position: center center;
                }
            }
        }

        &__buttons {
            padding-top: 40px;
        }
    }

    .example {
        padding: 90px 0 140px;
        margin-bottom: 0;

        &__wrap {
            padding: initial;
        }

        &__head {
            margin: initial;
            text-align: left;
            margin-bottom: 60px;
        }

        &__title {
            @include font(62, 500);
            margin-bottom: 20px;
        }

        &__text {
            max-width: 580px;
            width: 100%;
            @include font(20, 500);
            color: var(--gray-700);
        }
    }

    @include to(1200px) {
        padding: 60px 0;

        &__wrap {
            padding: 0 20px;
        }

        &__text {
            padding: 0 20px;
        }

        &__title {
            @include font(80, 500);
        }

        &__subtitle {
            max-width: 508px;
            width: 100%;
        }

        .promo-gallery {
            padding: 60px 0 60px;

            .promo-slide {
                &__picture {
                    img {
                        width: 100%;
                        height: auto;
                        object-fit: contain;
                    }
                }
            }

            &__buttons {
                margin-top: 0;
                padding-top: 18px;
            }
        }

        .example {
            padding: 70px 0 130px;

            &__blocks {
                gap: 20px;
            }
        }
    }

    @include to(768px) {
        padding: 50px 0;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500);
        }

        &__text {
            padding: 0 10px;
            margin-bottom: 10px;
        }

        .promo-gallery {
            padding: 50px 0 50px;
        }

        .example {
            padding: 60px 0 120px;

            &__blocks {
                gap: 10px;
            }
        }

        .promo-gallery,
        .example {
            &__head {
                margin-bottom: 40px;
            }

            &__title {
                @include font(42, 500);
            }

            &__text {
                @include font(18, 500);
            }
        }
    }

    @include to(520px) {
        &__subtitle {
            @include font(16, 500);
        }

        .example {
            padding: 50px 0 60px;
        }

        .promo-gallery,
        .example {
            &__head {
                margin-bottom: 30px;
            }

            &__title {
                @include font(32, 500);
                margin-bottom: 15px;
            }

            &__text {
                @include font(16, 500);
            }
        }

    }
}