.section-dream {
    padding: 90px 0 90px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(144, 500);
        text-transform: uppercase;
        margin-bottom: 12px;
        transition: 0.3s;
    }

    &__subtitle {
        max-width: 550px;
        @include font(20, 500);
    }

    @include to(1060px) {
        padding: 60px 0 60px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(80, 500);
        }
    }

    @include to(768px) {
        padding: 50px 0 50px;

        &__wrap {
            padding: 0 10px;
        }
        
        &__title {
            @include font(42, 500);
        }

        &__subtitle {
            max-width: 550px;
            @include font(20, 400);
        }
    }

    @include to(670px) {

        &__title {
            @include font(40, 500);
        }
        
        &__subtitle {
            @include font(16, 400);
        }
    }
}