.trust-wrap {
    padding-top: 60px;
    position: relative;
    background: #fff;
}

.trust {
    max-width: 1300px !important;
    margin-left: auto;
    margin-right: auto;
}

.trust-title {
    @include font(92, 500, 1);
    width: 100%;
    text-transform: uppercase;
    margin-bottom: 30px;
}

.trust-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.trust-info-e {
    width: calc(50% - 15px);
    margin-bottom: 30px;
    position: relative;
}

.trust-info-e.e3,
.trust-info-e.e4 {
    margin-bottom: 0px;
}

.trust-info-e-text {
    padding: 20px;
}

.trust-info-e-text.e4,
.trust-info-e-text.e5 {
    padding-left: 0;
}

.trust-info-e-text.e2,
.trust-info-e-text.e3 {
    position: absolute;
}

.trust-info-e-text-title {
    @include font(36);
    margin-bottom: 15px;
}

.trust-info-e-text-desc {
    max-width: 430px;
    font-size: 16px;
    font-weight: 400;
    color: #6e6e6e;
}

@media only screen and (max-width: 1023px) {
    .trust-title {
        font-size: 52px;
    }

    .trust-info-e {
        width: calc(50% - 10px);
        margin-bottom: 20px;
    }

    .trust-info-e-text-title {
        font-size: 30px;
    }
}

@media only screen and (max-width: 799px) {
    .trust-info-e-text-title {
        font-size: 23px;
    }
}

@media only screen and (max-width: 599px) {
    .trust-title {
        font-size: 40px;
    }

    .trust-info-e {
        width: 100%;
        margin-bottom: 20px !important;
    }
    .trust-info-e-text-title {
        font-size: 23px;
    }

    .trust-info-e-text-desc {
        max-width: 100%;
    }
}
