.section-callback-form
  margin-top: 0
  padding-top: 50px
  padding-bottom: 50px
  +to(520px)
    margin-top: 0
  &__wrap
    display: flex
    flex-direction: column
    align-items: center
  &__head
    width: 700px
    +to(768px)
      width: 100%
  &__title
    @include font(92, 500, 1)
    margin-bottom: 10px
    +to(768px)
      font-size: 72px
    +to(520px)
      font-size: 40px
  &__text
    @include font(20, 400, 1.4)
    margin-bottom: 20px
    +to(520px)
      font-size: 16px
  &__form
    width: 700px
    +to(768px)
      width: 100%

  .callback-form
    display: flex
    flex-direction: column
    gap: 20px
    &__line
      display: flex
      flex-direction: column
      position: relative
      gap: 5px
      // &:after
      //   content: ''
      //   position: absolute
      //   bottom: -1px
      //   left: 0
      //   width: 0%
      //   height: 1px
      //   display: block
      //   background: black
      // &:active, &:focus
      //   .callback-form__line:after
      //     width: 100%
    &__label
      @include font(16)
    &__input
      padding-bottom: 4px
      padding-top: 4px
      border-bottom: 1px solid rgba(0,0,0,.1)
      transition: .3s
      position: relative
      display: block
      &:active, &:focus
        border-bottom: 1px solid rgba(0,0,0,1)
    &__btn
      margin-top: 20px
      padding: 14px 40px
      width: fit-content
      position: relative
      text-transform: uppercase
      transition: .3s
      color: white
      @include font(12, 500)
      letter-spacing: 1.2px
      background: #ddd
      z-index: 0
      &:hover
        color: black
        &:before
          transform: scaleX(0)
      &::before
        content: ''
        background: black
        position: absolute
        width: 100%
        height: calc(100% + 1px)
        top: 0
        left: 0
        z-index: -1
        transform: scaleX(1)
        transform-origin: right
        transition: .3s
    &__help
      @include font(12, 500)
      color: #a6a6a6
