    }
    .header-hamburget {
        display: block;
        width: 26px;
        height: 26px;
        position: relative;
        z-index: 1002;
        cursor: pointer;
    }
    .header-hamburget-line {
        transition-duration: .5s;
        border-top: 2px solid #fff;
        position: absolute;
        left: 0;
        width: 26px;
    }
    .header-hamburget-line.e1 {
        top: 5px;
    }
    .header-hamburget-line.e2 {
        top: 12px;
    }
    .header-hamburget-line.e3 {
        top: 19px;
    }
    header.mini .header-hamburget-line {
        top: 12px;
    }
    header.mini .header-hamburget-line.e1 {
        transform: rotate(-45deg);
    }
    header.mini .header-hamburget-line.e2 {
        transform: rotate(45deg);
    }
    header.mini .header-hamburget-line.e3 {
        display: none;
    }
    .header-mob-menu {
        display: block;
        position: fixed;
        z-index: 1001;
        left: 0;
        top: 0;
        transform: translate(-110%,0);
        height: 100vh;
        width: 100vw;
        box-sizing: border-box;
        padding: 150px 20px 20px;
        transition: 500ms;
        background: #000;
        color: #fff;
        font-weight: 400;
        overflow: auto;
    }
    header.mini .header-mob-menu {
        transform: translate(0,0);
