.header-new {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1090;
    background-color: transparent;
    height: 78px;

    &.active {
        backdrop-filter: blur(25px);
        -webkit-backdrop-filter: blur(25px);
        background-color: rgba(0, 0, 0, .25);

        .header-new__wrap {
            border-bottom: 1px solid transparent;
        }

        .header-new__logo {
            border-right: initial;
            margin-right: 1px;
        }

        .header-new__btn {
            color: var(--black);
            background-color: rgba(255, 255, 255, 1);
        }

        .header-new-hamburger {

            &__btn {
                &-wrap {
                    span {
                        background-color: var(--white);
                    }
                }
            }
        }
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        padding: 0 40px;
        height: 78px;
        border-bottom: 1px solid var(--white);
        transition: .2s;
        position: relative;
    }

    &__overflow {
        width: 100%;
        transition: .2s;
    }

    &__scroll {
        width: 100%;
        transition: .2s;
    }

    &__blocks {
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding-left: 40px;
    }

    &__block {
        &--menu {}

        &--info {}
    }

    &__logo {
        height: 78px;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-right: 40px;
        border-right: 1px solid var(--white);
        transition: .2s;

        svg,
        img {
            display: flex;
            width: 170px;
            height: 57px;
        }

        svg {
            color: var(--white);
        }
    }

    &-menu {
        &__list {
            display: flex;
            align-items: center;
            gap: 24px;

            &-item {

                &:hover {
                    .header-new-menu-sub {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }

            &-link {
                display: flex;
                align-items: center;
                gap: 6px;
                padding: 32px 0;
                @include font(14, 400, 1);
                color: var(--white);
                text-transform: uppercase;
                position: relative;
                transition: .2s;

                &::before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    right: 50%;
                    bottom: 26px;
                    z-index: 1;
                    border-top: 2px solid var(--white);
                    transition: .3s;
                }

                svg {
                    display: flex;
                    width: 12px;
                    height: 12px;
                    transform: scaleY(1);
                    transition: .1s;
                }

                &:hover {
                    &::before {
                        left: 0;
                        right: 0;
                    }

                }
            }
        }

        &-sub {
            width: 100%;
            height: 100vh;
            background-color: rgba(0, 0, 0, .4);
            position: absolute;
            left: 0;
            right: 0;
            top: 100%;
            z-index: 1;
            opacity: 0;
            visibility: hidden;
            pointer-events: none;
            margin-top: 1px;
            transition: .2s;

            &__wrap {
                display: flex;
                justify-content: flex-end;
                width: 57.8%;
                padding: 17px 0 0 20px;
                margin-left: 251px;
                pointer-events: all;
            }

            &__blocks {
                display: flex;
                justify-content: space-between;
                gap: 50px;
                padding: 30px 30px 30px;
                width: 800px;
                height: 100%;
                border-radius: 10px;
                background-color: var(--white);
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
                position: relative;
            }

            &__block {
                flex: 1 1 100%;

                &--img {
                    flex: 0 0 250px;
                }

                &-img {
                    width: 100%;
                    height: 150px;
                    border-radius: 5px;
                    overflow: hidden;
                    margin-bottom: 10px;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                &-title {
                    @include font(14, 500, 1);
                    margin-bottom: 8px;
                }

                &-text {
                    @include font(13, 400, 1);
                    color: var(--gray-700);
                }
            }

            &__title {
                @include font(18, 500, 1);
                margin-bottom: 20px;
            }

            &__list {
                display: flex;
                flex-direction: column;
                gap: 10px;

                li {
                    a {
                        padding: 4px 0;
                        @include font(14, 500, 1.3);
                        color: var(--gray-700);
                        width: 100%;
                        position: relative;
                        transition: .2s;

                        &::before {
                            content: "";
                            position: absolute;
                            left: 50%;
                            right: 50%;
                            bottom: 0;
                            border-top: 2px solid var(--black);
                            transition: .3s;
                        }

                        &:hover {
                            color: var(--black);

                            &::before {
                                left: 0;
                                right: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    &-info {
        display: flex;
        align-items: center;
        height: 78px;

        &__items {
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }

        &__item {
            &:not(:first-child) {
                margin-left: 25px;
            }
        }

        &__tel {
            padding: 4px 0;
            @include font(14, 400, 1);
            color: var(--white);
            position: relative;
            transition: .2s;

            &::before {
                content: "";
                position: absolute;
                left: 50%;
                right: 50%;
                bottom: 0;
                z-index: 1;
                border-top: 2px solid var(--white);
                transition: .3s;
            }

            &:hover {
                &::before {
                    left: 0;
                    right: 0;
                }
            }
        }

        &-social {
            &__list {
                display: flex;
                align-items: center;
                gap: 24px;

                svg {
                    display: flex;
                    width: 20px;
                    height: 20px;
                }

                li {
                    position: relative;

                    a {
                        color: var(--white);
                    }

                    &:not(:first-child) {
                        &::before {
                            content: "";
                            position: absolute;
                            top: 50%;
                            left: -12px;
                            z-index: 1;
                            transform: translateY(-50%);
                            height: 12px;
                            width: 1px;
                            background-color: var(--white);
                        }
                    }
                }
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 180px;
        height: 43px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        backdrop-filter: blur(25px);
        -webkit-backdrop-filter: blur(25px);
        background-color: rgba(255, 255, 255, 0.3);
        color: var(--white);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--black);
        }

        &:hover {
            span {
                color: var(--white);
            }

            &::before {
                width: 100%;
            }
        }
    }

    &-hamburger {

        &__btn {
            display: none;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 5;
            height: 55px;
            padding: 0 20px;
            transition: .2s;

            &-wrap {
                width: 24px;
                height: 16px;
                position: relative;

                span {
                    display: block;
                    width: 100%;
                    height: 2px;
                    border-radius: 10px;
                    background-color: var(--white);
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%) rotate(0deg);
                    transition: .3s;
                }

                span:nth-child(1) {
                    top: 0;
                }

                span:nth-child(2) {
                    top: 50%;
                    transform: translate(-50%, -50%) rotate(0deg);
                }

                span:nth-child(3) {
                    bottom: 0;
                }

                &.active {
                    span:nth-child(1) {
                        top: 50%;
                        transform: translate(-50%, -50%) rotate(45deg);
                    }

                    span:nth-child(2) {
                        width: 0;
                        opacity: 0;
                    }

                    span:nth-child(3) {
                        bottom: initial;
                        top: 50%;
                        transform: translate(-50%, -50%) rotate(-45deg);
                    }
                }
            }
        }
    }


    @include to(1200px) {
        &__wrap {
            padding: 0 20px;
        }
    }

    @include to(1180px) {
        height: 55px;

        &.active.mobile-active {
            background-color: rgba(0, 0, 0, 0.5);
        }

        &.mobile-active {
            height: 100%;
            background-color: rgba(0, 0, 0, 0.5);

            .header-new__wrap {
                background-color: var(--white);
                border-bottom: 1px solid #eaeaea;
            }

            .header-new__logo {
                padding-left: 21px;

                svg,
                img {
                    color: var(--black);
                }
            }

            .header-new__overflow {
                position: absolute;
                top: 53px;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
                height: 100svh;
            }

            .header-new__scroll {
                display: flex;
                justify-content: center;
                border-top: 1px solid var(--white);
                height: 720px;
                overflow-x: hidden;
            }

            .header-new__blocks {
                background-color: var(--white);
                width: 100%;
                height: max-content;
                margin: 0 5px;
                flex-direction: column;
                justify-content: flex-start;
                border-bottom-right-radius: 12px;
                border-bottom-left-radius: 12px;
                transition: .2s;
            }

            .header-new__block {
                &--menu {
                    display: block;
                }
            }

            .header-new-menu {
                border-top: 1px solid #F4F4F4;

                &__list {
                    width: 100%;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 0;

                    .js-header-new-menu-sub-btn {
                        &.active {
                            background-color: #F4F4F4;
                            border-bottom: 1px solid transparent;

                            &::before {
                                transform: scaleY(-1);
                            }

                            &::after {
                                display: block;
                            }

                            .header-new-menu-sub {
                                display: block;
                                opacity: 1;
                                visibility: visible;
                            }
                        }
                    }

                    &-item {
                        width: 100%;

                        &--arrow {
                            position: relative;
                            pointer-events: all;

                            &::before {
                                content: "";
                                position: absolute;
                                top: 0;
                                right: 0;
                                z-index: 1;
                                width: 57.4px;
                                height: 51px;
                                background: url(../img/sprites/multi.svg#icon-chevron-down) no-repeat;
                                background-size: 26px;
                                background-position: center;
                                transform: scaleY(1);
                                pointer-events: all;
                                transition: .1s;
                            }

                            &::after {
                                content: "";
                                position: absolute;
                                top: 50px;
                                left: 0;
                                right: 0;
                                z-index: 1;
                                border-bottom: 1px solid #E4E4E4;
                                width: 95%;
                                margin: 0 auto;
                                display: none;
                                transition: .2s;
                                pointer-events: all;
                            }

                            a {
                                pointer-events: none;
                            }
                        }
                    }

                    &-link {
                        justify-content: space-between;
                        color: var(--black);
                        width: 100%;
                        border-bottom: 1px dashed #F4F4F4;
                        border-radius: 0;
                        padding: 18px 20px;
                        position: relative;

                        svg {
                            display: none;
                        }

                        &::before {
                            display: none;
                        }

                        &:hover {
                            &::before {
                                left: initial;
                                right: initial;
                            }
                        }

                        &:focus {
                            background-color: #F4F4F4;
                            border-bottom: 1px solid transparent;
                        }
                    }
                }

                &-sub {
                    display: none;
                    height: auto;
                    background-color: initial;
                    position: initial;
                    left: initial;
                    right: initial;
                    top: initial;
                    z-index: initial;
                    pointer-events: none;
                    margin-top: initial;

                    &__wrap {
                        display: block;
                        justify-content: initial;
                        width: 100%;
                        height: auto;
                        padding: initial;
                        margin: initial;
                    }

                    &__blocks {
                        justify-content: initial;
                        display: grid;
                        grid-template-rows: repeat(1, 1fr);
                        grid-template-columns: repeat(2, 1fr);
                        grid-gap: 51px 15px;
                        background-color: #F4F4F4;
                        box-shadow: initial;
                        border-radius: initial;
                        width: 100%;
                        padding: 24px 20px;
                    }

                    &__block {
                        grid-column: auto/span 1;

                        &--img {
                            grid-column: auto/span 2;
                            width: 100%;
                            flex-shrink: initial;
                            position: relative;

                            &::before {
                                content: "";
                                position: absolute;
                                top: -25px;
                                left: 0;
                                right: 0;
                                z-index: 1;
                                border-top: 1px solid #E4E4E4;
                            }
                        }
                    }

                    &__title {
                        @include font(15, 500, 1);
                        margin-bottom: 16px;
                    }

                    &__list {
                        gap: 8px;

                        li {
                            pointer-events: all;

                            a {
                                pointer-events: all;
                                padding: 5px 0;
                                display: block;
                                width: 100%;
                                @include font(13, 400, 1.3);
                                white-space: initial;

                                &::before {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }

            .header-new-info {
                padding: 20px;
                height: auto;

                &__items {
                    width: 100%;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: flex-start;
                    margin-right: initial;
                    gap: 20px;
                }

                &__item {
                    width: 100%;

                    &--btn {
                        display: block;
                    }

                    &--social {
                        display: block;
                    }

                    &:not(:first-child) {
                        margin-left: initial;
                    }
                }

                &__tel {
                    color: var(--black);

                    &::before {
                        border-top: 2px solid var(--black);
                    }
                }

                &-social {
                    &__list {
                        gap: 18px;

                        li {
                            a {
                                color: var(--black);
                            }

                            &:not(:first-child) {
                                &::before {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }

            .header-new__btn {
                color: var(--white);
                background-color: var(--black);
                max-width: 100%;
                width: 100%;
            }


            .header-new-hamburger {
                &__btn {
                    padding: 0 22px;

                    &-wrap {
                        span {
                            background-color: var(--black);
                        }
                    }
                }
            }
        }

        &__wrap {
            height: 55px;
            padding: initial;
            padding-right: 66px;
        }

        &__blocks {
            justify-content: flex-end;
            padding-left: initial;
        }

        &__block {
            &--menu {
                display: none;
            }
        }

        &__logo {
            display: flex;
            align-items: center;
            height: 55px;
            padding-top: initial;
            padding-bottom: initial;
            border-right: initial;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 5;
            padding-left: 20px;
            padding-right: 5px;
            padding-bottom: 1px;

            svg,
            img {
                display: flex;
                width: 103px;
                height: 32px;
            }
        }

        &-info {
            height: 55px;

            &__item {
                &:not(:first-child) {
                    margin-left: 20px;
                }

                &--social {
                    display: none;
                }

                &--btn {
                    display: none;
                }
            }
        }

        &-hamburger {
            &__btn {
                display: block;
            }
        }
    }

    @include to(768px) {
        &__wrap {
            padding-right: 44px;
        }

        &__logo {
            padding-left: 10px;
        }

        &-hamburger {
            &__btn {
                padding: 0 10px;
            }
        }
    }

    @include to-h(740px) {
        &.mobile-active {
            .header-new__scroll {
                height: 520px;
            }
        }
    }

    @include to-h(680px) {
        &.mobile-active {
            .header-new__scroll {
                height: 460px;
            }
        }
    }

    @include to-h(481px) {
        &.mobile-active {
            .header-new__scroll {
                height: 367px;
            }
        }
    }
}

@include to(1180px) {
    body {
        &.active {
            overflow: hidden;
        }
    }
}

.header-new--black {
    .header-new__wrap {
        border-bottom: 1px solid var(--gray-600);
    }

    .header-new__logo {
        border-right: 1px solid var(--gray-600);

        svg {
            color: var(--black);
        }
    }

    .header-new-menu {
        &__list {
            &-link {
                color: var(--black);

                &::before {
                    border-top: 2px solid var(--black);
                }
            }

        }
    }

    .header-new-info {
        &__tel {
            color: var(--black);

            &::before {
                border-top: 2px solid var(--black);
            }
        }

        &-social {
            &__list {
                li {
                    a {
                        color: var(--black);
                    }

                    &:not(:first-child) {
                        &::before {
                            background-color: var(--black);
                        }
                    }
                }
            }
        }
    }

    .header-new__btn {
        background-color: var(--black);

        &::before {
            background-color: var(--gray-200);
        }

        &:hover {
            span {
                color: var(--black);
                transition: 0.3s;
            }
        }
    }

    .header-new-hamburger {

        &__btn {
            &-wrap {
                span {
                    background-color: var(--black);
                }
            }
        }
    }

    &.active {
        .header-new__logo {
            svg {
                color: var(--white);
            }
        }

        .header-new-menu {
            &__list {
                &-link {
                    color: var(--white);

                    &::before {
                        border-top: 2px solid var(--white);
                    }
                }

            }
        }

        .header-new-info {
            &__tel {
                color: var(--white);

                &::before {
                    border-top: 2px solid var(--white);
                }
            }

            &-social {
                &__list {
                    li {
                        a {
                            color: var(--white);
                        }

                        &:not(:first-child) {
                            &::before {
                                background-color: var(--white);
                            }
                        }
                    }
                }
            }
        }

        .header-new__btn {
            &::before {
                background-color: var(--black);
            }

            &:hover {
                span {
                    color: var(--white);
                }
            }
        }
    }

    @include to(1180px) {
        .header-new__logo {
            border-right: initial;
        }
    }
}

//          
.header-new-menu__list-item--arrow {

    &.active-arrow-up {
        .header-new-menu__list-link {
            svg {
                transform: scaleY(-1);
            }
        }

    }
}