.section-advantages {
    overflow: hidden;
    position: relative;
    height: 1002px;
    background-repeat: no-repeat;
    background-position: left -40px bottom;
    background-size: cover;
    background-image: url(../img/bg-advantages-w1920.jpg);

    &__bg {
        width: 100%;
        height: 100%;
        background-color: rgba(#000, .2);
    }

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__wrap-2 {
        max-width: 1480px;
        width: 100%;
        height: auto;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__title {
        max-width: 700px;
        width: 100%;
        padding: 60px 0 30px;
        @include font(62, 500);
        color: var(--white);
        transition: 0.3s;
    }

    &__scroll {
        max-width: 100%;
        width: 100%;
        position: relative;

        &-adaptive {
            width: 740px;
            height: auto;
        }

        &-img {
            width: 740px;
            height: auto;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1;
            transition: 0.3s;
        }
    }

    &__items {
        display: flex;
        max-width: 742px;
        width: 100%;
        padding: 26px 0 30px;
        transition: 0.3s;
    }

    &__item {
        width: 100%;

        &:not(:last-child) {
            margin-right: 13px;
        }

        &:nth-child(1) {
            .section-advantages__item-title {
                max-width: 210px;
            }
        }

        &:nth-child(2),
        &:nth-child(3) {
            .section-advantages__item-title {
                max-width: 140px;
            }
        }
    }

    &__item-title {
        width: 100%;
        @include font(20, 500);
        color: var(--white);
        margin-bottom: 12px;
    }

    &__item-subtitle {
        @include font(16, 400);
        color: var(--white);
    }

    @include to (1900px) {
        background-image: url(../img/bg-advantages.jpg);
    }

    @include to(1755px) {
        background-position: left -20px bottom;
    }

    @include to(1600px) {
        background-position: left -70px bottom;
    }

    @include to(1536px) {
        background-position: left -140px bottom;
    }

    @include to(1442px) {
        background-position: left -160px bottom;
        height: 975px;

        &__scroll {

            &-adaptive,
            &-img {
                width: 693px;
            }
        }

        &__items {
            max-width: 709px;
        }
    }

    @include to(1060px) {
        background-position: left -179px bottom;
        height: 948px;

        &__wrap,
        &__wrap-2 {
            padding: 0 20px;
        }

        &__title {
            padding: 60px 0 28px;
            @include font(56, 500);
        }

        &__items {
            padding: 30px 0 30px;
        }
    }

    @include to(768px) {
        height: auto;
        background-position: left top -180px;
        background-image: url(../img/bg-advantages-w768.webp);

        &__wrap,
        &__wrap-2 {
            padding: 0 10px;
        }

        &__wrap:last-child {
            padding: initial;
        }

        &__title {
            @include font(42, 500);
            padding: 42px 0 20px;
        }

        &__item-title {
            margin-bottom: 6px;
        }

        &__item-title,
        &__item-subtitle {
            color: var(--black);
        }

        &__items {
            max-width: 100%;
            padding: 40px 10px 30px;
            background-color: var(--white);
            margin-top: 50px;
        }
    }

    @include to(712px) {

        &__items {
            flex-direction: column;
        }

        &__item {

            &:not(:last-child) {
                margin-right: initial;
                margin-bottom: 20px;
            }

            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                .section-advantages__item-title {
                    max-width: 100%;
                }
            }
        }
    }

    @include to(670px) {
        &__title {
            @include font(32, 500);
        }
    }

    @include to(576px) {
        &__item {
            max-width: 272px;
        }
    }

    @include to (476px) {
        background-position: left top -196px;
    }

    @include to(442px) {
        background-position: left -8px top -209px;
    }

    @include to(418px) {
        background-position: left -8px top -219px;
    }

    @include to(381px) {
        background-position: left -16px top -229px;
    }

    @include to(376px) {
        background-position: left -18px top -236px;
    }
}