
.techno-wrap {
    padding-top: 90px;
    padding-bottom: 50px;
    position: relative;
    background: #fff;
    margin-bottom: 50px
}

.techno-title {
    margin-bottom: 92px;
    @include font(62, 500, 1);
}

.techno-info {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    gap: 30px;
}

.techno-info-e {
    width: 100%;
    height: 500px;
    padding: 20px;
    flex-grow: 1;
}

.techno-info-e.e1 {
    background: url('../img/techno-1.webp') center bottom no-repeat #F5F5F5;
    background-size: contain;
}

.techno-info-e.e2 {
    background: url('../img/techno-2.webp') center bottom no-repeat #F5F5F5;
    background-size: contain;
}

.techno-info-e.e3 {
    background: url('../img/techno-3.webp') center bottom no-repeat #F5F5F5;
    background-size: contain;
}

.techno-info-e-title {
    margin-bottom: 15px;
    @include font(22);
}

.techno-info-e-desc {
    @include font(16, 400, 1.4);
    color: #6e6e6e;
}

@media only screen and (max-width: 1399px) {
    .techno-info {
        flex-wrap: nowrap;
        justify-content: flex-start;
        overflow: auto;
    }

    .techno-info-e {
        flex-shrink: 0;
        margin-right: 30px;
    }

    .techno-info-e:last-child {
        margin-right: 0;
    }
}

@media only screen and (max-width: 1023px) {
    .techno-title {
        font-size: 52px;
    }

    .techno-info-e {
        margin-right: 20px;
    }
}

@media only screen and (max-width: 799px) {
    .techno-title {
        font-size: 42px;
    }

    .techno-title br {
        display: none;
    }
}

@media only screen and (max-width: 520px) {
    .techno-wrap {
        padding-top: 60px;
    }

    .techno-title {
        font-size: 32px;
        margin-bottom: 45px;
    }

    .techno-info-e {
        width: 310px;
    }

    .techno-info-e {
        margin-right: 10px;
    }
}
