.team-wrap
  position: relative
  // z-index: 5
  padding: 0 40px 120px
  background: #fff
  margin-top: 100px
  +to(599px)
    padding-bottom: 60px

.team-title
  @include font(144,500,1)
  margin-bottom: 28px
  text-transform: uppercase
  +to(520px)
    @include font(40,500,1)

.team-desc
  @include font(20,500)
  margin-bottom: 30px
  +to(599px)
    @include font(16,500, 1.4)
    margin-bottom: 30px

.team-people-slider
  margin: 0 -15px

  & .slick-arrow
    position: absolute
    z-index: 2
    top: calc(50% - 17px)
    width: 35px
    height: 35px
    background: #f00
    overflow: hidden
    text-indent: 40px

  & .slick-arrow.slick-prev
    left: 20px
    background: url(../slick/arrow.png) no-repeat

  & .slick-arrow.slick-next
    right: 20px
    background: url(../slick/arrow.png) no-repeat
    transform: scalex(-1)

  &-e
    width: 20%

.team-people-e
  padding: 0 15px

  &-foto img
    width: 100%

  &-text-name
    padding: 15px 0 10px
    font-size: 18px

  &-text-status
    color: #a6a6a6

.body--advantages
    .team-wrap
        margin-top: 0
    .team-title
        > span
            font-size: 65px
            font-weight: 500
            padding-top: 100px
            padding-bottom: 25px
            text-transform: none
        +to(799px)
            > span
                font-size: 40px
                padding-top: 25px