.section-about-bullets {
    margin-bottom: 25px;

    &__text-block {
        color: rgb(255, 255, 255);
        font-size: 22px;
        font-weight: 500;
        width: 50%;
        margin-left: auto;

        h3 {
            font-size: 62px;
            margin-bottom: 15px;
            line-height: 1;
        }

        span {
            display: inline-block;
            font-size: 140px;
            padding-top: 25px;
        }

        p {
            width: 80%;
        }
    }

    &__video {
        padding: 50px 40px 80px 40px;
        width: 65%;
    }
}

.body--advantages .section-about-bullets {
    margin-bottom: 80px;
}

.body--team .section-about-bullets,
.body--vacancies .section-about-bullets,
.body--cooperation .section-about-bullets {
    padding: 40px 40px 40px 40px;
}
.body--cooperation .section-about-bullets {
    margin-bottom: 0;
}

.baner_team {
    padding: 50px 0 80px 0;
}
.body--cooperation .baner_team {
    padding-bottom: 20px;
}

.wrap_baner {
    width: 65%;
}

.text_block_team {
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
    color: rgb(255, 255, 255);
}

.text_block_team div {
    flex: 1 1 200px;

}

.text_block_team h4 {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 10px;
}

.text_block_team p {
    font-size: 16px;
    font-weight: 400;
}

.text_block_vacancies {
    width: 60%;
    color: rgb(255, 255, 255);
}

.text_block_vacancies h3 {
    font-size: 62px;
    font-weight: 500;
    margin-bottom: 20px;
}

.text_block_vacancies p {
    font-size: 20px;
    font-weight: 500;
}

@include to(1023px) {

    .section-about-bullets,
    .body--team .section-about-bullets,
    .body--vacancies .section-about-bullets,
    .body--cooperation .section-about-bullets {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .section-about-bullets {
        padding-left: 10px;
        padding-right: 10px;
        margin-top: 25px;

        &__text-block {
            margin-left: 0;
            width: 100%;

            h3 {
                font-size: 40px;
            }

            span {
                font-size: 60px;
            }

            p {
                font-size: 17px;
            }
        }

        &__video {
            padding: 35px 0;
            width: 100%;
        }
    }

    .body--advantages .section-about-bullets {
        margin-bottom: 50px;
    }

    .body--team .section-about-bullets,
    .body--vacancies .section-about-bullets,
    .body--cooperation .section-about-bullets {
        padding-left: 10px;
        padding-right: 10px;
    }

    .wrap_baner {
        width: 100%;
    }

    .baner_team {
        padding-top: 0;
    }

    .text_block_vacancies h3 {
        font-size: 40px;
    }

    .text_block_vacancies {
        width: 100%;
    }
}