.section-intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 820px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__title {
        @include font(72, 500, 1.2);
        text-transform: uppercase;
        margin: 46px 0;
        margin-bottom: 74px;
        transition: 0.3s;

        &-wrap {
            display: flex;
            flex-wrap: wrap;

            span {
                display: flex;
                align-items: center;
                transition: 0.3s;
            }

            &--grid-1 {
                span:nth-child(1) {
                    padding-right: 26px;
                }
            }

            &--grid-2 {
                span:nth-child(1) {
                    padding-right: 24px;
                }

                span:nth-child(2) {
                    display: none;
                    padding-right: 12px;
                }

                span:nth-child(3) {
                    padding-right: 17px;
                }

                span:nth-child(4) {
                    display: none;
                }
            }

            &--grid-3 {
                span:nth-child(1) {
                    padding-right: 24px;
                }

                span:nth-child(2) {
                    padding-right: 20px;
                }

                span:nth-child(3) {
                    display: none;
                }
            }
        }

        img {
            display: flex;
            overflow: hidden;
            width: auto;
            height: 56px;
            padding: 2px 0 2px;
            transition: 0.3s;
        }
    }

    &__order {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 650px;
        width: 100%;
        overflow: hidden;
        padding: 12px 20px;
        border-radius: 6px;
        background-color: var(--gray-100);
        backdrop-filter: blur(8px);
        @include font(16, 600);
        color: var(--black);
        margin-bottom: 24px;

        &-wrap {
            display: flex;
            align-items: center;
        }

        svg {
            width: 16px;
            height: 16px;
            margin-right: 12px;
        }
    }

    &__order-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--black);
        color: var(--white);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--gray-200);
        }

        &:hover {
            color: var(--black);

            &::before {
                width: 100%;
            }
        }
    }

    &__subtitle {
        max-width: 600px;
        width: 100%;
        @include font(17, 400);
    }

    @include to(1060px) {
        height: 700px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(53, 500, 1.2);
            margin-bottom: 48px;

            &-wrap {
                &--grid-1 {
                    span:nth-child(1) {
                        padding-right: 19px;
                    }
                }

                &--grid-2 {
                    span:nth-child(1) {
                        padding-right: 16px;
                    }

                    span:nth-child(3) {
                        padding-right: initial;
                    }
                }

                &--grid-3 {
                    span:nth-child(1) {
                        padding-right: 16px;
                    }

                    span:nth-child(2) {
                        padding-right: 16px;
                    }
                }
            }

            img {
                height: 44px;
                padding: 2px 0 4px;
            }
        }
    }

    @include to(768px) {
        height: 560px;
        padding: 92px 0 0;
        
        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500, 1.2);

            img {
                height: 36px;
                padding: 2px 0 3px;
            }
        }

        &__order {
            background-color: transparent;
            padding: initial;
            border-radius: initial;

            &-wrap {
                display: none;
            }
        }
    }

    @include to(670px) {
        &__title {
            @include font(32, 500, 1.2);
            margin-bottom: 36px;

            &-wrap {
                &--grid-1 {
                    span:nth-child(1) {
                        padding-right: 15px;
                    }
                }

                &--grid-2 {
                    span:nth-child(1) {
                        padding-right: 13px;
                    }
                }

                &--grid-3 {
                    span:nth-child(1) {
                        padding-right: 10px;
                    }

                    span:nth-child(2) {
                        padding-right: 13px;
                    }
                }
            }

            img {
                height: 30px;
                padding: 3px 0 4px;
            }
        }
    }

    @include to(450px) {

        &__title {

            &-wrap {

                &--grid-1 {
                    display: flex;

                    span:nth-child(1) {
                        padding-right: 13px;
                    }

                    span:nth-child(2) {
                        max-width: 132px;
                        width: 100%;
                        overflow: hidden;

                        img {
                            object-fit: cover;
                            object-position: left center;
                        }
                    }
                }

                &--grid-2 {
                    span:nth-child(1) {
                        padding-right: 12px;
                    }

                    span:nth-child(3) {
                        padding-right: 13px;
                    }

                    span:nth-child(4) {
                        display: flex;
                    }
                }

                &--grid-3 {
                    span:nth-child(1) {
                        display: none;
                    }

                    span:nth-child(2) {
                        padding-right: 12px;
                    }
                }
            }

            img {
                height: 27px;
                padding: 1px 0px 3px;
            }
        }

        &__order-btn {
            width: 180px;
            height: 43px;
        }
    }

    @include to (433px) {
        &__title {
            max-width: 400px;

            &-wrap {
                &--grid-1 {
                    span:nth-child(2) {
                        display: none;
                    }
                }

                &--grid-2 {
                    span:nth-child(2) {
                        display: flex;

                        img {
                            height: 30px;
                            padding: 3px 0px 3px;
                        }
                    }

                    span:nth-child(4) {
                        display: none;
                    }
                }

                &--grid-3 {
                    span:nth-child(2) {
                        padding-right: 16px;
                    }

                    span:nth-child(3) {
                        display: flex;

                        img {

                            padding: 1px 0px 3px;
                        }
                    }
                }
            }
        }
    }
}