.section-large {
    height: auto;
    position: relative;
}

.section-blur {
    position: relative;

    &__items {
        display: flex;
    }

    &__item {
        width: 50%;
        position: relative;
    }

    &__item-blur {
        height: 100vh;
        backdrop-filter: blur(70px);
        -webkit-backdrop-filter: blur(70px);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1;
    }

    &__item-img {
        display: flex;
        align-items: center;
        overflow: hidden;
        height: 100vh;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
}

.section-blur-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    padding: 40px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 1;

    &__block {
        max-width: 660px;
        width: 100%;
        margin-left: auto;
    }

    &__title {
        margin: 46px 0;
        @include font(70, 500);
        color: var(--white);
        transition: .3s;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--white);
        color: var(--black);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--black);
        }

        &:hover {
            span {
                color: var(--white);
            }

            &::before {
                width: 100%;
            }
        }
    }
}

.section-qualities {
    padding-top: 60px;
    padding-bottom: 100px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__text {
        max-width: 50%;
        width: 100%;
        margin-bottom: 80px;
    }

    &__title {
        @include font(92, 500);
        margin-bottom: 10px;
    }

    &__subtitle {
        max-width: 980px;
        width: 100%;
        @include font(20, 500);
    }

    &__blocks {
        display: flex;
    }

    &__block {
        display: flex;
        width: 50%;

        .card-info {
            display: none;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 500px;
    }

    &__item {
        display: flex;
    }

    &__item-num {
        flex-shrink: 0;
        width: 100px;
        @include font(140, 600, .8);
        transition: .3s;
    }

    &__item-wrap {
        flex-grow: 1;
    }

    &__item-title {
        @include font(25, 500);
        margin-bottom: 20px;
        transition: .3s;
    }

    &__item-subtitle {
        @include font(18, 400);
        transition: .3s;
    }
}

.section-large-area {
    width: 50%;
    height: 100%;
    // pointer-events: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    &__wrap {
        width: 100%;
        height: 100%;
        position: relative;
    }

    &__block {
        display: flex;
        justify-content: center;
        padding-top: 255px;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 128px;
        position: sticky;
        top: 0;
        right: 0;
        z-index: 1;
        transition: 0.2s;
    }
}

.card-info {
    width: 330px;
    height: 456px;
    border-radius: 18px;
    border: 3px solid var(--white);
    overflow: hidden;
    position: relative;

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__wrap {
        position: absolute;
        left: 6px;
        right: 6px;
        bottom: 6px;
        z-index: 1;
    }

    &__items {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(14, 1fr);
        grid-gap: 6px;
        background-color: var(--white);
        border-radius: 15px;
        padding: 10px;
    }

    &__item {
        &:nth-child(1) {
            grid-column: auto/span 2;
        }

        &:nth-child(2) {
            grid-column: auto/span 12;
        }

        &:nth-child(3) {
            grid-column: auto/span 4;
        }
    }

    &__item-svg {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        background-color: var(--black);
        border-radius: 50px;
        margin-top: 3px;

        svg {
            width: 14px;
            height: 14px;
            color: var(--white);
        }
    }

    &__head {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }

    &__title {
        @include font(20, 500);
        margin-bottom: 4px;
    }

    &__subtitle {
        @include font(16, 400);
    }

    &__price {
        @include font(16, 500);
        white-space: nowrap;
        margin-left: 8px;
    }
}


@include to(1200px) {
    .section-blur-info {
        padding: 20px;

        &__title {
            @include font(62, 500);
        }
    }

    .section-qualities {
        padding-bottom: 80px;

        &__wrap {
            padding: 0 20px;
        }

        &__text {
            margin-bottom: 60px;
        }

        &__title {
            @include font(62, 500);
        }

        &__subtitle {
            @include font(20, 400);
        }

        &__item-num {
            width: 70px;
            @include font(75, 600, .8);
        }
    }

    .section-large-area {
        &__block {
            padding-bottom: 100px;
        }
    }
}

@include to (1050px) {
    .section-blur-info {
        padding: 10px;

        &__title {
            @include font(42, 500);
        }
    }
}

@include to(1020px) {
    .section-blur {
        &__items {
            flex-direction: column;
        }

        &__item {
            width: 100%;

            &:nth-child(1) {
                order: 2;
            }

            &:nth-child(2) {
                order: 1;
            }
        }

        &__item-blur,
        &__item-img {
            height: 50svh;
        }
    }

    .section-blur-info {
        justify-content: flex-start;
        height: 50svh;
        width: 100%;
        top: initial;
        bottom: 0;

        &__block {
            margin-left: initial;
        }

        &__title {
            margin: 0 0 22px;
        }
    }

    .section-qualities {
        &__text {
            max-width: 100%;
            margin-bottom: 50px;
        }

        &__blocks {
            flex-direction: column;
        }

        &__block {
            width: 100%;

            &:nth-child(1) {
                order: 2;
            }

            &:nth-child(2) {
                display: flex;
                justify-content: center;
                order: 1;
                margin-bottom: 40px;
            }

            .card-info {
                display: block;
            }
        }

        &__items {
            height: initial;
        }

        &__item {
            text-align: center;
            flex-direction: column;
            justify-content: flex-start;
            padding: 30px;
        }

        &__item-num {
            flex-shrink: initial;
            width: initial;
            @include font(75, 600, .8);
            margin-bottom: 10px;

        }

        &__item-title {
            @include font(25, 500);
        }

        &__item-subtitle {
            @include font(18, 400);
        }
    }

    .section-large-area {
        display: none;
    }
}

@include to(768px) {

    .section-qualities {
        padding-bottom: 60px;

        &__wrap {
            padding: 0 10px;
        }

        &__text {
            margin-bottom: 45px;
        }

        &__title {
            @include font(42, 500);
        }

        &__subtitle {
            @include font(18, 400);
        }
    }
}

@include to(520px) {
    .section-qualities {
        padding-top: 45px;
        padding-bottom: 50px;

        &__text {
            margin-bottom: 40px;
        }

        &__title {
            @include font(40, 500);
        }

        &__subtitle {
            @include font(16, 500);
        }
    }

    .section-blur {
        &__items {
            flex-direction: column;
        }

        &__item {
            width: 100%;
        }
    }

    .section-blur-info {
        &__title {
            @include font(32, 500);
            margin-bottom: 20px;
        }

        &__btn {
            width: 180px;
            height: 43px;
        }
    }
}

@include from-h(1440px) {
