.section-planning-solution-compound {
    padding: 100px 0 60px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__head {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 70px;

        .section-start__form {
            margin-bottom: 40px;
        }
    }

    &__title {
        text-align: center;
        @include font(62, 500, 1);
        margin-bottom: 45px;
    }

    &__price {
        text-align: center;
        @include font(144, 500, 1);
        margin-bottom: 40px;

        &-item {
            position: relative;

            sup {
                position: absolute;
                top: 8%;
                right: -3%;
                z-index: 1;
                @include font(42, 500, 1);
            }
        }

    }

    &__subtitle {
        text-align: center;
        @include font(28, 500, 1);
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 50px 25px;
    }

    &__block {
        max-width: 416px;
        width: 100%;
        grid-column: auto/span 1;

        &-img {
            overflow: hidden;
            margin-bottom: 25px;

            img {
                object-fit: contain;
            }
        }

        &-title {
            @include font(18, 500, 1);
            margin-bottom: 14px;
        }

        &-subtitle {
            @include font(16, 400);
        }
    }

    @include to(1200px) {
        padding: 60px 0 50px;

        &__wrap {
            padding: 0 20px;
        }

        &__head {
            margin-bottom: 50px;

            .section-start__form {
                margin-bottom: 32px;
            }
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 35px;
        }

        &__price {
            @include font(128, 500, 1);
            margin-bottom: 35px;
        }

        &__blocks {
            grid-gap: 30px 20px;
        }

        &__block {
            &-img {
                margin-bottom: 18px;
            }

            &-title {
                @include font(17, 500, 1);
                margin-bottom: 12px;
            }

            &-subtitle {
                @include font(15, 400);
            }
        }
    }

    @include to(890px) {
        &__blocks {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 40px 20px;
        }
    }

    @include to(768px) {
        padding: 60px 0 40px;

        &__wrap {
            padding: 0 10px;
        }

        &__blocks {
            grid-gap: 37px 10px;
        }

        &__head {
            margin-bottom: 40px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 28px;
        }

        &__price {
            @include font(82, 500, 1);
            margin-bottom: 33px;

            &-item {
                sup {
                    @include font(26, 500, 1);
                }
            }
        }

        &__subtitle {
            @include font(24, 500, 1);
        }
    }

    @include to(520px) {
        padding: 40px 0 40px;

        &__head {
            margin-bottom: 28px;
        }

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 25px;

            br {
                display: none;
            }
        }

        &__price {
            @include font(52, 500, 1);
            margin-bottom: 27px;

            &-item {
                sup {
                    top: 5%;
                    right: -4%;
                    @include font(20, 600, 1);
                }
            }
        }

        &__subtitle {
            @include font(22, 500, 1);
        }
    }

    @include to(440px) {
        &__blocks {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}