.section-about-footer {
    color:rgb(255, 255, 255);
    position: relative;
    min-height: 790px;
    background-color: rgb(0, 0, 0);
    padding: 20px 40px 20px 40px;
    &__image-container {
        clip-path: polygon(0% 0%, 45% 10%, 40% 85%, 5% 70%);
        transition: all 3s;
        &--1 {
            clip-path: polygon(0% 3%, 35% 5%, 25% 70%, 1% 75%);
        }
        &--2 {
            clip-path: polygon(1% 4%, 46% 12%, 35% 70%, 10% 60%);
        }
        &--3 {
            clip-path: polygon(2% 12%, 44% 16%, 50% 74%, 8% 78%);
        }
        &--4 {
            clip-path: polygon(0% 5%, 39% 16%, 45% 75%, 8% 75%);
        }
        &--5 {
            clip-path: polygon(1% 9%, 46% 12%, 30% 86%, 1% 60%);
        }
        &--6 {
            clip-path: polygon(0% 0%, 35% 10%, 40% 75%, 5% 70%);
        }
        &--7 {
            clip-path: polygon(0% 5%, 30% 12%, 45% 78%, 2% 63%);
        }
        img {
            width: 60%;
        }
    }
}
.body--advantages,
.body--team,
.body--vacancies,
.body--cooperation {
    .section-about-footer {
        padding-top: 100px;
    }
}

.about-footer {
    position: absolute;
    left: 50%;
    top: 15px;
    font-size: 70px;
    font-weight: 500;
}
.about-footer-menu {
    ul {
        line-height: 1.5;
        li {
            position: relative;
            display: flex;
            flex-direction: column;
            width: fit-content;
            &:after {
                content: '';
                display: block;
                height: 3px;
                background-color:rgb(255, 255, 255);
                width: 0%;
                transition: width 0.5s linear;
            }
            &.about-footer-menu__link--active,
            &:hover {
                &:after {
                    width: 100%;
                }
            }
        }
    }
    a {
        display: inline-block;
    }
}

@include to(1023px) {
    .section-about-footer {
        padding-left: 20px;
        padding-right: 20px;
        min-height: 500px;
    }
    .about-footer {
        font-size: 50px;
    }
}

@include to(799px) {
    .section-about-footer {
        padding-left: 10px;
        padding-right: 10px;
        min-height: 415px;
        div {
            img {
                display: none;
            }
        }
    }
    .about-footer {
        left: 7px;
        font-size: 36px;
    }
}