.fon1 {
    background: url(/images/vakansii/fon-page-1.jpg) center top no-repeat;
    background-color: #1d1d1d;
    color: #fff;
    overflow: hidden;
    position: relative;
}

.fon1 .wrapper-in {
    position: relative;
    z-index: 3;
}

.page-top {
    padding-bottom: 100px;
    color: #fff;
}

.sub-h1 {
    margin: 30px 0;
}

.page-top-show-vakansii {
    margin: 70px 0 20px;
    width: 290px;
    height: 88px;
}

.expert-people {
    position: absolute;
    overflow: hidden;
    top: 250px;
    right: -5vw;
    width: 75vw;
    height: 43vw;
    z-index: 2;
    white-space: nowrap;
}

.expert-people-Column {
    display: inline-block;
    vertical-align: top;
    position: relative
}

.expert-people-Item {
    padding: 1vw;
    display: block;
    position: relative;
}

.expert-people-ItemPhoto {
    display: block;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%
}

.expert-people-Item.expert-people-ItemMedSquare .expert-people-ItemPhoto {
    width: 18vw;
    height: 18vw;
}

.expert-people-Item.expert-people-ItemSmallSquare .expert-people-ItemPhoto {
    width: 13vw;
    height: 13vw;
}

.expert-people-Item.expert-people-ItemSmallRect .expert-people-ItemPhoto {
    width: 13vw;
    height: 10vw;
}

.expert-people-Item.expert-people-ItemFull .expert-people-ItemPhoto {
    width: 23vw;
    height: 28vw;
}

.expert-people-Item:not(.expert-people-ItemFull) .expert-people-ItemPhoto {
    opacity: .3;
    -webkit-filter: blur(5px);
    filter: blur(5px);
}

.expert-people-ItemPhoto.rosa {
    background-image: url(/images/komanda/people/rosa.png)
}

.expert-people-ItemPhoto.adan {
    background-image: url(/images/komanda/people/adan.png)
}

.expert-people-ItemPhoto.siah {
    background-image: url(/images/komanda/people/siah.png)
}

.expert-people-ItemPhoto.mark {
    background-image: url(/images/komanda/people/mark.png)
}

.expert-people-ItemPhoto.sean {
    background-image: url(/images/komanda/people/sean.png)
}

.expert-people-ItemPhoto.gina {
    background-image: url(/images/komanda/people/gina.png)
}

.expert-people-ItemPhoto.welcome {
    background-image: url(/images/vakansii/welcome.jpg);
    position: relative;
}

.expert-people-ItemPhoto.welcome:after {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    left: -15px;
    bottom: -15px;
    width: 30px;
    border: 2px solid #fff;
    border-right: none;
    opacity: .6;
}

.expert-people-ItemPhoto.welcome:before {
    content: '';
    display: block;
    position: absolute;
    top: -15px;
    right: -15px;
    bottom: -15px;
    width: 30px;
    border: 2px solid #fff;
    border-left: none;
    opacity: .6;
}

.expert-people-ItemName,
.expert-people-ItemDesc {
    width: 23vw;
    color: #fff;
    white-space: normal;
}

.expert-people-ItemName {
    opacity: 0.8;
    font-size: 16px;
    margin: 15px 0 5px;
}

.expert-people-ItemDesc {
    opacity: 0.5;
    font-size: 14px;
}

.need-wrap {
    padding-top: 60px;
}

.need-title {
    text-align: center;
    font-size: 50px;
}

.need-subtitle {
    text-align: center;
    font-size: 18px;
    margin: 30px 0 80px;
}

.need-info {
    max-width: 924px;
    margin: 0 auto;
}

.need-info-e {
    display: flex;
    margin-bottom: 50px;
}

.need-info-e:last-child {
    margin-bottom: 0;
}

.need-info-e-num-wrap {
    flex-shrink: 0;
    width: 124px;
    position: relative;
}

.need-info-e-num {
    width: 40px;
    height: 40px;
    border: 2px solid #fff;
    background: #88837f;
    border-radius: 100%;
    box-sizing: border-box;
    font-size: 18px;
    line-height: 36px;
    text-align: center;
    position: absolute;
    z-index: 2;
    top: 126px;
    left: 0;
    opacity: 0.2;
    transition: 500ms;
}

.need-info-e-scale {
    position: absolute;
    z-index: 2;
    top: 182px;
    left: 19px;
    width: 2px;
    height: 270px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
}

.need-info-e-scale-in {
    background: #fff;
    max-height: 270px !important;
}

.need-info-e-cont {
    flex-grow: 1;
    border: 2px solid #fff;
    display: flex;
    padding: 24px;
    border-radius: 15px;
    transform: scale(0.5);
    transition: 500ms;
}

.need-info-e.active .need-info-e-cont {
    transform: scale(1);
}

.need-info-e.active .need-info-e-num {
    opacity: 1;
}

.need-info-e-cont-pic-wrap {
    flex-shrink: 0;
}

.need-info-e-cont-pic-wrap img {
    width: 240px;
    height: 240px;
}

.need-info-e-cont-desc {
    flex-grow: 1;
    padding: 30px;
    padding-bottom: 0px;
    position: relative;
}

.need-info-e-cont-desc-price {
    position: absolute;
    top: 0px;
    right: 0px;
    height: 25px;
    width: 90px;
    font-size: 12px;
    line-height: 25px;
    text-align: center;
    background: #fff;
    color: #000;
    border-radius: 6px;
}

.need-info-e-cont-desc-title {
    font-size: 36px;
    margin-bottom: 10px;
}

.need-info-e-cont-desc-task {
    margin-bottom: 10px;
}

.rezume {
    padding: 90px 0 120px;
}

.rezume-title {
    text-align: center;
    font-size: 50px;
}

.rezume-subtitle {
    text-align: center;
    font-size: 40px;
    margin-top: 20px;
}

/*======================================================*/

@media only screen and (max-width:1250px) {

    .need-title,
    .rezume-title,
    .rezume-subtitle {
        font-size: 46px;
    }
}

@media only screen and (max-width: 1023px) {
    .need-info-e-num-wrap {
        width: 90px;
    }
}

@media only screen and (max-width: 799px) {

    .expert-people-Item.expert-people-ItemFull {
        opacity: .3;
        -webkit-filter: blur(5px);
        filter: blur(5px);
    }

    .need-title,
    .rezume-title,
    .rezume-subtitle {
        font-size: 40px;
    }

    .need-info-e-cont-desc-title {
        font-size: 32px;
    }

    .need-info-e {
        display: block;
    }

    .need-info-e-num-wrap {
        display: none;
    }

}

@media only screen and (max-width: 599px) {
    .fon1 {
        background-image: none;
    }

    .header {
        margin-bottom: 50px;
    }

    .page-top {
        padding-bottom: 50px;
    }

    .expert-people {
        display: none;
    }

    .expert-people-Item.expert-people-ItemMedSquare .expert-people-ItemPhoto {
        width: 30vw;
        height: 30vw;
    }

    .page-top-show-vakansii {
        margin: 70px auto 20px;
    }

    .need-title,
    .rezume-title,
    .rezume-subtitle {
        font-size: 28px;
    }

    .need-info-e-cont-desc-title {
        font-size: 22px;
    }

    .need-subtitle {
        font-size: 14px;
        margin: 30px 0 60px;
    }

    .need-subtitle br {
        display: none;
    }

    .need-info-e-cont {
        border: none;
        display: block;
        padding: 0;
        border-radius: 15px;
        transform: scale(1) !important;
        text-align: center;
    }

    .need-wrap {
        padding-top: 0;
    }

    .need-info-e-cont-pic-wrap img {
        width: 200px;
        height: 200px;
    }

    .need-info-e-cont-desc-price {
        display: none;
    }

}

.section-vacancies {
    color: rgb(255, 255, 255);
    padding-top: 50px;
}

.need-info-e:last-child {
    margin-bottom: 0;
    padding-bottom: 100px;
}

.need-info-e-cont-desc-text ul li {
    list-style-type: disc;
}

@include to(799px) {
    .need-info-e-cont-desc-text ul li {
        text-align: left;
    }
}

.button-appl {
    width: 240px;
    height: 51px;
    background-color: #ffffff;
    position: relative;
    cursor: pointer;
    margin-top: 10px;
    font-weight: 500;
}

.button-appl-text {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: #000000;
    text-align: center;
    font-size: 12px;
    line-height: 51px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: 300ms;
}

.button-appl-text:hover {
    color: #000;
}

.button-appl:before {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    bottom: 0;
    width: 0%;
    transition: 300ms;
    background: #ddd;
}

.button-appl:hover:before {
    width: 100%;
}

@media only screen and (max-width: 600px) {
    .button-appl {
        margin-left: auto;
        margin-right: auto;
        margin-top: 30px;
        width: 200px;
    }

    .button-appl-text {
        font-size: 14px;
    }
}