
.tarif-wrap {
	position: relative;
    background: #F0F0F0;
    padding-top: 120px;
	padding-bottom: 120px;
	@media only screen and (max-width: 520px) {
		padding-top: 60px;
	    padding-bottom: 60px;
	}
}

.tarif {
	position: relative;
}

.tarif-title {
    font-size: 144px;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 60px;
    font-weight: 500;
		@media only screen and (max-width: 992px) {
			font-size: 72px;
		}
}

.tarif-info {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.tarif-info-section.e1 {
	width: 490px;
}

.tarif-info-section.e2 {
	max-width: 770px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.tarif-info-section-sep {
	border-right:1px solid #a6a6a6;
	align-self: stretch;
}

.tarif-info-e {
	background: #fff;
	border-radius: 20px;
	width: 380px;
	padding: 52px 40px 32px;
	box-sizing: border-box;
	
}

.tarif-info-e:not(.e1) {
	position: relative;
	padding-bottom: 197px;
}

.tarif-info-e:not(.e1) .tarif-info-e-cont {
	margin-bottom: 0;
}

.tarif-info-e:not(.e1) .tarif-info-e-price {
	margin-bottom: 0;
	position: absolute;
	left: 40px;
	bottom: 105px;
}

.tarif-info-e:not(.e1) .tarif-info-e-but {
	position: absolute;
	left: 40px;
	bottom: 32px;
}

.tarif-info-e.e2,
.tarif-info-e.e3 {
	margin-bottom: 10px;
}

.tarif-info-e.e1 {
	width: 100%;
	padding: 50px 60px;
}

.tarif-info-e.e1 .tarif-info-e-title {
	@include font(40);
	margin-bottom: 20px;
}

.tarif-info-e.e1 .tarif-info-e-day {
	margin-bottom: 30px;
	@include font(17);
}

.tarif-info-e.e1 .tarif-info-e-sep.e1 {
	border-top: 1px solid #a6a6a6;
	margin: 0 auto;
	max-width: 300px;
}

.tarif-info-e.e1 .tarif-info-e-desc {
	text-align: center;
	margin: 30px 0;
}

.tarif-info-e.e1 .tarif-info-e-sep.e2 {
	border-top: 1px solid #a6a6a6;
	margin: 0 auto;
	max-width: 50px;
}

.tarif-info-e.e1 .tarif-info-e-cont {
	margin: 40px 0;
	font-size: 17px;
}

.tarif-info-e.e1 .tarif-info-e-cont-title {
	@include font(20);
	margin-bottom: 15px;
}

.tarif-info-e.e1 .tarif-info-e-cont-title.e2 {
	margin-top: 15px;
}

.tarif-info-e.e1 .tarif-info-e-cont-item {
	padding: 5px 0 5px 24px;
	position: relative;
}

.tarif-info-e.e1 .tarif-info-e-cont-item:before {
	content: '';
    display: flex;
    width: 22px;
    height: 16px;
    background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat;
    background-size: 30px;
    position: absolute;
    top: 11px;
    left: -6px;
}

.tarif-info-e.e1 .tarif-info-e-price {
	margin-bottom: 30px;
}

.tarif-info-e.e1 .tarif-info-e-price-title {
	@include font(20);
	margin-bottom: 5px;
}

.tarif-info-e.e1 .tarif-info-e-price-sum {
	@include font(25);
}

.tarif-info-e-but {
	width: 180px;
    height: 43px;
    line-height: 43px;
    background-color: #121315;
    position: relative;
    cursor: pointer;
}

.tarif-info-e-but:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    bottom: 0;
    width: 0%;
    transition: 300ms;
    background: #ddd;
}

.tarif-info-e-but:hover:before {
    width: 100%;
}

.tarif-info-e-but-text {
	position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: #fff;
    text-align: center;
    font-size: 12px;
		font-weight: 500;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: 300ms;
}

.tarif-info-e-but-text:hover {
    color: #000;
}

.tarif-info-e-title {
		@include font(25);
    margin-bottom: 20px;
}

.tarif-info-e-day {
	margin-bottom: 20px
}

.tarif-info-e-cont {
	margin: 30px 0;
	font-size: 14px;
}

.tarif-info-e-cont-title {
	@include font(18);
	margin-bottom: 15px;
}

.tarif-info-e-cont-item {
	padding: 5px 0 5px 24px;
	position: relative;
}

.tarif-info-e-cont-item:before {
	content: '';
    display: flex;
    width: 22px;
    height: 16px;
    background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat;
    background-size: 30px;
    position: absolute;
    top: 8px;
    left: -6px;
}

.tarif-info-e-cont-item-more {
	display: inline-block;
	padding: 5px 0 5px;
	margin-left: 30px;
	color: #3399FF;
	cursor: pointer;
	padding-bottom: 2px;
	border-bottom: 1px solid #fff;
}

.tarif-info-e-cont-item-more:hover {	
	border-bottom-color: #3399FF;
}

.tarif-info-e-cont-item-other {
	display: none;
}

.tarif-info-e-price {
	margin-bottom: 30px;
}

.tarif-info-e-price-title {
	@include font(18);
	margin-bottom: 5px;
}

.tarif-info-e-price-sum {
	@include font(18);
}


@media only screen and (max-width:1390px) {
	
	.tarif-title {
	    text-align: center;
	}
	
	.tarif-info {
	    flex-wrap: wrap;
	    width: 100%;
	    max-width: 770px;
	    margin: 0 auto;
	}
	
	.tarif-info-section.e1 {
	    width: 100%;
	    margin-bottom: 10px;
    	border-radius: 20px;
	    background: #fff;
	}
	
	.tarif-info-e {
		width: calc(50% - 5px);
		max-width: 380px;
	}
	
	.tarif-info-e.e1 {
		margin: 0 auto;
		max-width: 400px;
	}

    .tarif-info-section-sep {
        display: none;
    }
    
    .tarif-info-e.e1 .tarif-info-e-desc br {
		display: none;
	}
    
}


@media only screen and (max-width: 520px) {
.tarif-title {
	font-size: 40px;
}

.tarif-info-e.e1 .tarif-info-e-title {
		font-size: 30px;
}

.tarif-info-section.e2 {
		display: block;
}

.tarif-info-e {
		width: auto;
		max-width: 100%;
}

.tarif-info-e.e4 {
	margin-bottom: 10px;
}
.tarif-info-e.e1 {
	padding: 32px 40px;
}

.tarif-info-e:not(.e1) {
	padding-top: 32px;
	padding-bottom: 172px;
}

.tarif-info-e.e1 .tarif-info-e-cont {
	margin: 40px 0;
}

.tarif-info-e:not(.e1) .tarif-info-e-price {
	bottom: 95px;
}
}