.section-block-result {
    width: 100%;
    min-height: 100vh;
    height: 100%;
    background-color: var(--brown-bg);
    color: var(--white);
    position: relative;

    &__text {
        display: flex;
        flex-direction: column;
        // max-width: 502px;
        max-width: 690px;
        width: 100%;
        padding: 40px;
        flex-shrink: 0;
        gap: 45px;
    }

    &__head {
        max-width: 426px;
        width: 100%;
        flex-shrink: 0;
    }

    &__title {
        @include font(52, 500, 1);
        margin-bottom: 25px;
        transition: .3s;
    }

    &__subtitle {
        @include font(17, 500);
        transition: .3s;
    }

    &__list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 35px;
        flex-grow: 1;
        // max-width: 426px;
        // width: 100%;

        li {
            max-width: 426px;
            width: 100%;
        }

        &-num {
            @include font(17, 500, 1);
            margin-bottom: 8px;
        }

        &-title {
            @include font(25, 500, 1);
            margin-bottom: 14px;
        }

        &-subtitle {
            @include font(16, 400);
        }
    }

    &__img {
        display: flex;
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        right: 40px;
        pointer-events: none;

        &-block {
            flex-shrink: 0;
            width: 542px;
            pointer-events: none;
        }

        img {
            flex-grow: 1;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
            pointer-events: none;
        }
    }

    @include to(1200px) {
        min-height: 100%;

        &__head {
            max-width: 345px;
        }

        &__text {
            max-width: 440px;
            padding: 35px 20px;
            gap: 36px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 20px;
        }

        &__subtitle {
            @include font(16, 500);
        }

        &__list {
            gap: 35px;

            &-num {
                @include font(15, 500, 1);
                margin-bottom: 7px;
            }

            &-title {
                @include font(22, 500, 1);
                margin-bottom: 12px;
            }

            &-subtitle {
                @include font(15, 400);
            }
        }

        &__img {
            right: 20px;

            &-block {
                width: 460px;
            }
        }
    }

    @include to(580px) {
        &__img {
            display: none;
        }
    }

    @include to(520px) {
        &__head {
            max-width: 285px;
        }

        &__text {
            gap: 33px;
        }

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 18px;
        }

        &__subtitle {
            @include font(16, 400);
        }
    }
}