.your-budget {
    padding: 90px 0 120px;

    &__title {
        @include font(62, 500);
        margin-bottom: 20px;
    }

    &__text {
        max-width: 580px;
        width: 100%;
        @include font(20, 500);
        color: var(--gray-700);
        margin-bottom: 60px;
    }

    &__blocks {
        display: flex;
        flex-direction: row;
        gap: 25px;
    }

    &__block {
        width: 100%;
        position: relative;
    }

    &__block-img {
        height: 556px;
        width: 100%;
        overflow: hidden;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
            object-position: center center;
        }
    }

    &__dots {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    &__dots-wrap {
        height: 100%;
        position: relative;
    }

    &__dot {
        width: 34px;
        height: 34px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;

        &:hover {
            z-index: 10;

            .your-budget__dot-num {
                background-color: #d40136;
                color: var(--white);
            }

            .your-budget__card {
                pointer-events: all;
                opacity: 1;
                z-index: 5;
            }
        }
    }

    &__dot-wrap {
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            z-index: -1;
            width: 34px;
            height: 34px;
            border-radius: 50%;
            animation-duration: 2.1s;
            animation-name: pulseDotYourBudget;
            animation-iteration-count: infinite;
        }
    }

    &__dot-num {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 34px;
        border-radius: 40px;
        background-color: var(--white);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        @include font(14, 500);
        cursor: default;
        transition: .2s;
    }

    @keyframes pulseDotYourBudget {
        0% {
            scale: 1;
            background-color: rgba(#fff, 0);
        }

        90% {
            scale: 1.65;
            background-color: rgba(#fff, 1);
        }


        100% {
            background-color: rgba(#fff, 0);
            scale: 1;
            opacity: 0;
        }
    }

    &__card {
        pointer-events: none;
        width: 140px;
        height: auto;
        background-color: var(--white);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        text-align: center;
        border-radius: 8px;
        overflow: hidden;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        opacity: 0;
        transition: .2s;
    }

    &__card-img {
        width: 100%;
        height: 176px;
        border-radius: 8px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__card-wrap {
        padding: 6px 4px 7px;
    }

    &__card-title {
        @include font(16, 500);
        margin-bottom: 6px;
    }

    &__card-price {
        @include font(15, 400);
    }

    &__dot--one {
        top: 55%;
        right: 16%;
    }

    &__dot--two {
        top: 40%;
        right: 51%;
    }

    &__dot--three {
        top: initial;
        right: initial;
        left: 39%;
        bottom: 8%;
    }

    &__dot--four {
        top: initial;
        right: initial;
        left: 24%;
        bottom: 31%;
    }

    &__items {
        display: flex;
        flex-direction: row;
        gap: 25px;
    }

    &__item {
        width: 100%;
    }

    &__list {
        display: block;
        margin-left: 32px;
        margin-top: 20px;

        li {
            list-style-type: decimal-leading-zero;
            list-style-position: outside;
            padding: 5px 2px 5px 5px;
            @include font(17, 500);

            span {
                display: inline-block;
            }
        }
    }

    @include to(1200px) {
        padding: 60px 0 90px;

        &__blocks {
            gap: 20px;
        }

        &__block-img {
            height: auto;

            img {
                object-fit: contain;
            }
        }
    }

    @include to (768px) {
        padding: 50px 0 80px;

        &__title {
            @include font(42, 500);
        }

        &__text {
            @include font(18, 500);
        }

        &__blocks {
            gap: 10px;
        }

        &__block {
            &:nth-child(1) {
                .your-budget__dot--four {
                    .your-budget__card {
                        left: -20px;
                        transform: translateX(0);
                    }
                }
            }

            &:nth-child(2) {
                .your-budget__dot--one {
                    .your-budget__card {
                        left: initial;
                        transform: translateX(0);
                        right: -20px;
                    }
                }
            }
        }

        &__dot-wrap {
            &::after {
                width: 28px;
                height: 28px;
            }
        }

        &__dot {
            width: 28px;
            height: 28px;
        }

        &__dot-num {
            width: 28px;
            height: 28px;
        }

        &__items {
            gap: 10px;
        }

        &__card {
            bottom: 35px;
        }

        &__list {
            margin-left: 28px;
            margin-top: 13px;

            li {
                @include font(16, 400);
                padding: 5px 2px 5px 3px;
            }
        }
    }

    @include to(705px) {
        &__list {
            margin-left: 26px;
            margin-top: 11px;

            li {
                @include font(15, 400);
                padding: 5px 2px 5px 3px;
            }
        }
    }

    @include to(662px) {
        &__list {

            li {
                @include font(14, 400, 1.4);
                padding: 2px 2px 2px 2px;
            }
        }
    }

    @include to(550px) {
        &__dot-wrap {
            &::after {
                width: 25px;
                height: 25px;
            }
        }

        &__dot--one {
            top: 52%;
            right: 12%;
        }

        &__dot--two {
            top: 36%;
            right: 48%;
        }

        &__dot--three {
            left: 36%;
            bottom: 6%;
        }

        &__dot--four {
            left: 21%;
            bottom: 29%;
        }

        &__dot {
            width: 25px;
            height: 25px;
        }

        &__dot-num {
            width: 25px;
            height: 25px;
        }
    }
    
    @include to(520px) {
        padding: 50px 0 60px;
    
        &__title {
            @include font(32, 500);
            margin-bottom: 15px;
        }
        
        &__text {
            @include font(16, 500);
            margin-bottom: 30px;
        }
    }
}