.section-plain-banner {
    padding-top: 160px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(92, 500, 1);
        margin: 0 0 70px;
    }

    @include to(1200px) {
        padding-top: 125px;
    
        &__wrap {
            padding: 0 20px;
        }
        
        &__title {
            @include font(62, 500, 1);
            margin: 0 0 40px;
        }
    }

    @include to(768px) {
        padding-top: 108px;
    
        &__wrap {
            padding: 0 10px;
        }
        
        &__title {
            @include font(52, 500, 1);
            margin-bottom: 35px;
        }
    }

    @include to(520px) {
        padding-top: 95px;
        
        &__title {
            @include font(42, 500, 1);
        }
    }
}