.section-facade-design-slider {
    padding: 120px 0;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 20px;
    }

    &__blocks {
        display: flex;
        flex-direction: column;
        gap: 60px;
    }

    &__title {
        @include font(62, 500, 1);
        margin-bottom: 90px;
    }

    &__descr {
        max-width: 700px;
        width: 100%;
        padding-top: 30px;

        &-title {
            @include font(32, 500, 1);
            margin-bottom: 12px;
        }

        &-subtitle {
            @include font(16, 400);
        }
    }

    @include to(1200px) {
        padding: 80px 0;

        &__blocks {
            gap: 40px;
        }

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 55px;
        }
    }

    @include to(768px) {
        padding: 60px 0;

        &__blocks {
            gap: 30px;
        }

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 45px;

            br {
                display: none;
            }
        }

        &__descr {
            padding-top: 20px;

            &-title {
                @include font(30, 500, 1);
                margin-bottom: 11px;
            }
        }
    }

    @include to(520px) {
        padding: 45px 0;

        &__blocks {
            gap: 28px;
        }

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 32px;
        }

        &__descr {
            padding-top: 18px;

            &-title {
                @include font(26, 500, 1);
                margin-bottom: 10px;
            }

            &-subtitle {
                @include font(15, 400);
            }
        }
    }
}