.section-animation {

    video::-webkit-media-controls-start-playback-button {
        display: none !important;
    }

    video::-webkit-media-controls-play-button {
        display: none !important;
    }

    &__blocks {
        display: flex;
        height: 100%;
    }

    &__block {
        width: 50%;
        height: 100%;
        position: relative;


        &--video {
            display: block;
            position: sticky;
            top: 0;
            z-index: 5;
            background-color: #D7D7D7;
            height: 100vh;
            transform: scaleX(-1);

            video {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .section-animation-video {}
        }

        &--info {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &-info {
        max-width: 555px;
        width: 100%;
        padding: 0 40px;

        &__blocks {
            display: flex;
            flex-direction: column;
            gap: 50px;
        }

        &__block {
            height: 100vh;

            &--height-1 {
                height: 400vh;
                position: relative;

                .section-animation-info__block-wrap,
                .section-animation-info__block-wrap--mobile {
                    position: sticky;
                    top: 0;
                    height: 100vh;
                }
            }

            &--height-2 {
                min-height: 100vh;
                height: initial;
            }

            &--height-3 {
                min-height: 100vh;
                height: initial;
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 100%;

                &--mobile {
                    display: none;
                }
            }

            &-num {
                @include font(20, 500, 1);
                margin-bottom: 14px;
            }

            &-title {
                @include font(40, 500, 1);
                margin-bottom: 40px;
            }

            &-text {
                padding-left: 32px;
            }
        }

        &-text__items {
            display: flex;
            flex-direction: column;
            gap: 25px;
            margin-bottom: 20px;
        }

        &-text__item {
            display: flex;
            flex-direction: column;
            gap: 14px;

            &-title {
                @include font(20, 500, 1);
            }

            &-description {
                @include font(17, 400, 1.4);
            }

            &-list {
                li {
                    @include font(17, 400);
                    padding: 5px 0 5px 22px;
                    position: relative;

                    &:before {
                        content: '';
                        display: block;
                        width: 22px;
                        height: 16px;
                        background: url(../img/sprites/multi.svg#icon-price-list-mark);
                        background-repeat: no-repeat;
                        background-size: 30px;
                        background-position: center;
                        position: absolute;
                        top: 8px;
                        left: -4px;
                        z-index: 1;
                    }
                }
            }
        }

        &__btn {
            display: flex;
            width: fit-content;
            align-items: center;
            position: relative;

            &::before {
                content: "";
                position: absolute;
                right: 0;
                bottom: 1px;
                z-index: 1;
                width: 100%;
                height: 1px;
                background-color: var(--black);
                transition: .2s;
            }

            &-text {
                @include font(15, 400, 1.3);
                padding-bottom: 3px;
                margin-right: 4px;
                transition: .2s;
            }

            &-icon {
                svg {
                    width: 15px;
                    height: 15px;
                }
            }

            &:hover {
                &::before {
                    width: 0;
                }
            }
        }
    }

    @include to(1320px) {
        &-info {
            max-width: 490px;

            &__block {
                &-num {
                    @include font(18, 500, 1);
                    margin-bottom: 10px;
                }

                &-title {
                    @include font(36, 500, 1);
                    margin-bottom: 30px;
                }

                &-text {
                    padding-left: 24px;
                }
            }

            &-text__item {
                &-title {
                    @include font(19, 500, 1);
                }

                &-description {
                    @include font(16, 400, 1.4);
                }

                &-list {
                    li {
                        @include font(16, 400);
                        padding: 4px 0 4px 20px;

                        &:before {
                            top: 6px;
                        }
                    }
                }
            }

            &-text__items {
                gap: 22px;
            }

            &-text__item {
                gap: 12px;
            }
        }
    }

    @include to(1200px) {

        &-info {
            &__block {
                &-title {
                    @include font(32, 500, 1);
                    margin-bottom: 27px;
                }
            }
        }
    }

    @include to(768px) {

        &-info {
            &__block {
                &-num {
                    @include font(16, 500, 1);
                    margin-bottom: 8px;
                }

                &-title {
                    @include font(28, 500, 1);
                    margin-bottom: 22px;
                }

                &-text {
                    padding-left: 18px;
                }
            }

            &-text__item {
                &-title {
                    @include font(18, 500, 1);
                }

                &-description {
                    @include font(15, 400, 1.4);
                }

                &-list {
                    li {
                        @include font(15, 400);
                        padding: 3px 0 3px 19px;

                        &:before {
                            top: 5px;
                        }
                    }
                }
            }

            &-text__items {
                gap: 22px;
                margin-bottom: 18px;
            }

            &-text__item {
                gap: 12px;
            }
        }
    }

    @include to(720px) {

        &__blocks {
            flex-direction: column;
        }

        &__block {
            width: 100%;

            &--video {
                height: 50svh;
            }
        }

        &-info {
            max-width: 580px;

            &__block {
                &--height-1 {
                    .section-animation-info__block-wrap--mobile {
                        top: 50vh;
                        height: 50vh;
                    }
                }
                
                &--height-2 {
                    min-height: initial;
                    height: 100vh;
                }
    
                &--height-3 {
                    min-height: initial;
                    height: 100vh;
                }
    

                &-wrap {
                    &--pc {
                        display: none;
                    }

                    &--mobile {
                        display: flex;
                    }
                }
            }
        }
    }

    @include to(520px) {

        &-info {
            padding: 0 20px;
        }
    }

    @include to-h(667px) {
        .section-animation-info__block-wrap--mobile {
            .section-animation-info-text__item-description {
                display: none;
            }
        }
    }

}