.section-portfolio {
    padding: 60px 0;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
        margin-bottom: 60px;
    }

    &__title {
        @include font(92, 500);
        text-transform: uppercase;
    }

    &__subtitle {
        @include font(20, 500);
        margin-top: 10px;
    }

    &__items {
        position: relative;
    }

    &__item {
        position: relative;
    }

    &__hor-line {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 1px;
        width: 100%;
        margin: 0 auto;
        background-color: var(--black);

        &--bottom {
            top: initial;
            bottom: 0;
        }
    }

    &__ver-line {
        height: 100%;
        width: 1px;
        background-color: var(--black);
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        margin: 0 auto 0 375px;
        z-index: 1;
    }
}

.section-portfolio-item {
    &__wrap {
        display: flex;
        gap: 60px;
        max-width: 100%;
        width: 100%;
        padding: 25px 0 25px 25px;
        margin: 0 auto;
        position: relative;
    }

    &__block {
        &--info {
            max-width: 320px;
            width: 100%;
            flex-shrink: 0;
        }

        &--img {
            flex-grow: 1;
        }
    }

    &__block-wrap {}

    &__title {
        @include font(36, 500);
        margin-bottom: 24px;
    }

    &__mobile-img {
        display: none;
        max-height: 350px;
        overflow: hidden;
        margin-bottom: 20px;

        img {
            max-width: 100%;
            width: 100%;
            height: auto;
            object-fit: contain;
            object-position: center center;
        }

        &--slider {
            max-height: 100%;
        }
    }

    &__subtitle {
        @include font(16, 400);
        color: var(--gray-500);
        margin-bottom: 30px;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center center;
    }

    &__tab-block {
        .noactive {
            display: block;
        }

        .block {
            display: block;
        }
    }

    &__tabs {
        display: none;
        max-width: 100%;
        width: 100%;
        height: 50px;
        border-top: 1px solid var(--gray-300);
        border-bottom: 1px solid var(--gray-300);
    }

    &__tab-btn {
        width: 100%;
        @include font(16, 500);
        text-transform: uppercase;
        text-align: center;
        position: relative;

        &::after {
            content: '';
            width: 0;
            height: 2px;
            background: var(--black);
            position: absolute;
            left: 0;
            right: 0;
            bottom: -1px;
            margin: 0 auto;
            z-index: 1;
            transition: .3s;
        }

        &.active {
            &::after {
                width: 100%;
            }
        }

        &:not(:last-child) {
            &::before {
                content: '';
                width: 1px;
                height: 20px;
                background-color: var(--gray-300);
                position: absolute;
                top: 0;
                right: -1px;
                bottom: 0;
                margin: auto 0;
                z-index: 1;
            }
        }
    }
}

.section-portfolio-item-task {
    margin-bottom: 30px;

    &__title {
        @include font(18, 500);
        margin-bottom: 15px;
    }

    &__list {
        li {
            @include font(16, 400);
            padding-left: 16px;
            margin-bottom: 4px;
            position: relative;

            &:after {
                content: '';
                display: block;
                width: 6px;
                height: 6px;
                background-color: var(--black);
                border-radius: 100%;
                position: absolute;
                top: 6px;
                left: 0;
                z-index: 1;
            }
        }
    }
}

.section-portfolio-item-difi {
    margin-bottom: 30px;

    &__title {
        @include font(18, 500);
        margin-bottom: 15px;
    }

    &__list {
        li {
            @include font(16, 400);
            padding-left: 16px;
            margin-bottom: 4px;
            position: relative;

            &:after {
                content: '';
                display: block;
                width: 6px;
                height: 6px;
                background-color: var(--black);
                border-radius: 100%;
                position: absolute;
                top: 6px;
                left: 0;
                z-index: 1;
            }
        }
    }
}

.section-portfolio-item-work {
    max-width: 335px;
    width: 100%;

    &__title {
        @include font(18, 500);
        margin-bottom: 15px;
    }

    &__drop {
        width: 46px;
        height: 46px;
        margin-bottom: 14px;
        position: relative;

        &-img {
            width: 46px;
            height: 46px;
            border-radius: 50%;
            overflow: hidden;
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            transition: all 0.3s cubic-bezier(.25, .8, .25, 1);

            img {
                display: flex;
                width: 50px;
                height: 50px;
                object-fit: cover;
                object-position: center;
            }
        }

        &-content {
            width: 320px;
            height: auto;
            padding: 6px;
            background: var(--white);
            pointer-events: none;
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            border-radius: 8px;
            position: absolute;
            left: 0;
            bottom: 55px;
            opacity: 0;
            transition: .2s;
            z-index: 1;

            img {
                display: flex;
                width: 100%;
                height: auto;
                object-fit: contain;
                border-radius: 6px;
            }
        }

        &:hover {
            .section-portfolio-item-work__drop-img {
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            }

            .section-portfolio-item-work__drop-content {
                pointer-events: all;
                opacity: 1;
            }
        }
    }


    &__subtitle {
        @include font(16, 400);
    }
}

.section-portfolio__item-trigger {
    &.active {
        .section-portfolio__hor-line {
            width: 100%;
            animation-name: lineHorWidth;
            animation-duration: 0.75s;
            animation-fill-mode: forwards;
            animation-timing-function: linear;
        }

        .section-portfolio__ver-line {
            width: 100%;
            animation-name: lineVerHeight;
            animation-duration: 0.75s;
            animation-fill-mode: forwards;
            animation-timing-function: linear;
        }

        .section-portfolio__hor-line--bottom {
            width: 100%;
            animation-name: lineHorWidth;
            animation-duration: .75s;
            animation-fill-mode: forwards;
            animation-timing-function: linear;
        }
    }
}

.section-portfolio-slider {
    min-height: 100%;
    &__img-after {
        min-width: calc(100vw - 405px) !important;
        min-height: 100%;
    }
    &__img-before-wrap {
        min-width: calc(100vw - 405px) !important;
        min-height: 100%;
    }
    &__switch {
        height: 100%;
    }
    &__drag-line {
        min-height: 100%;
        &:before,
        &:after {
            height: calc(100% - 21px) !important;
        }
    }
}

@keyframes lineHorWidth {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}

@keyframes lineVerHeight {
    0% {
        width: 1px;
        height: 0;
    }

    100% {
        width: 1px;
        height: 100%;
    }
}

@include to(1200px) {
    .section-portfolio {
        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(62, 500);
        }

        &__ver-line {
            margin: 0 auto 0 361px;
        }
    }

    .section-portfolio-item {
        &__wrap {
            gap: 45px;
            padding: 20px 0 20px 20px;
        }
    }

    .section-portfolio-item-work {

        &__drop {
            width: 36px;
            height: 36px;

            &-img {
                width: 36px;
                height: 36px;
            }
        }
    }
    .section-portfolio-slider {
        &__img-after {
            min-width: calc(100vw - 385px) !important;
        }
        &__img-before-wrap {
            min-width: calc(100vw - 385px) !important;
        }
    }
}

@include to(768px) {
    .section-portfolio {
        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500);
            text-align: center;
            margin-bottom: 50px;
        }

        &__hor-line {
            background-color: var(--gray-400);
        }

        &__ver-line {
            display: none;
            margin: initial;
        }
    }

    .section-portfolio-item {
        &__wrap {
            padding: 50px 0 0 0;
            gap: 0;
        }

        &__block {
            &--info {
                max-width: 100%;
                padding-right: 0;
            }

            &--img {
                display: none;
            }
        }

        &__title {
            @include font(40, 500);
            text-align: center;
        }

        &__mobile-img {
            display: block;
        }

        &__title,
        &__subtitle {
            padding: 0 10px;
        }

        &__block-wrap {
            max-width: 100%;
        }

        &__tab-block {
            margin-bottom: 30px;

            .noactive {
                display: none;
            }
        }

        &__tabs {
            display: flex;
        }

        &__tab-content {
            padding: 25px 10px;
        }
    }

    .section-portfolio-item-work {
        max-width: 100%;

        &__drop {
            width: initial;
            height: initial;

            &-img {
                width: 100%;
                height: auto;
                border-radius: initial;
                box-shadow: initial;

                img {
                    width: auto;
                    height: 200px;
                }
            }

            &-content {
                display: none;
            }
        }

        &:hover {
            .section-portfolio-item-work__drop-img {
                box-shadow: initial;
            }

            .section-portfolio-item-work__drop-content {
                pointer-events: initial;
                opacity: initial;
            }
        }
    }

    .section-portfolio-item-task,
    .section-portfolio-item-difi,
    .section-portfolio-item-work {
        margin-bottom: initial;

        &__title {
            display: none;
        }
    }

    .section-portfolio-slider {
        &__img-after {
            min-width: 100vw !important;
        }
        &__img-before-wrap {
            min-width: 100vw !important;
        }
    }
}

@include to(576px) {
    .section-portfolio-item {
        &__mobile-img {
            margin-bottom: 10px;
        }

        &__title {
            @include font(32, 500);
        }

        &__subtitle {
            @include font(16, 400);
            margin-bottom: 20px;
        }

        &__tab-btn {
            @include font (14, 500);
        }
    }

    .section-portfolio-item-task,
    .section-portfolio-item-difi,
    .section-portfolio-item-work {
        &__list {
            li {
                @include font(15, 400);
            }
        }
    }
}

@include to(520px) {
    .section-portfolio {
        &__title {
            @include font(40, 500);
        }
    }

    .section-portfolio-item {
        &__title {
            @include font(28, 500);
        }
    }
}

@include to(410px) {
    .section-portfolio-item {
        &__tab-btn {
            @include font (12, 500);
        }
    }

}