.footer
  position: relative
  border-top: 1px solid #ddd
  background: #efefef
  &__nav
    &-top
      padding-top: 30px
    &-bottom
      padding-top: 30px
      padding-bottom: 30px
  &__info
    display: flex
    @include font(15, 400, 1.2)
    gap: 30px
    flex-direction: column
  &__info-contact
    & > a
      border-bottom: 1px solid rgba(0,0,0,0)
      transition: .15s
      &:hover
        border-bottom: 1px solid rgba(0,0,0,1)
  &__info-block
    max-width: 200px
    +to(768px)
      max-width: 100%
    &--flex
      display: flex
      flex-direction: column
      justify-content: space-between
  &__info-phone
    color: black
    @include font(18, 400)
  &__info-title
    color: black
  &__info-text
    color: black
    margin-top: 14px
    display: inline-block
    a
      text-decoration: none
      &:hover
        color: inherit
  &__autor
    display: flex
    gap: 20px
    flex-wrap: wrap
    border-top: 1px solid #ddd
    padding-top: 30px
    padding-bottom: 30px
    color: #a6a6a6

.footer-copy-menu
  &__ul
    display: flex
    flex-wrap: wrap
    column-gap: 20px
    row-gap: 10px
  &__link,
  &__link:visited
    color: black
    &:hover
      text-decoration: underline

.footer-nav-top
  display: flex
  flex-wrap: wrap
  gap: 30px
  row-gap: 15px
  &__link
    @include font(15)
    color: black
    border-bottom: 1px solid rgba(0,0,0,0)
    transition: .15s
    &:hover
      border-bottom: 1px solid rgba(0,0,0,1)

.footer-nav-bottom
  font-size: 15px
  display: flex
  flex-direction: row
  gap: 30px
  +to(768px)
    flex-direction: column
  & a.footer-nav-bottom__title
    display: inline-block
    border-bottom: 1px solid rgba(0,0,0,0)
    transition: .15s
    &:hover
      border-bottom: 1px solid rgba(0,0,0,1)
  &__title
    font-weight: 500
    text-transform: uppercase
    color: black
    margin-bottom: 15px
    width: fit-content
    display: inline-block
  &__ul
    display: flex
    flex-direction: column
    gap: 10px
  &__link
    position: relative
    color: black
    transition: .15s
    border-bottom: 1px solid rgba(0,0,0, 0)
    &:hover
      border-bottom: 1px solid rgba(0,0,0,1)
