.top-block-wrap
  position: relative
  height: 700px
  &::after
    content: ''
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: inherit
    // background: linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(255, 0, 0, 0) 60%)
    // +to(520px)
    //   background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(255, 0, 0, 0) 44%)

.top-block
  color: #fff
  background-repeat: no-repeat
  background-position: center top
  // background-image: url(../img/top-1920x840.webp) !important
  background-size: cover
  position: relative
  z-index: 1

  &-title
    padding-top: 150px
    padding-bottom: 30px

.top-block-title-h1,
.top-block-title-h1-after
  @include font(60)
  display: inline

.top-block-video
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: inherit
  z-index: 0
  object-fit: cover
  source
    object-fit: cover

.top-block-image
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: inherit
  z-index: 0
  img
    object-fit: cover
    height: inherit
    width: inherit

.top-block-desc
  width: 600px
  height: 72px
  padding: 12px 15px
  display: flex
  justify-content: space-between
  border-radius: 4px
  background: hsla(0,0%,100%,.15)
  -webkit-backdrop-filter: blur(20px)
  backdrop-filter: blur(20px)
  color: white

  &-slider-e
    height: 48px
    line-height: 48px
    padding-left: 25px
    background: url(../img/check-mark.svg) no-repeat left center
    background-size: auto 15px
    border: none !important
    font-weight: 500

  &-but .but1
    width: 226px
    height: 46px
    background: #fff
    position: relative
    cursor: pointer
    font-weight: 500

  &-but .but1-text
    line-height: 46px

@media only screen and (max-width: 1600px)
  .top-block-wrap
    height: 840px

@media only screen and (max-width: 1534px)
  .top-block-wrap
    height: 800px

@media only screen and (max-width: 1440px)
  .top-block-wrap
    height: 800px

@media only screen and (max-width: 1366px)
  .top-block-wrap
    height: 700px

@media only screen and (max-width: 1280px)
  .top-block-wrap
    height: 700px

@media only screen and (max-width: 799px)
  .top-block-title-h1,
  .top-block-title-h1-after
    font-size: 42px

  .top-block-desc
    width: auto
    height: auto
    padding: 0
    display: block
    border-radius: 0
    background: none
    -webkit-backdrop-filter: none
    backdrop-filter: none

    &-slider
      display: none !important

@media only screen and (max-width: 520px)
  .top-block-wrap
    height: 100vh

  .top-block
    height: 100vh

    &-title
      padding-top: 200px

  .top-block-title-h1,
  .top-block-title-h1-after
    font-size: 30px

  .top-block-title br
    display: none
