.section-animation-head {
    height: 100%;

    &__blocks {
        display: flex;
    }

    &__block {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 100vh;

        &--bg-color {
            background-color: #D7D7D7;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 36px;
    }

    &__item {
        padding: 22px 34px;
        @include font(18, 400, 1);
        color: var(--black);
        background-color: var(--white);
        border-radius: 6px;
        transform: scale(1);
        position: relative;
        transition: .15s ease-in-out;

        &::before {
            content: "";
            position: absolute;
            left: 11%;
            bottom: -9px;
            z-index: 1;
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-top: 9px solid var(--white);
            transition: .3s ease-in-out;
        }

        &.active {
            color: var(--white);
            background-color: var(--black);

            &::before {
                left: 80%;
                border-top: 9px solid var(--black);
            }
        }
    }

    &-info {
        max-width: 555px;
        width: 100%;
        padding: 0 40px;
        transition: .2s;

        &__num {
            @include font(20, 500, 1);
            margin-bottom: 14px;
        }

        &__title {
            @include font(40, 500, 1);
            margin-bottom: 40px;
        }

        &__text {
            @include font(17, 400, 1.4);
            padding-left: 32px;
        }
    }

    @include to(1320px) {
        &__item {
            padding: 19px 30px;
            @include font(17, 400, 1);

            &::before {
                bottom: -8px;
            }
        }

        &-info {
            max-width: 490px;

            &__num {
                @include font(18, 500, 1);
                margin-bottom: 10px;
            }

            &__title {
                @include font(36, 500, 1);
                margin-bottom: 30px;
            }

            &__text {
                @include font(16, 400, 1.4);
                padding-left: 24px;
            }
        }
    }

    @include to(1200px) {
        &__items {
            gap: 26px;
        }

        &__item {
            padding: 15px 26px;
            @include font(16, 400, 1);

            &::before {
                bottom: -6px;
                border-left: 8px solid transparent;
                border-right: 8px solid transparent;
                border-top: 7px solid var(--white);
            }

            &.active {
                &::before {
                    border-top: 7px solid var(--black);
                }
            }
        }

        &-info {
            &__title {
                @include font(32, 500, 1);
                margin-bottom: 27px;
            }
        }
    }

    @include to(1024px) {
        // height: 565px;

        &__block {
            // height: 565px;
        }
    }

    @include to(950px) {
        &__item {
            padding: 12px 20px;
            @include font(15, 400, 1);
        }
    }

    @include to(768px) {
        &-info {
            max-width: 100%;
            padding: 0 30px;

            &__num {
                @include font(16, 500, 1);
                margin-bottom: 8px;
            }

            &__title {
                @include font(28, 500, 1);
                margin-bottom: 22px;
            }

            &__text {
                @include font(15, 400, 1.4);
                padding-left: 18px;
            }
        }
    }

    @include to(720px) {
        &__blocks {
            flex-direction: column;
        }

        &__block {
            width: 100%;
            height: auto;
            padding: 40px 30px;

            &--bg-color {
                padding: 100px 30px;
            }
        }

        &-info {
            max-width: 555px;
            padding: initial;
        }
    }

    @include to(520px) {
        &__block {
            padding: 40px 20px;

            &--bg-color {
                padding: 100px 20px;
            }
        }
    }
}