.section-blog-open {
    padding: 160px 0 0;
    margin: 0 0 70px 0;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__blocks {
        margin-bottom: 90px;
    }

    &__block {

        &-wrap,
        &-head {
            display: grid;
            grid-template-rows: repeat(1, 1fr);
            grid-template-columns: repeat(12, 1fr);
            grid-gap: 40px;
        }

        &-item {
            grid-column: 3 / 11;

            &--1 {
                grid-column: auto/span 2;
            }

            &--2 {
                grid-column: auto/span 8;
            }
        }
    }

    &-nav {
        &__items {
            display: flex;
            flex-direction: column;
        }

        &__item {
            padding: 16px 0;
            text-transform: uppercase;
            border-top: 1px solid var(--gray-300);

            &:last-child {
                border-bottom: 1px solid var(--gray-300);
            }

            &-title {
                color: var(--gray-700);
                @include font(14, 400, 1);
                margin-bottom: 16px;
            }

            &-subtitle {
                @include font(14, 400, 1);
            }
        }

        &-share {
            &__list {
                display: flex;
                align-items: center;
                gap: 16px;

                img,
                svg {
                    width: 22px;
                    height: 22px;
                    transition: .2s;
                }

                svg {
                    color: var(--gray-700);
                }

                li {
                    a {
                        &:hover {
                            svg {
                                color: var(--black);
                            }
                        }
                    }
                }
            }
        }
    }

    &-services {
        margin: 20px 0 0 0;
        &__title {
            margin: 0 0 20px 0;
            @include font(14, 600, 1);
            text-transform: uppercase;
        }
        &__description {
            margin: 20px 0;
            @include font(13, 400, 1);
            color: var(--gray-700);
        }
        &__list {
            margin: 20px 0 0 0;
            padding: 0 5px;
            display: flex;
            flex-direction: column;
            gap: 15px;
            &-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                position: relative;
                padding-left: 16px;
                @include font(13, 600, 1);
                transition: .2s;

                &:before {
                    position: absolute;
                    top: 50%;
                    left: 0;
                    margin-top: -3px;
                    display: block;
                    border: 1px solid var(--black);
                    content: '';
                    height: 6px;
                    width: 6px;
                    transform: rotate(-45deg);
                    transition: .3s;
                }

                &-icon {
                    width: 14px;
                    height: 8px;
                    transform: rotate(-90deg);
                    transition: .3s;
                    svg {
                        width: 14px;
                        height: 8px;
                    }
                }

                &:hover {
                    &:before {
                        background: var(--black);
                        transform: rotate(0);
                    }
                    .section-blog-open-services__list-item-icon {
                        transform: rotate(-90deg) translateY(2px);
                    }
                }
            }
        }
    }

    &__breadcrumbs {
        width: auto;
        height: auto;
        margin-bottom: 40px;

        span {
            display: inline-block;
            @include font(14, 400, 1);
            color: var(--gray-700);
            text-transform: uppercase;
            border-bottom: 1px solid transparent;
            word-wrap: break-word;
            transition: .15s;

            a {
                display: block;
                transition: .15s;
            }

            &:not(:first-child) {
                margin-left: 10px;
                position: relative;

                &::before {
                    content: '/';
                    color: var(--gray-700);
                    position: absolute;
                    width: 6px;
                    height: 12px;
                    top: 0;
                    left: -10px;
                    z-index: 1;
                }
            }

            &:not(:last-child) {
                &:hover {
                    a,
                    span {
                        color: var(--black);
                    }
                }
            }
        }
    }

    &__btn {
        display: inline-block;
        margin-bottom: 24px;

        &-wrap {
            display: flex;
            align-items: center;
        }

        &-icon {
            margin-right: 12px;

            svg {
                width: 18px;
                height: 18px;
            }
        }

        &-title {
            @include font(14, 400, 1.4);
            white-space: nowrap;
            text-transform: uppercase;
            text-decoration: underline transparent;
            transition: .2s;
        }

        &:hover {
            .section-blog-open__btn-title {
                text-decoration: underline;
            }
        }
    }

    &__title {
        @include font(40, 500, 1.2);
        margin: 0 0 24px;
    }

    &__subtitle {
        @include font(18, 400, 1.4);
        color: var(--gray-700);
        margin-bottom: 24px;
    }

    &__categories {
        margin: 24px 0;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
    }

    &__category-link {
        padding: 8px 16px;
        height: 30px;
        display: inline-flex;
        align-items: center;
        @include font(14, 400, 1);
        text-transform: uppercase;
        border-radius: 100px;
        background: #202641;
        color: rgba(255,255,255,0.89);
        transition: .2s;
        &:hover {
            background: rgba(0,0,0,0.89);
        }
    }

    &__img {
        max-width: 100%;
        width: 100%;

        img {
            object-fit: contain;
            height: auto;
        }
    }

    &-content {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 40px;


        &__description {
            grid-column: 3 / 11;

            img {
                margin: 16px 0 40px;
            }

            h1 {
                @include font(40, 500, 1.2);
                margin: 0 0 24px;
            }

            h2 {
                @include font(32, 500, 1.2);
                margin-bottom: 24px;
            }

            h3 {
                @include font(28, 500, 1.2);
                margin-bottom: 24px;
            }

            h4 {
                @include font(24, 500, 1.2);
                margin-bottom: 24px;
            }

            h5 {
                @include font(20, 500, 1.2);
                margin-bottom: 24px;
            }

            h6 {
                @include font(18, 500, 1.2);
                margin-bottom: 24px;
            }

            p {
                @include font(18, 400, 1.4);
                margin-bottom: 24px;

                img {
                    margin: 0 0 15px;
                }

                blockquote {
                    margin: 0 16px 24px;
                }
            }

            a {
                @include font(18, 400, 1.2);
                color: var(--cobalt);
                text-decoration: underline;
                transition: .2s;

                &:hover {
                    color: var(--cobaltDark);
                }
            }

            b {
                font-weight: 600;
            }

            mark {
                background-color: #E7F3FE;
                @include font(18, 400, 1.4);
                padding-left: 6px;
                padding-right: 6px;
                margin-bottom: 24px;
            }

            blockquote {
                @include font(18, 400, 1.4);
                border-left: 4px solid #106FC8;
                background-color: #E7F3FE;
                padding: 16px 16px 16px 24px;
                margin: 24px 16px;
                *:last-child {
                    margin-bottom: 0;
                }
            }

            ul {
                margin-bottom: 24px;

                li {
                    @include font(18, 400, 1.4);
                    padding: 5px 0 5px 20px;
                    position: relative;

                    &:before {
                        content: '-';
                        display: flex;
                        position: absolute;
                        top: 4px;
                        left: 0;
                        z-index: 1;
                    }
                }
            }
        }
    }

    @include to(1200px) {
        padding: 125px 0 0;
        margin: 0 0 70px 0;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(36, 500, 1.2);
            margin: 0 0 20px;
        }

        &__subtitle {
            @include font(16, 400, 1.4);
            margin-bottom: 20px;
        }

        &__blocks {
            margin-bottom: 80px;
        }

        &__block {

            &-wrap,
            &-head {
                grid-gap: 25px;
            }
        }

        &__breadcrumbs {
            margin-bottom: 26px;
        }

        &__btn {
            margin-bottom: 20px;

            &-icon {
                margin-right: 10px;

                svg {
                    width: 16px;
                    height: 16px;
                }
            }
        }

        &-content {
            grid-gap: 20px;

            &__description {
                img {
                    margin: 16px 0 30px;
                }

                h1 {
                    @include font(36, 500, 1.2);
                    margin: 0 0 20px;
                }

                h2 {
                    @include font(28, 500, 1.2);
                    margin-bottom: 20px;
                }

                h3 {
                    @include font(24, 500, 1.2);
                    margin-bottom: 20px;
                }

                h4 {
                    @include font(20, 500, 1.2);
                    margin-bottom: 20px;
                }

                h5 {
                    @include font(18, 500, 1.2);
                    margin-bottom: 20px;
                }

                h6 {
                    @include font(16, 500, 1.2);
                    margin-bottom: 20px;
                }

                p {
                    @include font(16, 400, 1.4);
                    margin-bottom: 20px;

                    blockquote {
                        margin: 0 6px 20px;
                    }
                }

                a {
                    @include font(16, 400, 1.2);
                }

                mark {
                    @include font(16, 400, 1.4);
                    padding-left: 0;
                    padding-right: 0;
                    margin-bottom: 20px;
                }

                blockquote {
                    @include font(16, 400, 1.4);
                    padding: 12px 12px 12px 20px;
                    margin: 20px 6px;
                }

                ul {
                    margin-bottom: 20px;

                    li {
                        @include font(16, 400, 1.4);
                        padding: 3px 0 3px 20px;
                    }
                }
            }
        }
    }

    @include to(1024px) {
        padding: 125px 0 0;
        margin: 0 0 70px 0;

        &__blocks {
            margin-bottom: 60px;
        }

        &__block {

            &-wrap,
            &-head {
                display: flex;
                flex-direction: column;
                grid-template-rows: initial;
                grid-template-columns: initial;
                grid-gap: initial;
                gap: 25px;
            }

            &-item {
                grid-column: initial;

                &--1 {
                    grid-column: initial;
                    order: 2;
                }

                &--2 {
                    grid-column: initial;
                    order: 1;
                }
            }
        }

        &-services {
            &__list {
                padding: 0;
                flex-direction: row;
                flex-wrap: wrap;
                gap: 10px;
                &-item {
                    flex: 0 0 calc( (100% - 10px) / 2 );
                    justify-content: flex-start;
                    &-text {
                        width: fit-content;
                    }
                    &-icon {
                        display: none;
                    }
                }
                /*
                &-item {
                    gap: 0;
                    padding: 0;

                    &:before {
                        display: none;
                    }
                    &:after {
                        content: ',';
                        padding-right: 5px;
                    }
                    &:last-child {
                        &:after {
                            display: none;
                        }
                    }
    
                    &-icon {
                        display: none;
                    }
    
                    &:hover {
                        text-decoration: underline;
                    }
                }
                */
            }
        }

        &-content {
            grid-template-columns: repeat(1, 1fr);

            &__description {
                grid-column: auto/span 1;
            }
        }
    }

    @include to(768px) {
        padding: 108px 0 0;
        margin: 0 0 50px 0;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(32, 500, 1.2);
            margin: 0 0 16px;
        }

        &__subtitle {
            margin-bottom: 18px;
        }
        
        &__blocks {
            margin-bottom: 45px;
        }

        &__block {

            &-wrap,
            &-head {
                gap: 20px;
            }
        }

        &-nav {
            &__item {
                padding: 14px 0;

                &-title {
                    margin-bottom: 12px;
                }
            }
        }
        
        &-content {
            grid-gap: 20px;

            &__description {
                img {
                    margin: 10px 0 18px;
                }

                h1 {
                    @include font(32, 500, 1.2);
                    margin: 0 0 16px;
                }

                h2 {
                    @include font(26, 500, 1.2);
                    margin-bottom: 16px;
                }

                h3 {
                    @include font(22, 500, 1.2);
                    margin-bottom: 16px;
                }

                h4 {
                    @include font(18, 500, 1.2);
                    margin-bottom: 16px;
                }

                h5 {
                    @include font(17, 500, 1.2);
                    margin-bottom: 16px;
                }

                h6 {
                    margin-bottom: 16px;
                }

                p {
                    margin-bottom: 16px;

                    blockquote {
                        margin: 0 0 16px;
                    }
                }
                
                mark {
                    margin-bottom: 16px;
                }

                blockquote {
                    padding: 10px 10px 10px 18px;
                    margin: 16px 0;
                }

                ul {
                    margin-bottom: 16px;

                    li {
                        padding: 3px 0 3px 16px;
                    }
                }
            }
        }
    }

    @include to(520px) {
        padding: 95px 0 0;
        margin: 0 0 50px 0;
    }

    @include to(380px) {
        &-services {
            &__list {
                &-item {
                    flex: 0 0 100%;
                }
            }
        }
    }
}