.section-contact {
    padding: 200px 0 140px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__bricks {
        display: flex;
        flex-direction: column;
    }

    &__brick {
        overflow: hidden;

        &:not(:first-child) {
            padding-top: 65px;
        }

        &:not(:last-child) {
            padding-bottom: 80px;
            border-bottom: 1px solid var(--gray-300);
        }

        iframe {
            max-width: 100% !important;
            width: 100% !important;
            height: 500px !important;
        }
    }

    &__title {
        @include font(92, 500, 1);
        margin: 0;
        transition: .3s;
    }

    &__blocks {
        display: flex;
        justify-content: space-between;
    }

    &__block {
        width: 100%;
    }

    &__items {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 50px;
        max-width: 600px;
        width: 100%;
        padding-top: 10px;

        &-wrap {
            display: flex;
            justify-content: space-between;
            flex-grow: 1;
        }
    }

    &__item {
        max-width: 250px;
        width: 100%;
        height: 100%;

        &:nth-child(1) {
            margin-right: 35px;
        }
    }

    &__item-title {
        @include font(20, 500, 1);
        margin-bottom: 20px;
    }

    &__item-list {

        &--num {
            display: block;
            margin-left: 17px;

            li {
                list-style-type: decimal;
                list-style-position: outside;
                padding-left: 5px;
            }
        }

        &--social-btn {
            li {
                display: flex;
                gap: 15px;

                a {
                    border-bottom: initial;

                    svg {
                        display: flex;
                        width: 24px;
                        height: 24px;
                        color: var(--black);
                        transition: .2s ease-in-out;
                    }

                    &:nth-child(1):hover {
                        svg {
                            color: var(--blue-telegram);
                        }
                    }

                    &:nth-child(2):hover {
                        svg {
                            color: var(--green-whatsapp);
                        }
                    }
                }

            }

        }

        li:not(:last-child) {
            margin-bottom: 10px;
        }

        li,
        a {
            @include font(16, 400);
            color: var(--gray-800);
        }

        a {
            border-bottom: 1px solid var(--gray-800);
            transition: .2s ease-in-out;

            &:hover {
                color: var(--black);
                border-color: var(--black);
            }
        }
    }
}

.section-contact-slider {
    max-width: 1400px;
    width: 100%;
    overflow: hidden;

    &__title {
        @include font(62, 500, 1);
        margin-bottom: 60px;
        transition: .3s;
    }

    &__slide {
        width: 100%;
        margin-right: 16px;
    }

    &__slide-img {
        width: 100%;
        height: 520px;
        overflow: hidden;
        transition: .3s;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
            transition: .3s;
        }
    }

    &__blocks {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-top: 30px;
        margin-left: auto;
        width: 200px;
    }

    &__block {
        &:last-child {
            margin-left: 20px;
        }
    }

    &__arrows {
        display: flex;
        align-items: center;
    }

    &__prev {
        transform: scaleX(-1);
    }

    &__next,
    &__prev {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px;
        cursor: pointer;

        svg {
            display: flex;
            width: 16px;
            height: 16px;
            color: var(--black);
            transition: .3s;
        }

        &:hover {
            svg {
                color: var(--gray-800);
            }
        }
    }
}

@include to(1200px) {
    .section-contact {
        padding: 140px 0 140px;

        &__wrap {
            padding: 0 20px;
        }

        &__brick {
            &:not(:first-child) {
                padding-top: 54px;
            }

            &:not(:last-child) {
                padding-bottom: 45px;
            }
        }

        &__title {
            @include font(80, 500, 1);
        }

        &__city {
            @include font(52, 500, 1);
        }

        .section-callback-form {
            padding-top: initial;
        }
    }

    .section-contact-slider {
        &__title {
            @include font(52, 500, 1);
            margin-bottom: 36px;
        }

        &__slide-img {
            height: 450px;
        }
    }

}

@include to(1100px) {
    .section-contact {
        &__blocks {
            gap: 80px;
        }

        &__block {
            width: initial;

            &:first-child {
                width: auto;
            }

            &:last-child {
                width: 100%;
            }
        }

        &__items {
            max-width: 100%;
        }
    }
}

@include to(1024px) {
    .section-contact {
        padding: 120px 0 100px;

        &__brick {
            &:not(:first-child) {
                padding-top: 38px;
            }

            &:not(:last-child) {
                padding-bottom: 38px;
            }
        }

        &__title {
            @include font(62, 500, 1);
        }

        &__blocks {
            gap: initial;
        }

        &__block {
            &:first-child {
                width: 100%;
            }

            &:last-child {
                width: 100%;
            }
        }
    }

    .section-contact-slider {
        &__title {
            @include font(42, 500, 1);
            margin-bottom: 30px;
        }
    }
}

@include to(874px) {
    .section-contact {

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            margin-bottom: 50px;
        }

        &__city {
            @include font(42, 500, 1);
            margin-bottom: 40px;
        }

        &__blocks {
            flex-direction: column;
        }

        &__items {
            max-width: 100%;
            gap: 38px;
            padding-top: initial;
        }

        &__item-title {
            @include font(18, 500, 1);
            margin-bottom: 17px;
        }

        &__item-list {

            li,
            a {
                @include font(15, 400);
            }
        }
    }

    .section-contact-slider {
        &__slide-img {
            height: 300px;
        }
    }
}

@include to(520px) {
    .section-contact {
        padding: 100px 0 80px;

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 34px;
        }

        &__city {
            @include font(32, 500, 1);
            margin-bottom: 29px;
        }

        &__items {
            gap: 0;

            &-wrap {
                flex-direction: column;
                justify-content: flex-start;

                & {
                    .section-contact__item {
                        margin-bottom: 35px;
                    }
                }

                &:last-child {
                    .section-contact__item {
                        &:last-child {
                            margin-bottom: initial;
                        }
                    }
                }
            }
        }

        &__item {
            max-width: 100%;

            &:nth-child(1) {
                margin-right: initial;
            }
        }

        &__item-title {
            margin-bottom: 10px;
        }

        .section-callback-form {
            margin-top: 50px;
        }
    }

    .section-contact-slider {
        &__slide-img {
            height: 200px;
        }
    }

}