.section-about-principles {
    background-color: rgb(0, 0, 0);
    padding-right: 40px;
    padding-bottom: 120px;
    h3 {
        padding-top: 25px;
        padding-bottom: 200px;
        font-size: 140px;
        font-weight: 500;
        text-align: center;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
    }
    &__inner {
        width: 100%;
        p {
            width: 100%;
        }
        div {
            display: flex;
            justify-content: flex-start;
            padding-right: 40px;
            padding: 25px 0;
            border-top: 1px solid #a6a6a6;
            width: 660px;
            margin-left: auto;
            margin-right: auto;
            h4 {
                min-width: 200px;
                font-size: 25px;
                font-weight: 500;
            }
            p {
                font-size: 16px;
                font-weight: 400px;
                color: #a6a6a6;
                line-height: 1.5;
            }
        }
    }
}
.body--advantages {
    .section-about-principles {
        background-color: rgb(255, 255, 255);
        padding-top: 80px;
        h3 {
            color: #000;
        }
        &__inner {
            color: #000;
            div {
                p {
                    color: #000;
                }
            }
        }
    }
}

@include to(1023px) {
    .section-about-principles {
        padding-left: 20px;
        padding-right: 20px;
        h3 {
            font-size: 100%;
        }
    }
}
@include to(799px) {
    .section-about-principles {
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 40px;
        h3 {
            font-size: 40px;
            padding-bottom: 25px;
        }
        &__inner {
            div {
                width: 100%;
                h4 {
                    font-size: 25px;
                }
            }
        }
    }
}