.guarantee-wrap
  padding-top: 90px
  position: relative
  background: #fff

.guarantee

  &-info
    display: flex
    flex-wrap: wrap
    justify-content: space-between

  &-info-e
    width: calc(50% - 20px)
    margin-top: 45px

.guarantee-info-e.e1,
.guarantee-info-e.e2
  margin-top: 0

.guarantee-info-e-num
  margin-bottom: 20px

.guarantee-info-e-title
  @include font(22)
  margin-bottom: 15px

.guarantee-info-e-desc
  font-size: 15px
  font-weight: 400
  color: #6e6e6e

@media only screen and (max-width: 520px)

  .guarantee-wrap
    padding-top: 60px

  .guarantee-info
    display: block

    &-e
      width: auto
      margin-top: 30px

    &-e.e2
      margin-top: 30px
