.section-about-actuality {
    background-color: rgb(255, 255, 255);
    padding: 25px 40px 120px 40px;
    h3 {
        color: rgb(0, 0, 0);
        font-size: 22px;
        font-weight: 500;
    }
    &__inner {
        margin-left: 30%;
        margin-top: -25px;
        width: 100%;
        div {
            margin-top: 25px;
            span {
                color: rgb(0, 0, 0);
            }
            h4 {
                color: rgb(0, 0, 0);
                font-size: 45px;
                font-weight: 500;
            }
            p {
                font-size: 18px;
                font-weight: 400;
                color: #a6a6a6;
            }
            p, h4 {
                width: 45%;
            }
        }
    }
    &__image-wrap {
        margin-top: 25px;
        margin-left: 30%;
    }
}
@include to(1023px) {
    .section-about-actuality {
        padding-left: 20px;
        padding-right: 20px;
    }
}
@include to(799px) {
    .section-about-actuality {
        padding-left: 10px;
        padding-right: 10px;
        &__inner {
            span {
                font-size: 60px;
            }
            div {
                p, h4 {
                    width: 100%;
                    margin-bottom: 15px;
                }
                h4 {
                    font-size: 28px;
                }
            }
        }
        &__image-wrap {
            margin-left: 0;
        }
    }
}