.section-facade-design {
    position: relative;

    &__img {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        position: relative;
        pointer-events: none;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, .17);
            pointer-events: none;
        }
    }

    &__wrap {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: 3;
        padding: 150px 40px 5px 40px;
        pointer-events: none;
        
        & * {
            pointer-events: all;
        }
    }

    &__title {
        @include font(60, 500, 1);
        margin: 0 0 30px;
        color: var(--white);
    }

    &__dots {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }

    &__dots-wrap {
        height: 100%;
        position: relative;
    }

    &__dot {
        width: 34px;
        height: 34px;
        position: absolute;
        z-index: 1;

        &:hover, &.active {
            z-index: 10;

            .section-facade-design__dot-num {
                background-color: #d40136;
                color: var(--white);
            }

            .section-facade-design__card {
                pointer-events: all;
                opacity: 1;
                z-index: 5;
            }
        }
    }

    &__dot-wrap {
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            z-index: -1;
            width: 34px;
            height: 34px;
            border-radius: 50%;
            animation-duration: 2.1s;
            animation-name: pulseFacadeDesign;
            animation-iteration-count: infinite;
        }
    }

    &__dot-num {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 34px;
        border-radius: 40px;
        background-color: var(--white);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        @include font(14, 500);
        cursor: default;
        transition: .2s;
    }

    @keyframes pulseFacadeDesign {
        0% {
            scale: 1;
            background-color: rgba(#fff, 0);
        }

        90% {
            scale: 1.65;
            background-color: rgba(#fff, 1);
        }


        100% {
            background-color: rgba(#fff, 0);
            scale: 1;
            opacity: 0;
        }
    }

    &__card {
        pointer-events: none;
        width: 140px;
        height: auto;
        background-color: rgba(0, 0, 0, 0.3);
        backdrop-filter: blur(22px);
        -webkit-backdrop-filter: blur(22px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        text-align: center;
        color: var(--white);
        border-radius: 8px;
        overflow: hidden;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        opacity: 0;
        transition: .2s;
    }

    &__card-img {
        width: 100%;
        height: 176px;
        border-radius: 8px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__card-wrap {
        padding: 6px 4px 7px;
    }

    &__card-title {
        @include font(16, 500);
        margin-bottom: 6px;
    }

    &__card-price {
        @include font(15, 400);
    }

    &__dot--1 {
        top: 74%;
        left: 67.5%;
    }

    &__dot--2 {
        top: 46.13%;
        left: 48.7%;
    }

    &__dot--3 {
        top: 70.08%;
        left: 25.04%;
    }

    &__dot--4 {
        top: 57.89%;
        left: 62.01%;
    }

    @include to(1200px) {
        &__wrap {
            padding: 150px 20px 5px 20px;
        }
    }

    @include to(1024px) {
        &__dot--2 {
            top: 46.13%;
            left: 47.5%;
        }
    }

    @include to(768px) {
        &__wrap {
            padding: 150px 10px 5px 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin: 0 0 30px;
        }
    }

    @include to-h(652px) {
        &__dot--1 {
            top: 78%;
            left: 76.5%;
        }

        &__dot--2 {
            top: 50.13%;
            left: 75.5%;
        }

        &__dot--3 {
            top: 77.08%;
            left: 15.04%;
        }

        &__dot--4 {
            top: 59.89%;
            left: 47%;
        }
    }

    @include to-h(568px) {
        &__dot--4 {
            top: 60.89%;
            left: 47%;
        }
    }

    @include to-h(480px) {

        &__dot--2 {
            top: 51.13%;
            left: 78.5%;
        }

        &__dot--3 {
            top: 82.08%;
        }

        &__dot--4 {
            top: 87.89%;
            left: 50%;
        }

    }

    @include to(520px) {
        &__title {
            @include font(36, 500, 1);

            br {
                display: none;
            }
        }
    }
}