.section-facade-design-form {
    padding: 110px 0 110px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__img {
        width: 100%;
        height: 620px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__blocks {
        width: 100%;
        position: relative;
    }

    &__head {
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 100%;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 20px 10px 20px 40px;
        z-index: 1;
    }

    &__title {
        @include font(62, 500, 1);
        color: var(--white);
        margin-bottom: 30px;
    }

    &__subtitle {
        @include font(18, 500);
        color: var(--white);
        margin-bottom: 35px;

        a {
            border-bottom: 1px solid var(--white);
            @include font(15, 500);
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--white);
        color: var(--black);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--black);
        }

        &:hover {
            span {
                color: var(--white);
            }

            &::before {
                width: 100%;
            }
        }
    }

    @include to(1200px) {
        padding: 70px 0 70px;

        &__wrap {
            padding: 0 20px;
        }

        &__head {
            padding: 20px 10px 20px 20px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 26px;
        }

        &__subtitle {
            margin-bottom: 32px;
        }
    }

    @include to(768px) {
        padding: 50px 0 50px;

        &__wrap {
            padding: 0 10px;
        }

        &__head {
            justify-content: flex-start;
            padding: 78px 10px 20px 15px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 24px;
        }

        &__subtitle {
            @include font(17, 500);
            margin-bottom: 30px;
        }
    }

    @include to(520px) {
        padding: 40px 0 40px;

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 20px;
        }

        &__subtitle {
            @include font(16, 500);
            margin-bottom: 28px;

            a {
                @include font(14, 500);
            }
        }

        &__btn {
            width: 180px;
            height: 43px;
        }
    }

    @include to(410px) {
        &__subtitle {
            br {
                display: none;
            }
        }

    }
}