.section-discount-head {
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-color: #A2B8C5;

    &__wrap {
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-position: center bottom -1px;
        background-size: cover;
        background-image: url(../img/section-discount-head-bg.webp);
        scale: 0.63;
        position: relative;
    }

    &__title {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        @include font(140, 500, 1);
        text-transform: uppercase;
        white-space: nowrap;
        color: var(--white);
        margin: 0;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1;
        transition: .75s;
    }

    &__discount {
        display: flex;
        align-items: flex-end;
        height: 33.3vh;
        overflow: hidden;

        &-wrap {
            padding-top: 5%;
            margin-bottom: -2.3%;
            scale: .62;
        }
    }

    &__promo {
        height: 20vh;

        &-wrap {
            padding-top: 13%;
        }
    }

    &__discount,
    &__promo {

        &-wrap {
            cursor: default;

        }
    }

    @include ep-to(658px, 926px) {
        &__discount {
            height: 33.3svh !important;

            &-wrap {
                padding-top: 18% !important;
                margin-bottom: initial !important;
            }
        }

        &__promo {
            &-wrap {
                padding-top: 20% !important;
            }
        }
    }

    @include ep-to(658px, 896px) {
