.anim-header-mob-menu {
    opacity: 0;
    display: inline-block;
    transition-duration: 1.5s;
    transition-delay: 1s;
    transform: rotateX(75deg) rotateY(10deg) rotateZ(-9deg);
    transform-origin: top;
}

.anim-header-mob-menu.active {
    opacity: 1;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
}

/*=========================================================*/

.anim-popup-text {
    opacity: 0;
    display: inline-block;
    transition-duration: 1.5s;
    transform: rotateX(75deg) rotateY(10deg) rotateZ(-9deg);
    transform-origin: top;
}

.anim-popup-text.active {
    opacity: 1;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
}

/*==========================================================*/

.scroll-anim-move-rotate {
    display: inline-block;
    opacity: 0;
    transition-duration: 0.7s;
    transition-delay: 1ms;
    transform: rotateX(75deg) rotateY(10deg) rotateZ(-9deg);
    transform-origin: top;
}

.scroll-anim-move-rotate.visible {
    opacity: 1;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
}

.scroll-anim-move-to-left {
     opacity: 0;
     transition-duration: 0.4s;
    transition-delay: 1ms;
     transform: translateX(15px);
 }

.scroll-anim-move-to-left.visible {
    opacity: 1;
    transform: translateX(0);
}

.scroll-anim-move-to-up {
    opacity: 0;
    transition-duration: 0.4s;
    transition-delay: 1ms;
    transform: translatey(15px);
}

.scroll-anim-move-to-up.visible {
    opacity: 1;
    transform: translatey(0);
}

@media only screen and (max-width: 599px) {
    .scroll-anim-move-rotate {
        opacity: 1 !important;
        transition-duration: 0s !important;
        transition-delay: 0s !important;
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) !important;
    }

    .scroll-anim-move-to-left {
        opacity: 1 !important;
        transition-duration: 0s !important;
        transition-delay: 0ms !important;
        transform: translateX(0) !important;
    }

    .scroll-anim-move-to-up {
        opacity: 1 !important;
        transition-duration: 0s !important;
        transition-delay: 0s !important;
        transform: translatey(0) !important;
    }

    .anim-header-mob-menu {
        opacity: 1 !important;
        transition-duration: 0s !important;
        transition-delay: 0s !important;
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) !important;
    }

    .anim-popup-text {
        opacity: 1 !important;
        transition-duration: 0s !important;
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) !important;
    }
}