.good-wrap
  position: relative
  background: #fff
  border-radius: 0 0 20px 20px

.good
  padding-top: 60px
  padding-bottom: 100px
  // max-width: 1300px;
  &-top
    margin-bottom: 120px
    &-e
      @include font(92)
      +to(520px)
        font-size: 40px

.good-top__bottom
  @include font(20, 500, 1.4)
  max-width: 70%
  margin-top: 10px
  +to(520px)
    font-size: 16px
    max-width: 100%

.good-info
  display: flex
  justify-content: space-between

  &-text-wrap
    flex-shrink: 0
    width: 50%
    padding-left: 50px
    box-sizing: border-box
    display: flex
    flex-direction: column
    justify-content: space-around

  &-text
    display: flex

  &-text-num
    flex-shrink: 0
    width: 100px
    font-size: 140px
    font-weight: 600
    line-height: 0.8

  &-text-word
    flex-grow: 1
    overflow: hidden

  &-text-word-title
    @include font(25)
    margin-bottom: 20px
    +to(520px)
      font-size: 22px
      max-width: 100%

.good-info-text-word-desc__text
  @include font (18, 400)
  +to(520px)
    font-size: 16px

.good-slider-page-main .slick-arrow
  position: absolute
  top: -45px
  width: 20px
  height: 20px

  &.slick-prev
    right: 30px
    background: url("/wp-content/themes/lppowers4_test/images/portfolio/arrow-left.png") no-repeat center

  &.slick-next
    right: 0
    background: url("/wp-content/themes/lppowers4_test/images/portfolio/arrow-right.png") no-repeat center

.good-info-text-word-desc-slab
  display: inline-block
  background: #eee
  padding: 12px 15px
  border-radius: 3px
  line-height: 1
  margin: 0 5px 10px 0

.good-info-text-word-desc-do
  display: inline-block
  line-height: 1
  margin: 0 15px 10px 0
  text-decoration: underline

.good-info-text-word-desc-list li
  margin-bottom: 5px
  background: url(../img/tick-2.png) no-repeat left center
  background-size: auto 14px
  padding-left: 23px

  &:last-child
    margin-bottom: 0

.good-pic-wrap
  width: 545px
  height: 560px
  background: #C8C8C8
  display: flex
  overflow: hidden
  +to(520px)
    width: 100%
    height: 50vh
    margin-bottom: 40px

.good-pic-e
  flex-shrink: 0
  display: flex
  align-items: center
  line-height: 1
  animation-name: one
  animation-duration: 30000ms
  animation-timing-function: linear
  animation-iteration-count: infinite
  +to(520px)
    margin-bottom: 30px

  & > div
    flex-shrink: 0
    padding-left: 30px
    // width: 100%
    height: 100%
    img
      object-fit: contain
      height: 100%
      width: 100%

@keyframes one
  0%
    transform: translateX(0)
  100%
    transform: translateX(-100%)

@media only screen and (max-width: 992px)

  .good-top
    @include font(40, 500)
    margin-bottom: 60px

  .good-info
    display: block

    &-text-wrap
      width: auto
      padding-left: 0
      display: block

    &-text
      display: block
      text-align: center
      margin-bottom: 60px

    &-text:last-child
      margin-bottom: 0px

    &-text-num
      width: auto
      font-size: 75px
      line-height: 1

    &-text-word-desc
      max-width: 500px
      margin: 0 auto

    &-text-word-desc-list
      display: inline-block
      text-align: left

@media only screen and (max-width: 520px)
  .good
    padding-top: 45px
    padding-bottom: 45px

    &-top
      margin-bottom: 30px

    &-info-text
      margin-bottom: 30px

    &-info-text-num
      font-size: 60px

    &-info-text-word-desc .slick-slider
      margin-top: 55px

    &-slider-page-main .slick-arrow
      top: -37px

    &-slider-page-main .slick-arrow.slick-prev
      right: calc(50% + 10px)

    &-slider-page-main .slick-arrow.slick-next
      right: auto
      left: calc(50% + 10px)

  .good-slider-page-main .good-info-text-word-desc-slab.e3,
  .good-slider-page-main .good-info-text-word-desc-slab.e4
    display: none
