.section-docs {
    margin-top: 70px;
    padding: 0 0 120px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 25px;
    }

    &__block {
        grid-column: auto/span 1;

        &-img {
            overflow: hidden;
            height: 364px;
            margin-bottom: 24px;
            position: relative;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }

            .section-docs-block-img__btn {
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;

                &-wrap {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 44px;
                    height: 44px;
                    background-color: rgba(0, 0, 0, .6);
                    backdrop-filter: blur(12px);
                    -webkit-backdrop-filter: blur(12px);
                    border-radius: 50%;
                    transform: scale(1);
                    transition: .2s;

                    svg {
                        width: 20px;
                        height: 20px;
                        color: var(--white);
                    }
                }

                &:hover {
                    .section-docs-block-img__btn-wrap {
                        transform: scale(1.1);
                    }
                }
            }
        }

        &-title {
            @include font(36, 500);
            margin-bottom: 16px;
        }

        &-subtitle {
            @include font(18, 400);
        }
    }

    @include to(1200px) {
        margin-top: 40px;
        padding: 0 0 100px;

        &__wrap {
            padding: 0 20px;
        }

        &__blocks {
            gap: 20px;
        }
    }

    @include to(992px) {
        &__block {
            &-img {
                margin-bottom: 16px;
            }

            &-title {
                @include font(32, 500);
                margin-bottom: 8px;
            }

            &-subtitle {
                @include font(16, 400);
            }
        }
    }

    @include to(768px) {
        margin-top: 35px;
        padding: 0 0 90px;

        &__wrap {
            padding: 0 10px;
        }

        &__blocks {
            gap: 10px;
        }

        &__block {
            &-img {
                margin-bottom: 12px;
            }

            &-title {
                @include font(25, 500);
                margin-bottom: 6px;
            }
        }
    }

    @include to(520px) {
        padding: 0 0 90px;

        &__blocks {
            grid-template-columns: repeat(1, 1fr);
            gap: 35px;
        }

    }
}