.discuss-free-wrap
  padding-top: 90px
  position: relative
  padding-bottom: 60px
  background: #fff

.discuss-free
  &-info
    border: 16px solid transparent
    background: #EBEBEB
    position: relative
    overflow: hidden
  &-info-left
    width: calc(100% - 500px)
    padding: 0 40px 50px 30px
  &-info-right
    position: absolute
    top: 0
    right: 0
    bottom: 0
    overflow: hidden
    border-radius: 10px
  &-info-right img
    max-width: 500px
  &-title
    @include font(52)
    padding: 35px 0 35px
  &-desc
    @include font(18)
    font-size: 18px
    margin: 26px 0 34px
  &-desc li
    background: url(../img/discuss-tick.webp) no-repeat left top
    background-size: auto 21px
    padding-left: 35px
    margin-bottom: 15px
  &-des li:last-child
    margin-bottom: 0
  & .request-call
    border-radius: 100px
    overflow: hidden
    border: 1px solid #000
    display: inline-flex
    background: #fff
  & .request-call-tel input
    height: 48px
    width: 330px
    padding: 0 10px 0 25px
    font-size: 14px
  & .request-call-but
    flex-shrink: 0
    width: 200px
    line-height: 48px
    font-weight: 500
    font-size: 12px
    letter-spacing: 0.1em
    border-radius: 100px 0 0 100px
    text-align: center
    color: #fff
    text-transform: uppercase
    background: #000
    cursor: pointer
    transition: 300ms
  & .request-call-but:hover
    background: #333

@media only screen and (max-width: 1349px)
  .discuss-free-info-left
    margin: 0 auto 60px
    width: auto
    max-width: 532px
    padding: 0 0 50px

  .discuss-free-title
    font-size: 42px
    & br
      display: none

  .discuss-free-info-right
    text-align: center
    position: static

@media only screen and (max-width: 799px)

  .discuss-free-info-left
    padding: 0 10px
    margin-bottom: 60px

  .discuss-free-info
    margin: 0 -10px

@media only screen and (max-width: 599px)

  .discuss-free-wrap
    padding-top: 60px

  .discuss-free-title
    font-size: 32px

  .discuss-free-desc
    margin-top: 0px

    & li
      padding-left: 35px
      margin-bottom: 15px

  .discuss-free-info-left
    padding: 0
    margin-bottom: 45px

  .discuss-free .request-call
    display: flex

    &-tel
      flex-grow: 1

    &-tel input
      height: 40px
      width: 100%

    &-but
      width: 152px
      height: 40px
      line-height: 40px
      font-size: 11px

  .discuss-free-info-right img
    width: 100%
    max-width: 600px
