.section-brends {
    background-color: rgb(255, 255, 255);
    padding-bottom: 80px;
    padding: 40px;
}

.section-brends h3 {
    font-size: 62px;
    font-weight: 500;
    padding-bottom: 60px;
}

.wrap-brends {
    display: grid;
    grid-template-columns: repeat(6, minmax(100px, 300px));
}

.wrap-brends img {
    width: 100%;
}

@include to(1023px) {
    .section-brends {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .section-brends {
        padding-left: 10px;
        padding-right: 10px;
    }

    .section-brends h3,
    .section-recommend h3 {
        font-size: 40px;
    }

    .wrap-brends {
        grid-template-columns: repeat(3, 1fr);
        gap: 5px;
    }
}