.document-wrap
  padding-top: 90px
  position: relative
  background: #fff

.document-title
  @include font(62, 500, 1)
  margin-bottom: 92px

.document-info
  display: flex
  justify-content: space-between
  &-e
    width: 310px
    height: 380px
    padding: 20px
  &-e.e1
    background: url(../img/document-1.webp) no-repeat
  &-e.e2
    background: url(../img/document-2.webp) no-repeat
  &-e.e3
    background: url(../img/document-3.webp) no-repeat
  &-e.e4
    background: url(../img/document-4.webp) no-repeat
  &-e-title
    @include font(22)
    margin-bottom: 10px
  &-e-desc
    font-size: 15px
    font-weight: 400
    color: #6e6e6e

@media only screen and (max-width: 1366px)
  .document-info
    flex-wrap: nowrap
    justify-content: flex-start
    overflow: auto
    &-e
      flex-shrink: 0
      margin-right: 30px
    &-e:last-child
      margin-right: 0

@media only screen and (max-width: 992px)
  .document-title
    font-size: 52px

    .document-info-e
      margin-right: 20px

@media only screen and (max-width: 799px)
  .document-title
    font-size: 42px
    & br
      display: none

@media only screen and (max-width: 520px)
  .document-wrap
    padding-top: 60px

    .document-title
      font-size: 32px
      margin-bottom: 45px

    .document-info-e
      margin-right: 10px
