
.popup-object {
    position: fixed;
    z-index: 1200;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    padding: 50px 20px;
    overflow: auto;
}

.popup-object-close {
    position: fixed;
    z-index: 2;
    top: 0px;
    right: 20px;
    font-size: 30px;
    font-weight: 400;
    height: 30px;
    line-height: 25px;
    width: 30px;
    background: #000;
    text-align: center;
    cursor: pointer;
    color: #fff;
    border-radius: 100%;
}

.popup-object-cont {
    max-width: 1200px;
    margin: 0 auto;
}

.popup-object-cont-title {
    font-size: 48px;
    margin-bottom: 30px;
}

.popup-object-cont-desc{
    margin-bottom: 30px;
    line-height: 1.5;
    font-size: 18px;
    font-weight: 400;
}

.popup-object-cont-info {
    margin-bottom: 40px;
}

.popup-object-cont-info-e {
    display: flex;
    margin-bottom: 20px;
}

.popup-object-cont-info-e:last-child {
    margin-bottom: 0;
}

.popup-object-cont-info-e-pic-wrap {
    width: 60px;
    display: flex;
    align-items: center;
}

.popup-object-cont-info-e-pic-wrap img {
    width: 30px;
    height: 30px;
}

.popup-object-cont-info-e-text-title {
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 15px;
}

.popup-object-cont-info-e-text-data {
    font-size: 15px;
}

.popup-object-call-title {
    font-size: 48px;
    margin: 30px 0;
    text-align: center;
}

.popup-object-call .but4 {
    margin: 0 auto;
}

.popup-object-work-info-e {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
}

.popup-object-work-info-e:last-child {
    margin-bottom: 0;
}

.popup-object-work-info-e-name {
    font-size: 22px;
    padding-right: 30px;
    width: 170px;
    flex-shrink: 0;
}

@media only screen and (max-width: 1023px) {
    .popup-object-close {
        right: 0;
    }
    .popup-object-cont-desc br {
        display: none;
    }
    .popup-object-work-info-e {
        display: block;
        margin-bottom: 45px;
    }
    .popup-object-work-info-e-name {
        font-size: 22px;
        padding-right: 0;
        width: auto;
        margin-bottom: 15px;
    }
    .popup-object-work-info-e-name br {
        display: none;
    }
    .popup-object-cont-title {
        font-size: 42px;
    }
    .popup-object-call-title {
        font-size: 42px;
    }
}

@media only screen and (max-width: 799px) {
    .popup-object-cont-title {
        font-size: 36px;
    }
    .popup-object {
        padding: 40px 10px 30px;
    }
    .popup-object-call-title {
        font-size: 36px;
    }
}

@media only screen and (max-width: 599px) {
    .popup-object-cont-title {
        font-size: 28px;
    }
    .popup-object-cont-desc {
        font-size: 15px;
    }
    .popup-object-cont-title {
        margin-bottom: 15px;
    }
    .popup-object-call-title {
        font-size: 26px;
    }
}

