  }

  .map-object-desc {
      font-size: 18px;
      margin-bottom: 30px;
  }

  .map-object-desc br {
      display: none;
  }
}



.show-hint-2-wrap {
  position: relative;
}

.show-hint-2 {
  position: absolute;
  z-index: 2;
  top: 10%;
  left: 40%;
}

.show-hint-2.active {
  z-index: 10;
}

.show-hint-2-mark {
  width: 33px;
  height: 33px;
  cursor: pointer;
  position: relative;
  z-index: 5;
}

.show-hint-2-mark.active .show-hint-2-mark-sign {
  background: #f00;
  color: #fff;
}

.show-hint-2-mark-sign {
  width: 33px;
  height: 33px;
  border-radius: 100%;
  background: #fff;
  position: absolute;
  z-index: 3;
  top: 0;
  left:0;
  line-height: 33px;
  text-align: center;
  font-size: 16px;
}

.show-hint-2-mark-border {
  border-radius: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background: rgba(122,122,122,0.1);
  animation: show-hint-mark-border-amin 2.8s -.6s infinite ease-out;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
}

@keyframes  show-hint-2-mark-border-amin  {
  50% {top: -14px; left: -14px; right: -14px; bottom: -14px;}
}

.show-hint-2-popup {
  display: none;
  width: 265px;
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  top: 38px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 15px 0 rgb(0,0,0,0.2);
}

