.example
  background: white
  padding-top: 50px
  padding-bottom: 150px
  margin-bottom: 50px
  +to(520px)
    padding-bottom: 0
  +to(520px)
    padding-bottom: 0
  &__wrap
    padding: 0 50px
    +to(520px)
      padding: 50px 25px
    +to(520px)
      padding: 50px 25px
  &__head
    margin-bottom: 90px
    text-align: center
    max-width: 60%
    margin-right: auto
    margin-left: auto
    +to(992px)
      max-width: 100%
    margin-bottom: 90px
    text-align: center
    max-width: 60%
    margin-right: auto
    margin-left: auto
    +to(992px)
      max-width: 100%
  &__title
    @include font(52)
    margin-bottom: 10px
    +to(768px)
      font-size: 42px
    +to(520px)
      font-size: 32px
  &__text
    @include font(20)
    +to(520px)
      font-size: 16px
      line-height: 1.4
  &__blocks
    display: flex
    flex-direction: row
    gap: 50px
    +to(768px)
      gap: 25px
    +to(520px)
      flex-direction: column
    +to(768px)
      gap: 25px
    +to(520px)
      flex-direction: column
  &__info
    margin-top: 20px
    @include font(20, 500, 1.4)
    @include font(20, 500, 1.4)
    width: 80%
    +to(520px)
      width: 100%
      font-size: 16
      text-align: center
  &__video
    flex-grow: 1
    width: calc(50% - 25px)
    display: inline-block
    position: relative
    +to(520px)
      width: 100%
      // display: flex
      // flex-direction: column
  &__swiper
    height: 100%
  &__slider
    flex-grow: 1
    width: calc(50% - 25px)
    display: inline-block
    position: relative
    +to(520px)
      width: 100%
    +to(520px)
      width: 100%
    & .swiper-pagination-bullet
      width: 60px
      height: 2px
      border-radius: 1px
      transition: .3s
    & .swiper-pagination-bullet-active
      background: white
  &__picture
    height: 100%
    display: flex
    img
      height: 100%
      width: 100%
      object-fit: cover
