.section-discount {
    padding: 100px 0 120px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(62, 500, 1);
        margin-bottom: 70px;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 40px;
    }

    &__block {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        grid-column: auto/span 6;
        padding: 50px 65px 48px 65px;
        background-color: var(--gray-100);
    }

    &__block-title {
        flex-shrink: 0;
        @include font(25, 500);
        margin-bottom: 30px;
    }

    &__block-subtitle {
        flex-grow: 1;
        margin: 0;
        @include font(16, 500);
    }

    &__block-img {
        flex-shrink: 0;
        width: 100%;
        height: 40svh;
        padding-top: 40px;
        margin-bottom: 40px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }

    &__block-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 110px;
        height: 35px;
        @include font(14, 500, 1);
        text-transform: uppercase;
        background-color: var(--gray-200);
        border-radius: 12px;
        padding: 2px;
        cursor: pointer;
        transition: .18s;

        &:hover {
            background-color: var(--black);
            color: var(--white);
        }
    }

    @include to(1200px) {
        padding: 80px 0 100px;

        &__wrap {
            padding: 0 20px;
        }

        &__blocks {
            gap: 20px;
        }

        &__block {
            padding: 35px;
        }

        &__block-title {
            @include font(20, 500);
            margin-bottom: 18px;
        }

        &__block-subtitle {
            @include font(15, 500);
        }

        &__block-img {
            padding-top: 24px;
            margin-bottom: 30px;
        }
    }

    @include to(920px) {
        &__block-img {
            height: 32svh;
        }
    }

    @include to(768px) {
        padding: 70px 0 90px;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 50px;
        }

        &__blocks {
            gap: 10px;
        }

        &__block {
            padding: 26px 20px 26px;
        }

        &__block-img {
            height: 26svh;
            padding-top: 20px;
            margin-bottom: 25px;
        }
    }

    @include to(520px) {
        padding: 60px 0 70px;

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 40px;
        }

        &__blocks {
            gap: 10px;
        }
        
        &__block-title {
            @include font(18, 500, 1);
            margin-bottom: 14px;
        }
        
        &__block-subtitle {
            @include font(14, 400);
        }

        &__block-img {
            height: 18svh;
        }
    }
    
    @include to(490px){
        padding: 50px 0 50px;
    
        &__blocks {
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 20px;
        }
        
        &__block { 
            grid-column: auto/span 1;
        }
    }
}