.section-faq-page {
    margin-top: 182px;
    padding: 0 0 190px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 20px;
    }

    &__title {
        @include font(92, 500, 1);
        text-transform: uppercase;
        margin: 0 0 90px;
    }

    &__blocks {
        display: flex;
        justify-content: space-between;
    }

    &__block {
        flex: 1;
        position: relative;

        &:nth-child(1) {
            max-width: 500px;
            width: 100%;
        }

        &:nth-child(2) {
            width: 100%;
        }
    }

    &-nav {
        position: sticky;
        top: 0;
        left: 0;
        z-index: 1;
        padding-top: 25px;
        @include font(15, 500, 1);

        &__list {
            li {
                a {
                    display: block;
                    padding: 7px 2px 7px 16px;
                    color: var(--gray-400);
                    border-left: 1px solid var(--gray-300);
                    transition: .2s;

                    &.active {
                        color: var(--black);
                        border-left: 1px solid var(--black);
                    }

                    &:hover {
                        color: var(--black);
                    }
                }
            }
        }
    }

    &-accordion {
        &__items {}

        &__item {
            border-bottom: 1px solid var(--gray-300);

            &-title {
                display: flex;
                justify-content: space-between;
                @include font(20, 500, 1);
                padding: 25px 0;
                cursor: pointer;
                transition: .2s;

                &:hover {
                    color: var(--gray-700);

                    .section-faq-page-accordion__item-icon {
                        span {
                            background-color: var(--gray-700);
                        }
                    }
                }

                &.active {
                    .section-faq-page-accordion__item-icon {
                        span:nth-child(2) {
                            transform: translate(-50%, -50%) rotate(0);
                        }
                    }
                }
            }

            &-icon {
                width: 20px;
                height: 20px;
                position: relative;

                span {
                    display: block;
                    width: 100%;
                    height: 2px;
                    background-color: var(--black);
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) rotate(0deg);
                    z-index: 1;
                    transition: .2s;
                }

                span:nth-child(2) {
                    transform: translate(-50%, -50%) rotate(90deg);
                }
            }

            &-text {
                @include font(16, 400);
                overflow: hidden;
                height: 0;
                position: relative;

                &.active {
                    height: 100%;
                }

                p {
                    margin-top: 15px;
                    margin-bottom: 15px;

                    &:first-child {
                        margin-top: 0;
                    }

                    &:last-child {
                        margin-bottom: 25px;
                    }
                }

                a {
                    border-bottom: 1px solid var(--black);
                }
            }
        }
    }

    @include to(1200px) {
        margin-top: 152px;
        padding: 0 0 160px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(62, 500, 1);
            margin-bottom: 65px;
        }

        &__block {
            &:nth-child(1) {
                max-width: 340px;
            }
        }
    }

    @include to(950px) {
        &__block {
            &:nth-child(1) {
                max-width: 260px;
            }
        }
    }

    @include to(768px) {
        margin-top: 112px;
        padding: 0 0 130px;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 20px;
        }

        &__blocks {
            flex-direction: column;
        }

        &__block {
            &:nth-child(1) {
                max-width: 100%;
            }
        }

        &-nav {
            margin-bottom: 18px;

            &__list {
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                gap: 5px 8px;

                li {
                    a {
                        color: var(--gray-400);
                        border-left: initial;
                        padding: 4px 0;

                        &.active {
                            color: var(--gray-400);
                            border-left: initial;
                        }
                    }
                }
            }
        }
    }

    @include to(520px) {
        margin-top: 98px;
        padding: 0 0 130px;

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 12px;
        }

        &-nav {
            margin-bottom: 70px;
        }
    }
}