.section-order-project {
    padding-bottom: 120px;

    &__wrap {
        display: flex;
        justify-content: center;
        max-width: 100%;
        width: 100%;
        height: 100%;
        position: relative;
        padding: 90px 0 240px;
        overflow: hidden;
    }

    &__items {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 40px;
    }

    &__item {
        display: block;
        width: 594px;
        height: 712px;
        flex-shrink: 0;

        img {
            border-radius: 3px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center center;
        }

        &:nth-child(2n) {
            width: 452px;
            height: 604px;
        }

        &:nth-child(1) {
            transform: translateY(1.5%);
        }

        &:nth-child(2) {
            transform: translateY(14%);
        }

        &:nth-child(3) {
            transform: translateY(-4%);
        }

        &:nth-child(4) {
            transform: translateY(15%);
        }

        &:nth-child(5) {
            transform: translateY(1.2%);
        }

        &:nth-child(6) {
            transform: translateY(-1.5%);
        }

        &:nth-child(7) {
            transform: translateY(-0.7%);
        }

        &:nth-child(8) {
            transform: translateY(4%);
        }

        &:nth-child(9) {
            transform: translateY(-4%);
        }
    }

    &__blocks {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__info {
        max-width: 740px;
        width: 100px;
    }

    &__hor-line {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        width: 100%;
        height: 1px;
        background-color: var(--gray-300);

        &--bottom {
            top: initial;
            bottom: 0;
        }
    }

    @keyframes lineHorWidthProject {
        0% {
            width: 0;
        }

        100% {
            width: 100%;
        }
    }

    &__block {
        padding: 33px 0;
        position: relative;

        a {
            border-bottom: 1px solid var(--black);
            transition: .2s;

            &:hover {
                color: var(--gray-700);
                border-color: var(--gray-700);
            }
        }

        &-wrap {
            display: flex;
            justify-content: space-between;
            gap: 28px;

            &.active {
                .section-order-project__hor-line {
                    width: 100%;
                    animation-name: lineHorWidth;
                    animation-duration: .86s;
                    animation-fill-mode: forwards;
                    animation-timing-function: linear;
                }

                .section-order-project__hor-line--bottom {
                    width: 100%;
                    animation-name: lineHorWidth;
                    animation-duration: .86s;
                    animation-fill-mode: forwards;
                    animation-timing-function: linear;
                }
            }

        }

        &-title {
            @include font(52, 500, 1);
            margin-bottom: 30px;
            transition: .3s;
        }

        &-subtitle {
            @include font(16, 500);
        }

        &-img {
            width: 316px;
            height: 362px;
            flex-shrink: 0;
            transition: .3s;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
            }

            &--mobile {
                display: none;
            }
        }
    }

    &__list {
        display: block;
        margin-left: 19px;
        margin-top: 22px;

        li {
            list-style-type: decimal;
            list-style-position: outside;
            padding: 2px 2px 2px 22px;
            @include font(16, 400);
        }
    }

    @include to(1600px) {
        &__items {
            gap: 25px;
        }

        &__item {
            width: 425px;
            height: 592px;

            &:nth-child(2n) {
                width: 349px;
                height: 470px;
            }

            &:nth-child(4) {
                transform: translateY(19%);
            }
        }
    }

    @include to(1366px) {

        &__wrap {
            padding: 60px 0 180px;
        }

        &__items {
            gap: 20px;
        }

        &__item {
            width: 400px;
            height: 540px;

            &:nth-child(2n) {
                width: 300px;
                height: 430px;
            }
        }
    }


    @include to(1160px) {

        &__wrap {
            padding: 50px 0 140px;
        }

        &__item {
            width: 300px;
            height: 440px;

            &:nth-child(2n) {
                width: 250px;
                height: 330px;
            }

            &:nth-child(4) {
                transform: translateY(26%);
            }
        }
    }

    @include to(1200px) {
        padding-bottom: 100px;

        &__blocks {
            padding: 0 20px;
        }

        &__block {
            &-title {
                @include font(42, 500, 1);
                margin-bottom: 25px;
            }
        }
    }

    @include to(920px) {
        &__items {
            gap: 15px;
        }

        &__item {
            width: 245px;
            height: 346px;

            &:nth-child(2n) {
                width: 176px;
                height: 254px;
            }
        }
    }

    @include to(768px) {
        padding-bottom: 80px;

        &__blocks {
            padding: 0 10px;
        }

        &__block {
            &-wrap {
                gap: 16px;
            }

            &-title {
                @include font(32, 500, 1);
                margin-bottom: 20px;
            }

            &-subtitle {
                @include font(15, 500);
            }

            &-img {
                width: 252px;
                height: 290px;
            }
        }

        &__list {
            margin-top: 19px;

            li {
                padding: 3px 2px 3px 13px;
                @include font(15, 400);
            }
        }
    }

    @include to(690px) {

        &__wrap {
            padding: 30px 0 90px;
        }

        &__items {
            gap: 10px;
        }

        &__item {
            width: 184px;
            height: 243px;

            &:nth-child(2n) {
                width: 146px;
                height: 196px;
            }
        }
    }

    @include to(635px) {
        padding-bottom: 60px;

        &__block {
            padding: 33px 0 40px;

            &-wrap {
                flex-direction: column;
                justify-content: center;
                gap: 32px;
            }

            &-img {
                width: 100%;
                flex-shrink: initial;

                &--display-none-mobile {
                    display: none;
                }

                &--mobile {
                    display: block;
                }
            }
        }
    }

    @include to(580px) {
        &__wrap {
            padding: 0 0 70px;
        }

        &__item {

            &,
            &:nth-child(2n) {
                width: 170px;
                height: 244px;
            }

            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(6),
            &:nth-child(7),
            &:nth-child(8),
            &:nth-child(9) {
                transform: initial;
            }
        }
    }
}

.section-intro--order-project {
    .section-intro__title-wrap--grid-1 {
        span:nth-child(2) {
            padding-right: 26px;
        }
    }

    .section-intro__title-wrap--grid-2 {
        span:nth-child(2) {
            display: flex;
            padding-right: 0;
        }
    }

    @include to(1060px) {
        .section-intro__title-wrap--grid-1 {
            span:nth-child(2) {
                padding-right: 19px;
            }
        }
    }

    @include to(768px) {
        .section-intro__title-wrap--grid-1 {
            span:nth-child(1) {
                padding-right: 16px;
            }

            span:nth-child(2) {
                padding-right: 16px;
            }
        }
    }

    @include to(670px) {
        .section-intro__title {
            @include font(42, 500, 1);
        }

        .section-intro__title-wrap--grid-1 {
            max-width: 260px;
            width: 100%;

            span:nth-child(1) {
                padding-right: 15px;
            }

            span:nth-child(2) {
                padding-right: initial;
            }
        }

        .section-intro__title-wrap--grid-2 {
            span:nth-child(1) {
                padding-right: 15px;
            }
        }
    }

    @include to(433px) {
        .section-intro__title-wrap--grid-1 {
            span:nth-child(1) {
                padding-right: 12px;
            }

            span:nth-child(2) {
                display: flex;
            }
        }

        .section-intro__title-wrap--grid-2 {
            span:nth-child(1) {
                padding-right: 10px;
            }
        }
    }
}