.section-special {
    padding: 90px 0 90px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        max-width: 660px;
        width: 100%;
        @include font(62, 500);
        margin-bottom: 92px;
    }

    &__items {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        width: 100%;
    }

    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 120px;
    }

    &__item-wrap {
        width: 50%;
    }

    &__img {
        display: flex;
        width: 100%;
        height: auto;
        object-fit: contain;
    }

    &__item-text {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-basis: 50%;
        @include font(20, 500);
        padding: 40px;

        span {
            max-width: 474px;
            width: 100%;
        }
    }

    &__item:nth-child(2n + 2) {
        .section-special__item-wrap {
            order: 2;
        }

        .section-special__item-text {
            order: 1;
            text-align: right;
        }
    }

    &__item:nth-child(2n + 1) {
        .section-special__item-text {
            text-align: left;
        }
    }

    @include to(1200px) {
        padding: 60px 0 60px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(62, 500);
            margin-bottom: 62px;
        }

        &__item {
            margin-bottom: 60px;
        }

        &__item-text {
            @include font(18, 500);

            span {
                max-width: 378px;
            }
        }
    }

    @include to(768px) {
        padding: 40px 0 40px;

        &__wrap {
            padding: 0 10px;

            &--blocks {
                padding: initial;
            }
        }

        &__title {
            @include font(42, 500);
            margin-bottom: 42px;
        }

        &__item {
            margin-bottom: 40px;
        }

        &__item-text {
            @include font(16, 500);
            padding: 20px;
        }
    }

    @include to(520px) {
        padding: 30px 0 30px;

        &__title {
            @include font(32, 500);
        }

        &__item {
            flex-direction: column;
            margin-bottom: 25px;
        }

        &__item:nth-child(2n + 2) {
            .section-special__item-wrap {
                order: 1;
            }

            .section-special__item-text {
                order: 2;
                text-align: left;
            }
        }

        &__item-wrap {
            width: 100%;
        }

        &__item-text {
            @include font(16, 400);
            padding: 20px 10px;

            span {
                max-width: 100%;
            }
        }
    }
}