.whatsapp {
    display: block;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    font-size: 12px;
    position: relative;
}

.whatsapp:after {
    content:'';
    position: absolute;
    left:0;
    right:0;
    bottom: -5px;
    border-top: 1px solid #aaa;
}

.whatsapp:hover:after {
    border-top-color: #000;
}

.but1 {
    width: 180px;
    height: 43px;
    background: #fff;
    position: relative;
    cursor: pointer;
}

.but1-text {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom:0;
    left:0;
    right:0;
    color: #000;
    text-align: center;
    font-size: 12px;
    line-height: 43px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    transition: 300ms;
}

.but1:before {
    content:'';
    position: absolute;
    z-index: 1;
    left:0;
    top: 0;
    bottom:0;
    width: 0%;
    transition: 300ms;
    background: #ddd;
}

.but1:hover:before {
    width: 100%;
}

.but2 {
    width: 166px;
