.popup-request-call {
    position: fixed;
    top: 0;
    left: 0;
    z-index:1201;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    display: none;
}

.popup-request-call .close{
    position: absolute;
    z-index: 2;
    width: 30px;
    height: 30px;
    top: 30px;
    right:30px;
    cursor: pointer;
}

.popup-request-call-in {
    position: absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.popup-request-call-in form {
    background: #fff;
    padding: 40px;
    margin-top: 150px;
    width: 100%;
    max-width: 360px;
    box-sizing: border-box;
}

.popup-request-call .name-field {
    margin: 0 auto;
    text-align: left;
    color: #303030;
    font-size: 12px;
}
.popup-request-call form input {
    display: block;
    margin: 5px auto 30px;
    width: 100%;
    height: 48px;
    border-radius: 3px;
    background: #f0f0f0;
    box-sizing: border-box;
    padding: 0 10px;
    font-size: 14px;
}
.popup-request-call .but2 {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media screen and (max-width: 640px){
    .popup-request-call .close{
        top: 10px;
        right:10px;
    }

    .popup-request-call-in form {
        padding: 30px 20px;
        margin-top: 100px;
    }
}

.message_sent_wrap {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
}

.message_sent_text {
    position: absolute;
    left: calc(50% - 150px);
    top: calc(50% - 50px);
    width: 300px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 16px;
    background: #fff;
}

.message_sent_text:after {
    content:'X';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    background: #ddd;
}



