    animation-name: rect-top;
    animation-duration: 7s;
}

@keyframes rect-left {
    0% {right: 90%;transform: skewx(15deg);}
    50% {right: 100%;transform: skewx(15deg);}
    100% {right: 90%;transform: skewx(15deg);}
}

@keyframes rect-right {
    0% {left: 90%;transform: skewx(15deg);}
    50% {left: 100%;transform: skewx(15deg);}
