.section-rate {
    width: 100%;
    margin: 0 auto;
    padding: 90px 0 90px;
    background-color: var(--gray-section);

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        max-width: 1200px;
        width: 100%;
        @include font(92, 500);
        text-transform: uppercase;
        margin-bottom: 60px;
        transition: 0.3s;
    }

    &__items {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 25px;
    }

    &__item {
        overflow: hidden;
        background-color: var(--white);
        border-radius: 20px;
        padding: 24px 24px 126px;
        position: relative;
        grid-column: auto/span 1;

        &:first-child {
            grid-column: auto/span 4;
            padding: 30px 30px;
        }
    }

    &-item-full {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(17, 1fr);
        grid-gap: 20px;

        &__img {
            width: 100%;
            height: 270px;
            margin-right: 20px;
        }

        &__block {
            &:first-child {
                grid-column: auto/span 3;
            }

            &:nth-child(2) {
                grid-column: auto/span 5;
            }

            &:nth-child(3) {
                grid-column: auto/span 8;
                padding: 0 25px;
                position: relative;

                &::after,
                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: 1;
                    width: 1px;
                    height: 210px;
                    background-color: var(--gray-600);
                }

                &::after {
                    left: 0;
                }

                &::before {
                    right: 0;
                }
            }

            &:last-child {
                grid-column: auto/span 1;
            }

            &:nth-child(3),
            &:nth-child(4) {
                padding-top: 17px;
            }
        }

        &__title {
            @include font(40, 500);
            margin-bottom: 20px;
        }

        &__subtitle {
            @include font(17, 500);
            padding-bottom: 30px;
            border-bottom: 1px solid var(--gray-600);
        }

        &__description {
            padding-top: 20px;
        }

        &__list-wrap {
            display: flex;
        }

        &__list {
            width: 50%;

            li {
                @include font(17, 400);
                padding: 5px 0 5px 24px;
                position: relative;

                &:before {
                    content: '';
                    display: flex;
                    width: 22px;
                    height: 16px;
                    background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat;
                    background-size: 30px;
                    position: absolute;
                    top: 8px;
                    left: -6px;
                }
            }

            &:first-child {
                margin-right: 30px;
            }
        }

        &__list-title {
            @include font(20, 500);
            margin-bottom: 15px;
        }

        &__block-title {
            @include font(20, 500);
            margin-bottom: 5px;
        }

        &__price {
            @include font(25, 500);
            margin-bottom: 30px;
        }

        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 180px;
            height: 43px;
            overflow: hidden;
            @include font(12, 500, 1);
            letter-spacing: 0.1em;
            text-transform: uppercase;
            background-color: var(--black);
            color: var(--white);
            cursor: pointer;
            transition: 0.3s;
            position: relative;
            z-index: 1;

            span {
                z-index: 5;
            }

            &::before {
                content: '';
                position: absolute;
                z-index: 0;
                left: 0;
                top: 0;
                bottom: 0;
                width: 0%;
                background-color: var(--gray-200);
                transition: 0.3s;
            }

            &:hover {
                color: var(--black);

                &::before {
                    width: 100%;
                }
            }
        }
    }

    &__item-title {
        @include font(25, 500);
        white-space: nowrap;
        margin-bottom: 20px;
    }

    &__item-price {
        @include font(20, 400);
        margin-bottom: 20px;
    }

    &__item-img {
        max-width: 100%;
        width: 100%;
        height: auto;
        object-fit: contain;
        margin-bottom: 20px;
    }

    &__list-title {
        @include font(18, 500);
        margin-bottom: 15px;
    }

    &__item-list {
        li {
            @include font(14, 500);
            padding: 5px 0 5px 25px;
            position: relative;

            &:before {
                content: '';
                display: flex;
                width: 22px;
                height: 16px;
                background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat;
                background-size: 30px;
                position: absolute;
                top: 8px;
                left: -6px;
            }
        }
    }

    &__item-bottom {
        max-width: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        left: 50%;
        bottom: 20px;
        padding: 0 24px;
        transform: translateX(-50%);
    }

    &__item-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 43px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--black);
        color: var(--white);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        margin-bottom: 10px;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            background-color: var(--gray-200);
            transition: 0.3s;
        }

        &:hover {
            color: var(--black);

            &::before {
                width: 100%;
            }
        }
    }

    &__item-subtitle {
        @include font(15, 400);
        color: var(--gray-700);
        text-align: center;
    }

    .js-rate-list__wrap {
        overflow: hidden;
        transition: 0.5s;

        &.active {
            max-height: 400px;
        }
    }

    .js-item-list-btn {
        display: none;
        @include font(15, 400, 1.25);
        color: var(--blue);
        border-bottom: 1px solid transparent;
        padding-top: 10px;
        transition: 0.3s;

        &:hover {
            border-bottom: 1px solid var(--blue);
        }
    }

    @include to(1520px) {
        padding: 60px 0 60px;

        &__wrap {
            padding: 0 20px;
        }

        &__items {
            grid-template-columns: repeat(8, 1fr);
            grid-gap: 20px;
        }

        &__item {
            grid-column: auto/span 2;
            padding: 18px 18px 126px;

            &:first-child {
                grid-column: auto/span 8;
            }
        }

        &__item-bottom {
            padding: 0 18px;
        }
    }

    @include to(1370px) {
        &__item-title {
            @include font(24, 500);
        }
    }

    @include to(1365px) {

        &__items {
            grid-template-columns: repeat(9, 1fr);
        }

        &__item {
            grid-column: auto/span 3;
            padding: 30px 30px 126px;

            &:first-child {
                grid-column: auto/span 9;
            }
        }

        &__item-title {
            @include font(25, 500);
        }
    }

    @include to(1274px) {

        &-item-full {
            grid-template-columns: repeat(23, 1fr);

            &__block {
                &:nth-child(1) {
                    display: none;
                }

                &:nth-child(2) {
                    grid-column: auto/span 7;
                }

                &:nth-child(3) {
                    grid-column: auto/span 12;
                }

                &:last-child {
                    grid-column: auto/span 4;
                }
            }
        }
    }

    @include to(1200px) {
        &__item {
            &:first-child {
                padding: 24px 24px;
            }
        }
    }

    @include to(1155px) {
        &__item {
            &:first-child {
                padding: 40px 40px;
            }
        }

        &-item-full {
            max-width: 500px;
            width: 100%;
            margin: 0 auto;
            display: block;
            grid-template-rows: initial;
            grid-template-columns: initial;
            grid-gap: initial;

            &__block {
                &:first-child {
                    grid-column: initial;
                }

                &:nth-child(2) {
                    grid-column: initial;
                }

                &:nth-child(3) {
                    grid-column: initial;
                    padding-top: initial;
                    padding: initial;
                    padding: 50px 0;


                    &::after,
                    &::before {
                        top: 0;
                        left: 50%;
                        transform: translateY(0) translateX(-50%);
                        height: 1px;
                        width: 120px;
                    }

                    &:before {
                        top: 0;
                        right: initial;
                    }

                    &:after {
                        display: none;
                    }
                }

                &:last-child {
                    grid-column: initial;
                }
            }

            &__subtitle {
                border-bottom: 1px solid var(--gray-700);
            }

            &__description {
                text-align: center;
                @include font(17, 400);
                padding-top: initial;
                margin: 30px 0 30px;
            }
        }
    }

    @include to(1140px) {
        &__item {
            padding: 18px 18px 126px;
            grid-column: auto/span 3;
        }
    }

    @include to(1070px) {
        &__item-title {
            @include font(24, 500);
        }
    }

    @include to(1060px) {
        &__title {
            @include font(80, 500)
        }
    }

    @include to(1032px) {
        &__items {
            grid-template-columns: repeat(12, 1fr);
        }

        &__item {
            padding: 30px 30px 126px;
            grid-column: auto/span 6;

            &:first-child {
                grid-column: auto/span 12;
            }
        }

        &__item-img {
            height: 200px;
            object-fit: cover;
        }

        &__item-title {
            @include font(25, 500);
        }

        .js-rate-list__wrap {
            max-height: 0;
        }

        .js-item-list-btn {
            display: block;
        }
    }

    @include to(746px) {
        &__item {
            padding: 18px 18px 126px;
        }
    }

    @include to(768px) {
        padding: 50px 0 50px;
        
        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500);
        }

        &-item-full {
            &__title {
                @include font(30, 500);
                margin-bottom: 15px;
            }

        }
    }

    @include to(698px) {
        &__item {
            padding: 30px 30px 126px;
            grid-column: auto/span 12;
        }

        &__item-title {
            @include font(25, 500);
        }

        &__item-img {
            height: 200px;
            object-fit: cover;
        }
    }

    @include to(675px) {
        &__item-title {
            @include font(25, 500);
        }
    }


    @include to(670px) {
        &__title {
            @include font(32, 500);
            margin-bottom: 34px;
        }
    }

    @include to(580px) {

        &-item-full {
            max-width: 320px;

            &__list {
                width: initial;

                &:first-child {
                    margin-right: initial;
                    margin-bottom: 30px;
                }
            }

            &__list-wrap {
                flex-direction: column;
            }

            &__btn {
                max-width: 100%;
                width: 100%;
            }
        }
    }

    @include to(400px) {
        &__item {
            padding: 18px 18px 124px;

            &:first-child {
                padding: 26px 18px 30px;
            }
        }

        &__item-bottom {
            padding: 0 18px;
        }

        &__item-img {
            height: auto;
            object-fit: contain;
        }
    }

}