.section-advantages-without-modesty {
    padding: 40px 40px 40px 40px;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    h3 {
        font-size: 144px;
        font-weight: 500;
        margin-bottom: 120px;
        text-transform: uppercase;
        line-height: 1;
    }
    h4 {
        font-size: 45px;
        font-weight: 500;
        margin-bottom: 20px;
    }
    p {
        font-size: 18px;
        font-weight: 400;
        line-height: 1.5;
        max-width: 550px;
    }
    > div {
        > div {
            display: flex;
        flex-flow: wrap;
        justify-content: space-between;
        margin-top: 25px;
        }
    }
}
.text_wrap_top img, .text_wrap_bottom img {
    order: 1;
    flex: 1;
    width: 50%;
    max-width: 580px;
}
.text_wrap_top div, .text_wrap_bottom div {
    order: 2;
    margin-left: 40px;
    flex: 1;
    min-width: 400px;
}
.text_wrap_center img {
    order: 2;
    flex: 1;
    max-width: 580px;
    width: 50%;
}
.text_wrap_center div {
    order: 1;
    margin-right: 40px;
    flex: 1;
    min-width: 400px;
    margin-top: 27%;
}
.text_wrap_top div{
    margin-top: 100px;
}
.text_wrap_bottom div{
    margin-top: auto;
    margin-bottom: 100px;
}

@include to(1023px) {
    .section-advantages-without-modesty {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(960px) {
    .section-advantages-without-modesty {
        h3 {
            font-size: 100px;
        }
    }
    .text_wrap_top div, 
    .text_wrap_center div,
    .text_wrap_bottom div{
        order: 1;
        margin: 0 0 25px 0;
    }
    .text_wrap_top img, 
    .text_wrap_center img,
    .text_wrap_bottom img{
        order: 2;
    }
    .without-modesty h3 {
        font-size: 100px;
    }
}

@include to(799px) {
    .section-advantages-without-modesty {
        padding-left: 10px;
        padding-right: 10px;
        h3 {
            font-size: 40px;
            margin-bottom: 15px;
        }
        p {
            font-size: 16px;
        }
        h4 {
            font-size: 28px;
            margin-bottom: 5px;
        }
    }
    .text_wrap_top div, .text_wrap_bottom div{
        min-width: 100%;
    }
}