.stages {
    padding: 90px 0 120px;

    &__title {
        @include font(62, 500);
        margin-bottom: 60px;
    }

    &__blocks {
        display: none;
    }

    &__block {}

    &__block-num {
        @include font(17, 500);
        margin-bottom: 7px;
    }

    &__block-title {
        @include font(25, 500);
        margin-bottom: 15px;
    }

    &__progress {
        display: none;
        width: 100%;
        height: 16px;
        margin: 10px 0;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            height: 14px;
            border-radius: 20px;
        }

        &--one {
            &::before {
                width: 100%;
                background-color: #E6E6E6;
            }
        }

        &--two {
            &::before {
                width: 100%;
                background-color: #B3B3B3;
            }
        }

        &--three {
            &::before {
                width: 100%;
                background-color: #434343;
            }
        }
    }

    &__text {
        max-width: 380px;
        width: 100%;
        @include font(16, 400);
        color: var(--gray-700);
    }

    @include to(1200px) {
        padding: 60px 0 90px;

        &__title {
            margin-bottom: 40px;
        }
    }

    @include to (768px) {
        padding: 50px 0 80px;

        &__title {
            @include font(42, 500);
        }
    }

    @include to(585px) {
        &__title {
            margin-bottom: 50px;
        }
    
        &__blocks {
            display: flex;
            flex-direction: column;
            row-gap: 35px;
        }
    }

    @include to(520px) {
        &__title {
            @include font(32, 500);
            margin-bottom: 34px;
        }
    }
}

.stages-anim {

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 20px;
        position: relative;
    }

    &__blocks--progress {
        position: relative;

        .stages-anim__blocks-line {
            width: 120%;
            height: 16px;
            border-radius: 20px;
            background-color: var(--white);
            position: absolute;
            top: 50%;
            right: -10%;
            transform: translateY(-50%);
            z-index: 3;
        }

        &.active {
            .stages-anim__blocks-line {
                animation-name: stagesLineProgressAnim;
                animation-duration: 3.2s;
                animation-fill-mode: forwards;
                animation-timing-function: linear;
            }
        }
    }

    @keyframes stagesLineProgressAnim {
        0% {
            width: 120%;
        }

        100% {
            width: 0;
        }
    }

    &__block {
        grid-column: auto/span 4;
    }

    &__block-num {
        @include font(17, 500);
        margin-bottom: 12px;
    }

    &__block-title {
        @include font(25, 500);
        margin-bottom: 2px;
    }

    &__progress {
        width: 100%;
        height: 16px;
        margin: 20px 0;
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            height: 16px;
            border-radius: 20px;
        }

        &--one {
            &::before {
                width: 120%;
                background-color: #E6E6E6;
            }
        }

        &--two {
            &::before {
                width: 120%;
                background-color: #B3B3B3;
            }
        }

        &--three {
            &::before {
                width: 100%;
                background-color: #434343;
            }
        }
    }

    &__text {
        @include font(16, 400);
        color: var(--gray-700);
    }

    @include to(1010px) {
        &__block-title {
            max-width: 230px;
            width: 100%;
        }
    }

    @include to(768px) {
        &__block-num {
            @include font(15, 500);
            margin-bottom: 8px;
        }

        &__block-title {
            max-width: 150px;
            @include font(20, 500);
        }

        &__block-title {
            @include font(16, 400);
        }
    }

    @include to(585px) {
        display: none;
    }
}