.section-paralax-block {
    padding: 110px 0 0;

    &__wrap {
        max-width: 1160px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__items {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 50px;
        position: relative;
    }

    &__item {
        display: flex;
        flex-direction: column;


        &--info {
            max-width: 440px;
            width: 100%;
            padding-top: 85px;
            gap: 130px;
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: 45px;
        }

        &--3 {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            position: absolute;
            top: -344px;
            left: -200px;
            z-index: 2;
            height: 1200px;
        }

        &--5 {
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: absolute;
            top: 248px;
            right: -195px;
            z-index: 2;
            height: 800px;
        }

        &-img {
            position: relative;
            overflow: hidden;

            &--height-1 {
                max-width: 525px;
                width: 100%;
                height: 768px;
            }

            &--height-2 {
                max-width: 302px;
                width: 100%;
                height: 402px;
            }


            &--height-3 {
                max-width: 412px;
                width: 100%;
                height: 603px;
                margin-left: auto;
            }

            &--height-4 {
                max-width: 302px;
                width: 100%;
                height: 402px;
            }

            img {
                width: 100%;
                height: auto;
                object-fit: cover;
                object-position: center;
                will-change: transform;
            }
        }

        &-title {
            @include font(38, 400, 1.3);
        }

        &-text {
            @include font(24, 400, 1.3);
        }
    }

    &-dot {
        width: 34px;
        height: 34px;
        position: absolute;
        z-index: 1;

        &:hover {
            z-index: 10;

            .section-paralax-block-dot__num {
                background-color: #d40136;
                color: var(--white);
            }

            .section-paralax-block-dot__card {
                pointer-events: all;
                opacity: 1;
                z-index: 5;
            }
        }

        &--1 {
            top: 20%;
            left: 50%;
            transform: translateX(-50%);
        }

        &--2 {
            top: 15%;
            right: 21%;
        }

        &--3 {
            top: 20%;
            left: 20%;
        }

        &--4 {
            top: 15%;
            left: 21%;
        }


        &__wrap {
            position: relative;
            z-index: 1;

            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                z-index: -1;
                width: 34px;
                height: 34px;
                border-radius: 50%;
                animation-duration: 2.1s;
                animation-name: pulseDotAnim;
                animation-iteration-count: infinite;
            }
        }

        @keyframes pulseDotAnim {
            0% {
                scale: 1;
                background-color: rgba(#fff, 0);
            }

            90% {
                scale: 1.65;
                background-color: rgba(#fff, 1);
            }


            100% {
                background-color: rgba(#fff, 0);
                scale: 1;
                opacity: 0;
            }
        }

        &__num {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 34px;
            height: 34px;
            border-radius: 40px;
            background-color: var(--white);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            @include font(14, 500);
            cursor: default;
            transition: .2s;
        }

        &__card {
            pointer-events: none;
            width: 140px;
            height: auto;
            background-color: var(--white);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
            text-align: center;
            border-radius: 8px;
            overflow: hidden;
            position: absolute;
            top: 40px;
            left: 50%;
            transform: translateX(-50%);
            z-index: 1;
            opacity: 0;
            transition: .2s;

            &-img {
                width: 100%;
                height: 176px;
                border-radius: 8px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }

            &-wrap {
                padding: 6px 4px 7px;
            }

            &-title {
                @include font(16, 500);
                margin-bottom: 6px;
            }

            &-price {
                @include font(15, 400);
            }
        }
    }

    &-slider {
        display: none;

        &__wrap {
            margin-bottom: 40px;
        }

        &__title {
            @include font(16, 300, 1.2);
            margin-bottom: 30px;
        }

        &__subtitle {
            @include font(24, 400, 1.3);
        }

        &__swiper {
            width: 100%;
            height: 100%;
        }

        &__wrapper {
            display: flex;
        }

        &__slide {
            flex: 0 0 400px;
            min-width: 400px;
            margin-right: 25px;


            &-img {
                display: block;
                width: 100%;
                height: 540px;
                overflow: hidden;
                position: relative;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }
        }
    }
    
    @include to(1700px) {
        padding: 70px 0 0;
    }

    @include to(1200px) {
        padding: 80px 0 30px;

        &__wrap {
            max-width: 1480px;
            width: 100%;
            padding: 0 20px;
        }

        &__items {
            display: none;
        }

        &-slider {
            display: block;
        }
    }

    @include to(992px) {
        &-slider {
            &__slide {
                flex: 0 0 300px;
                min-width: 300px;
                margin-right: 15px;

                &-img {
                    height: 440px;
                }
            }
        }
    }

    @include to(768px) {
        &-slider {
            &__slide {
                flex: 0 0 260px;
                min-width: 260px;
                margin-right: 15px;

                &-img {
                    height: 390px;
                }
            }
        }
    }
}