.section-blog-related {
    margin: 70px 0;
    &__title {
        color: var(--gray-700);
        @include font(14, 400, 1);
        text-transform: uppercase;
        margin: 0 0 32px 0;
    }
    &__more-link,
    &__more-link:visited {
        margin: 32px 0 0 0;
        padding: 8px 16px;
        display: flex;
        width: fit-content;
        justify-content: center;
        align-items: center;
        @include font(16, 500, 1);
        color: #324fbe;
        border: 1px solid #324fbe;
        border-radius: 4px;
        transition: all 0.05s;
        &:hover {
            color: #283f98;
            border: 1px solid #283f98;
            box-shadow: inset 0 0 0 1px #283f98;
        }
        &:active {
            background-color: #202641;
            border: 1px solid #202641;
            box-shadow: none;
            color: rgba(255, 255, 255, 0.89);
        }
    }
}

@include to(768px) {
    .section-blog-related {
        margin: 50px 0;
    }
}