.section-faq-wrap
  position: relative
  padding-bottom: 120px
  color: black

.section-faq-title
  padding: 84px 0 28px
  @include font(92, 500, 1)
  user-select: none
  +to(768px)
    font-size: 72px
  +to(520px)
    font-size: 40px

.section-faq-info-wrap
  display: flex
  flex-wrap: wrap

.section-faq-desc
  font-size: 22px

.section-faq-info-right
  width: 100%
  box-sizing: border-box

.section-faq-info-left
  padding-right: 40px

.section-faq-info-item:first-child
  padding-top: 25px
  border-top: 1px solid #aaa

.section-faq-info-item
  padding-bottom: 25px
  border-bottom: 1px solid #aaa
  margin-bottom: 30px

  &-title
    @include font(22)
    position: relative
    cursor: pointer
    padding-right: 40px
    +to(520px)
      font-size: 18px

  &-title:after
    content: '+'
    position: absolute
    bottom: 0px
    right: 10px
    @include font(22)

  &-title.active:after
    content: '-'

  &-desc
    display: none

  &-desc p
    margin-top: 20px
    line-height: 1.4
