.section-project-composition-interactive {
    padding: 40px 0 60px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__block {
        width: 100%;
        position: relative;
    }

    &__img {
        width: 100%;

        img {
            width: 100%;
            height: auto;
            object-fit: contain;
            object-position: center;
        }
    }

    &__dots {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    &__dots-wrap {
        height: 100%;
        position: relative;
    }

    &__dot {
        width: 34px;
        height: 34px;
        position: absolute;
        z-index: 1;

        &:hover,
        &.active {
            z-index: 10;

            .section-project-composition-interactive__dot-operator {
                background-color: #d40136;
                color: var(--white);

                &::before,
                &::after {
                    background-color: var(--white);
                }

                &::after {
                    transform: translate(-50%, -50%) rotate(0deg);
                }
            }

            .section-project-composition-interactive__card {
                pointer-events: all;
                opacity: 1;
                z-index: 5;
            }
        }

    }

    &__dot-wrap {
        pointer-events: all;
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            pointer-events: none;
            margin: auto;
            z-index: -1;
            width: 34px;
            height: 34px;
            border-radius: 50%;
            animation-duration: 2.1s;
            animation-name: pulseDotProjectComposition;
            animation-iteration-count: infinite;
        }
    }

    &__dot-operator {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 34px;
        border-radius: 40px;
        background-color: var(--white);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        @include font(14, 500);
        cursor: default;
        position: relative;
        transition: .2s;

        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
            width: 12px;
            height: 2px;
            background-color: var(--black);
            transition: .2s;
        }

        &::after {
            transform: translate(-50%, -50%) rotate(90deg);
        }
    }

    @keyframes pulseDotProjectComposition {
        0% {
            scale: 1;
            background-color: rgba(#fff, 0);
        }

        90% {
            scale: 1.65;
            background-color: rgba(#fff, 1);
        }


        100% {
            background-color: rgba(#fff, 0);
            scale: 1;
            opacity: 0;
        }
    }

    &__card {
        padding: 10px 9px 9px 9px;
        pointer-events: none;
        width: 265px;
        height: auto;
        background-color: rgba(0, 0, 0, 0.3);
        backdrop-filter: blur(22px);
        -webkit-backdrop-filter: blur(22px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        color: var(--white);
        border-radius: 8px;
        overflow: hidden;
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        opacity: 0;
        transition: .2s;
    }

    &__card-title {
        @include font(20, 500, 1);
        margin-bottom: 11px;
        padding-bottom: 10px;
        border-bottom: 1px solid var(--gray-300);
    }

    &__card-subtitle {
        @include font(15, 400);
        margin-bottom: 14px;
    }

    &__card-img {
        width: 100%;

        img {
            border-radius: 6px;
            width: 100%;
            height: auto;
            object-fit: contain;
        }
    }

    &__dot--one {
        left: 17%;
        bottom: 18%;
    }

    &__dot--two {
        right: 29%;
        bottom: 34%;
    }

    &__dot--three {
        right: 15%;
        bottom: 67%;

        .section-project-composition-interactive__card {
            bottom: inherit;
            top: 40px;
        }
    }

    @include to(1200px) {

        &__wrap {
            padding: 0 20px;
        }
    }

    @include to(768px) {
        padding: 25px 0 30px;

        &__wrap {
            padding: 0 10px;
        }

        &__card {
            left: 0;
            transform: initial;
        }

        &__dot--one {
            .section-project-composition-interactive__card {
                left: -20px;
            }
        }

        &__dot--two {
            .section-project-composition-interactive__card {
                left: initial;
                right: -35px;
            }
        }

        &__dot--three {
            .section-project-composition-interactive__card {
                bottom: 40px;
                top: initial;
                left: initial;
                right: -35px;
            }
        }
    }

    @include to(520px) {
        padding: 20px 0 35px;
    }

    @include to(410px) {
        padding: 28px 0 48px;

        &__card {
            padding: 7px 6px 6px 6px;
            width: 240px;
        }
    }
}