.section-style-arch {
    &__wrap {
        position: relative;
    }

    &__video {
        width: 100%;
        height: 100svh;

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__img-blur {
        display: none;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        padding: 40px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: 1;
    }

    &__block {
        width: 100%;
    }

    &__title {
        margin: 46px 0;
        @include font(70, 500);
        color: var(--white);
        transition: .3s;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--white);
        color: var(--black);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--black);
        }

        &:hover {
            span {
                color: var(--white);
            }

            &::before {
                width: 100%;
            }
        }
    }

    @include to(1200px) {
        &__title {
            @include font(62, 500);
        }

        &__info {
            padding: 20px;
        }
    }

    @include to(992px) {
        &__video {
            height: 50svh;
            
            video {
                object-position: center bottom;
            }
        }

        &__img-blur {
            display: block;
            height: 50svh;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
            }
        }

        &__info {
            justify-content: flex-start;
            top: initial;
            height: 50svh;
            backdrop-filter: blur(70px);
            -webkit-backdrop-filter: blur(70px);
        }

        &__title {
            @include font(42, 500);
            margin: 0 0 22px;
        }
    }

    @include to(768px) {
        &__info {
            padding: 10px;
        }
    }

    @include to(520px) {
        &__title {
            @include font(32, 500);
        }

        &__btn {
            width: 180px;
            height: 43px;
        }
    }
}