.section-about-our-offices {
    background-color: rgb(0, 0, 0);
    padding: 25px 40px;
    &__head-office {
        float: left;
        h3 {
            color: rgb(255, 255, 255);
            font-size: 65px;
            font-weight: 500;
        }
        p {
            color: rgb(255, 255, 255);
            font-size: 22px;
            font-weight: 500;
        }
    }
    &__slider-wrap {
        width: 70%;
        margin-left: auto;
        margin-right: 1px;
        padding: 0 0 0 33px;
    }
    &__slider-num {
        margin-left: auto;
        margin-right: 40px;
        color:rgb(255, 255, 255);
    }
    &__slider-arrow-prev {
        background: url(../img/about-arrow-prev-white.png) no-repeat center !important;
    }
    &__slider-arrow-next {
        background: url(../img/about-arrow-next-white.png) no-repeat center !important;
    }
}

@include to(1023px) {
    .section-about-our-offices {
        padding-left: 20px;
        padding-right: 20px;
        h3 {
            font-size: 100%;
        }
    }
}
@include to(799px) {
    .section-about-our-offices {
        padding-left: 10px;
        padding-right: 10px;
        &__head-office {
            float: none;
            padding-bottom: 15px;
            h3 {
                font-size: 40px;
            }
            p {
                font-size: 16px;
            }
        }
        &__slider-wrap {
            width: 100%;
            margin-left: 0;
            margin-right: 0px;
            padding-left: 0;
        }
        &__slider-arrow {
            margin-right: -9px;
        }
    }
}