.section-projects
  margin-bottom: 80px
  &__wrap
  &__title
    @include font(144, 500)
    // margin-bottom: 28px
    text-transform: uppercase
    +to(768px)
      font-size: 72px
    +to(520px)
      font-size: 40px
    & > .section-projects__examples
      margin-top: 28px
  &__subtitle
    @include font(20, 500)
    margin-top: 10px
    margin-bottom: 60px
  &__examples
    margin-bottom: 60px
  &__show-more
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    font-size: 18px
    width: 30%
    margin: 0 auto
    +to(768px)
      width: 80%
  &__show-text
    margin-bottom: 30px
    padding-bottom: 15px
    text-align: center
    border-bottom: 1px solid rgba(0,0,0,.2)
    width: 100%
    position: relative
    @include font(16, 400)
  &__progress
    position: absolute
    bottom: -1px
    left: 0
    height: 1px
    background: black
  &__show-btn
    display: flex
    align-items: center
    justify-content: center
    padding: 15px 40px
    border: 1px solid #aaa
    width: 100%
    position: relative
    text-transform: uppercase
    transition: .3s
    &-text
      display: block
      position: relative
      z-index: 2
      @include font(15, 500)
    &:hover
      color: white
      border: 1px solid black
      &:before
        transform: scaleX(1)
    &::before
      content: ''
      background: black
      position: absolute
      width: 100%
      height: calc(100% + 1px)
      top: 0
      left: 0
      z-index: 1
      transform: scaleX(0)
      transform-origin: left
      transition: .3s

.single-project
  margin-bottom: 60px
  &__visual
    position: relative
    margin-bottom: 30px
  &__switcher
    position: absolute
    top: 20px
    display: flex
    justify-content: center
    width: 100%
    z-index: 1
  &__name
    position: absolute
    bottom: 50px
    left: 50px
    color: white
    @include font(48, 500)
    z-index: 1
  &__info
    display: flex
    flex-direction: row
    gap: 30px
    position: relative
    +to(520px)
      flex-direction: column
  &__info-block
    flex-grow: 1
    @include font(16, 400, 1.4)
    width: 100%
    max-width: 700px
    +to(768px)
      max-width: 80%
    +to(520px)
      max-width: 100%
    ul
      list-style: disc
      margin-left: 20px
      li
        list-style: disc
    &--gray
      color: #a6a6a6
  &__info-title
    @include font(32, 500)
    margin-bottom: 12px
    +to(520px)
      font-size: 28px
  &__visit-btn
    @include font(14)
    width: fit-content
    display: inline-block
    text-transform: uppercase
    color: black
    position: absolute
    bottom: 5px
    right: 0
    +to(520px)
      position: relative
    &::before
      content: ''
      display: inline-block
      position: absolute
      bottom: -3px
      border-bottom: 3px solid black
      width: 100%
      transform: scaleX(1)
      transform-origin: right
      transition: .4s ease
    &:hover
      &::before
        transform: scaleX(0)

  &__swiper
  &__slide
    width: 100%
    height: 750px
    display: block
    background: rgba(0,0,0,.1)
    +to(520px)
      height: 500px
    img
      width: 100%
      height: 100%
      object-fit: cover

  & .swiper-pagination

    & .swiper-pagination-bullet
      width: 60px
      height: 2px
      border-radius: 1px
      transition: .3s
    & .swiper-pagination-bullet-active
      background: white

//     
.custom-button-prev, .custom-button-next
  background: rgba(#fff, .2)
  background-image: url('../img/service-arrow.svg')
  background-size: 15px
  background-position: center center
  background-repeat: no-repeat
  border-radius: 20px
  display: block
  width: 56px
  height: 36px
  position: absolute
  top: calc(50% - 18px)
  z-index: 1
  backdrop-filter: blur(4px)
  color: black
  transition: .3
  &:hover
    cursor: pointer
  &.swiper-button-disabled
    opacity: 0
    &:hover
      cursor: default

.custom-button-prev
  right: 18px
.custom-button-next
  left: 18px
  transform: scaleX(-1)

.switch
  border-radius: 40px
  background: rgba(#fff, .2)
  display: flex
  flex-direction: row
  padding: 3px 3px
  position: relative
  backdrop-filter: blur(10px)
  &__btn
    padding: 10px 20px
    position: relative
    font-weight: 500
    &:first-child
      margin-right: -5px
    &::before
      content: ''
      display: block
      background: white
      width: 100%
      height: 100%
      position: absolute
      z-index: -1
      top: 0
      left: 0
      border-radius: 20px
      transition: .3s
      opacity: 0
    &--active
      &::before
        opacity: 1
