.section-recommend {
    padding: 40px;
    background-color: rgb(255, 255, 255);
}

.section-recommend h3 {
    font-size: 62px;
    font-weight: 500;
    padding-bottom: 80px;
}

.section-recommend .wrap-recommend {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 100px;
}

.wrap-recommend .bullet {
    margin-bottom: 48px;
}

.wrap-recommend .bullet h4 {
    font-size: 45px;
    font-weight: 500;
}

.wrap-recommend .bullet span {
    font-size: 100px;
    margin-top: -18px;
}

.wrap-recommend .bullet p {
    font-size: 18px;
    font-weight: 400;
    color: #a6a6a6;
}

.wrap-recommend .img-wrap {
    max-width: 568px;
    flex: 1 1 300px;
}

.wrap-recommend .recommend-descr {
    max-width: 600px;
    flex: 1 1 300px;
}

.section-recommend__text-block {
    margin-left: 0;
    width: auto;
    color: #000;
}

@include to(1023px) {
    .section-recommend {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .section-recommend {
        padding-left: 10px;
        padding-right: 10px;
    }

    .section-recommend h3 {
        font-size: 40px;
    }

    .wrap-recommend .bullet span {
        font-size: 60px;
    }

    .wrap-recommend .bullet h4 {
        font-size: 28px;
    }
}