.section-reviews {
    padding: 120px 0 0;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__head {
        margin-bottom: 100px;
    }

    &__title {
        @include font(62, 500, 1);
        margin-bottom: 37px;
    }

    &__subtitle {
        @include font(20, 500);
    }

    &__blocks {
        display: flex;
        flex-direction: column;
    }

    &__block {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, .25);
        }

        &-img {
            img {
                width: 100vw;
                height: 100vh;
                object-fit: cover
            }
        }
    }

    &-info {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px;

        &__block {
            display: flex;
            flex-direction: column;
            align-items: center;
            max-width: 40vw;
            width: 100%;
            background-color: var(--white);
            border-radius: 8px;
            padding: 30px;
            text-align: center;

            &-num {
                @include font(16, 500, 1);
                margin-bottom: 30px;
            }

            &-title {
                @include font(42, 400, 1);
                margin-bottom: 30px;
            }

            &-subtitle {
                @include font(18, 400, 1);
                margin-bottom: 30px;
            }

            &-text {
                display: flex;
                flex-direction: column;
                gap: 30px;
                @include font(17, 400);
                margin-bottom: 30px;
            }

            &-img {
                width: 300px;
                height: 250px;
                margin-bottom: 30px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }

    &__btn {
        @include font(14, 500);
        text-transform: uppercase;
        color: var(--black);
        position: relative;

        &::before {
            content: "";
            position: absolute;
            right: 0;
            bottom: -4px;
            z-index: 1;
            width: 100%;
            height: 3px;
            background-color: var(--black);
            transition: .3s;
        }

        &:hover {
            &::before {
                width: 0;
            }
        }
    }

    @include to(1366px) {
        &-info {
            &__block {
                max-width: 550px;
            }
        }
    }

    @include to(1200px) {
        padding: 90px 0 0;

        &__wrap {
            padding: 0 20px;
        }

        &__head {
            margin-bottom: 70px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 30px;
        }

        &__subtitle {
            @include font(19, 500);
        }
    }

    @include to-h(720px) {
        &__block {
            height: 133svh;

            &-img {
                img {
                    height: 133svh;
                }
            }
        }
    }

    @include to(768px) {
        padding: 70px 0 0;

        &__wrap {
            padding: 0 10px;
        }

        &__head {
            margin-bottom: 50px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 27px;
        }

        &__subtitle {
            @include font(18, 500);
        }

        &-info {
            &__block {
                padding: 22px 20px 25px;

                &-num {
                    margin-bottom: 23px;
                }

                &-title {
                    @include font(32, 400, 1);
                    margin-bottom: 24px;
                }

                &-subtitle {
                    margin-bottom: 21px;
                }

                &-text {
                    gap: 24px;
                    @include font(16, 400);
                    margin-bottom: 24px;
                }

                &-img {
                    margin-bottom: 22px;
                }
            }
        }
    }

    @include to(520px) {
        padding: 50px 0 0;

        &__head {
            margin-bottom: 40px;
        }

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 24px;
        }

        &__subtitle {
            @include font(16, 500);
        }

        &-info {
            &__block {
                &-subtitle {
                    margin-bottom: 20px;
                }
            
                &-text {
                    gap: 22px;
                    @include font(16, 400);
                    margin-bottom: 22px;
                }
            
                &-img {
                    max-width: 100%;
                    width: 100%;
                }
            }
        }
    }
}