.promo-blocks
  background: #f0f0f0
  padding: 50px 0
  &__title
    @include font(92, 500, 0.8)
    margin-bottom: 56px
    +to(768px)
      font-size: 72px
    +to(520px)
      font-size: 40px
      line-height: 1

  &__wrap
  &__gallery
  .wrap
    max-width: 1300px

body >
  .promo-gallery,
  .example,
  .your-budget,
  .save,
  .stages
    max-width: 1140px
    width: 100%
    margin: 0 auto
    border-bottom: 1px solid var(--gray-300)
    +to(1200px)
      padding-left: 20px
      padding-right: 20px
    +to(768px)
      padding-left: 10px
      padding-right: 10px
  .promo-gallery
    margin-bottom: 0
    padding: 90px 0 120px
    .promo-gallery
      &__head
        max-width: 1060px
        width: 100%
        margin-left: auto
        margin-bottom: 60px
      &__title
        max-width: 820px
        width: 100%
        @include font(62, 500)
        margin-bottom: 20px
      &__text
        margin-bottom: 0
      .promo-slide
        &__picture
          img
            width: 980px
            height: 620px
            object-fit: cover
            object-position: center center
      &__buttons
        padding-top: 40px
    +to(1200px)
      padding: 60px 20px 60px
      .promo-slide
        &__picture
          img
            width: 100%
            height: auto
            object-fit: contain
      .promo-gallery
        &__buttons
          margin-top: 0
          padding-top: 18px
    +to(768px)
      padding: 50px 10px 50px
  .example
    padding: 90px 0 140px
    margin-bottom: 0
    .example
      &__wrap
        padding: initial
      &__head
        margin: initial
        text-align: left
        margin-bottom: 60px
      &__title
        @include font(62, 500)
        margin-bottom: 20px
      &__text
        max-width: 580px
        width: 100%
        @include font(20, 500)
        color: var(--gray-700)
    +to(1200px)
      padding: 70px 20px 130px
      .example
        &__blocks
          gap: 20px
    +to(768px)
      padding: 60px 10px 120px
      .example
        &__blocks
          gap: 10px
    +to(520px)
      .example
        padding: 50px 10px 60px
  +to(768px)
    .promo-gallery .promo-gallery,
    .example .example
      &__head
        margin-bottom: 40px
      &__title
        @include font(42, 500)
      &__text
        @include font(18, 500)
  +to(520px)
    .promo-gallery .promo-gallery,
    .example .example
      &__head
        margin-bottom: 30px
      &__title
        @include font(32, 500)
        margin-bottom: 15px
      &__text
        @include font(16, 500)