.get-wrap
  padding-top: 90px
  position: relative
  background: #fff

.get-title
  margin-bottom: 30px
  @include font(144, 500, 1)
  text-transform: uppercase

.get-desc
  @include font(22, 500, 1)
  margin-bottom: 92px

.get-info
  display: flex
  justify-content: space-between

  &-cont
    width: calc(33% - 40px)

  &-cont-nav
    position: sticky
    top: 110px
    left: 0

  &-cont-nav li
    margin-bottom: 20px

  &-cont-nav li:last-child
    margin-bottom: 0

  &-cont-nav li a
    display: block
    color: #999
    @include font(16, 500, 1)

  &-cont-nav li a:hover
    color: #000

  &-cont-nav li a.active
    color: #000

  &-cont-nav li a.active:before
    content: ''
    display: inline-block
    width: 10px
    border-top: 2px solid #000
    margin-right: 8px
    position: relative
    top: -4px

  &-details
    width: 66%

  &-details-e
    position: relative
    margin-bottom: 60px
    @include font(17,500, 1.2)

  &-details-e:last-child
    margin-bottom: 0

.get-info-details-e #target1,
.get-info-details-e #target2,
.get-info-details-e #target3,
.get-info-details-e #target4
  position: absolute
  top: -110px
  left: 0

.get-info-details-e-title
  @include font(45, 500, 1)
  margin-bottom: 20px

.get-info-details-e-pic-wrap
  margin-bottom: 20px
  position: relative

  & img
    Width: 100%

  &.e1 .show-hint.e1
    top: 41%
    left: 31.5%

  &.e1 .show-hint.e2
    top: 53%
    left: 34%

  &.e1 .show-hint.e3
    top: 60%
    left: 38%

  &.e1 .show-hint.e4
    top: 40%
    left: 62%

  &.e1 .show-hint.e5
    top: 40%
    left: 55%

  &.e1 .show-hint.e6
    top: 53%
    left: 55%

  &.e2 .show-hint.e1
    top: 72%
    left: 17%

  &.e2 .show-hint.e2
    top: 60%
    left: 72%

  &.e2 .show-hint.e3
    top: 30%
    left: 82%

  &.e3 .show-hint.e1
    top: 24%
    left: 30%

  &.e3 .show-hint.e2
    top: 64%
    left: 38%

  &.e3 .show-hint.e3
    top: 38%
    left: 55%

