.get-info-details-e-desc
  max-width: 645px
  color: #6e6e6e

  & b
    color: #000
    font-weight: 500

@media only screen and (max-width: 1280px)
  .get-title
    font-size: 92px

@media only screen and (max-width: 1023px)
  .get-title
    font-size: 52px

  .get-info-details-e-title
    font-size: 40px

@media only screen and (max-width: 799px)
  .get-title
    font-size: 42px

  .get-desc
    margin-bottom: 60px

  .get-info-details-e-title
    font-size: 32px

@media only screen and (max-width: 599px)
  .get-wrap
    padding-top: 60px

  .get-title
    font-size: 40px
    margin-bottom: 15px

  .get-desc
    margin-bottom: 45px
    font-size: 18px

    & br
      display: none

  .get-info
    display: block

    &-cont
      width: auto
      margin-bottom: 45px

    &-details
      width: auto

    &-details-e
      margin-bottom: 30px

    &-details-e-title
      font-size: 22px

    &-details-e-desc
      font-size: 14px

.show-hint-wrap
  position: relative

.show-hint
  position: absolute
  z-index: 1
  top: 10%
  left: 40%

  &.active
    z-index: 10

  &-mark
    width: 30px
    height: 30px
    cursor: pointer
    position: relative
    z-index: 5

  &-mark.active &-mark-sign
    background: #ff0000

  &-mark-sign
    width: 30px
    height: 30px
    border-radius: 100%
    background: #fff
    position: absolute
    z-index: 3
    top: 0
    left: 0
    line-height: 33px
    text-align: center
    font-size: 16px

  &-mark-sign.plus-minus:before
    content: ''
    position: absolute
    top: 14px
    left: 10px
    width: 10px
    border-top: 2px solid #000

  &-mark.active &-mark-sign.plus-minus:before
    border-color: #fff

  &-mark-sign.plus-minus:after
    content: ''
    position: absolute
    top: 10px
    left: 14px
    height: 10px
    border-right: 2px solid #000

  &-mark.active &-mark-sign.plus-minus:after
    display: none

  &-mark-border
    border-radius: 100%
    position: absolute
    top: 0px
    left: 0px
    right: 0px
    bottom: 0px
    background: rgba(122,122,122,0.1)
    animation: show-hint-mark-border-amin 2.8s -.6s infinite ease-out
    -webkit-backdrop-filter: blur(20px)
    backdrop-filter: blur(20px)

@keyframes  show-hint-mark-border-amin
  50%
    top: -8px
    left: -8px
    right: -8px
    bottom: -8px

.show-hint-popup
  display: none
  position: absolute
  z-index: 2
  top: 38px
  left: 50%
  transform: translateX(-50%)
  width: 200px
  padding: 15px
  text-align: center
  background: rgba(232, 232, 232, 0.24)
  backdrop-filter: blur(16px)
  border-radius: 5px

  &-text
    font-size: 15px
    font-weight: 400

  &-text.text-black
    color: #000

  &-text.text-white
    color: #ffffff
