.section-planning-solution-care {
    padding: 80px 0 90px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        max-width: 1270px;
        width: 100%;
        text-align: center;
        @include font(62, 500, 1);
        margin-bottom: 80px;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 50px 25px;
    }

    &__block {
        max-width: 416px;
        width: 100%;
        grid-column: auto/span 1;

        &-img {
            margin-bottom: 25px;
        }

        &-title {
            @include font(18, 500, 1);
            margin-bottom: 14px;
        }

        &-text {
            @include font(16, 400);
        }
    }

    @include to(1200px) {
        padding: 60px 0 80px;

        &__wrap {
            padding: 0 20px;
        }
        
        &__blocks {
            grid-gap: 30px 20px;
        }
        
        &__block {
            &-img {
                margin-bottom: 18px;
            }

            &-title {
                @include font(17, 500, 1);
                margin-bottom: 12px;
            }

            &-text {
                @include font(15, 400);
            }
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 60px;
        }
    }
    
    @include to(890px) {
        &__blocks {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 40px 20px;
        }
    }

    @include to(768px) {
        padding: 50px 0 70px;

        &__wrap {
            padding: 0 10px;
        }
        
        &__blocks {
            grid-gap: 37px 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 50px;
        }
    }

    @include to(520px) {
        padding: 40px 0 50px;

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 40px;
        }
    }
    
    @include to(440px) {
        &__blocks {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}