.section-project-composition-text {
    padding: 60px 0;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__title {
        max-width: 610px;
        width: 100%;
        @include font(72, 500, 1);
        text-align: center;
        margin: 0 auto;
    }

    @include to(1200px) {
        &__wrap {
            padding: 0 20px;
        }
        
        &__title {
            @include font(62, 500, 1);
        }
    }

    @include to(768px) {
        padding: 50px 0;
    
        &__wrap {
            padding: 0 10px;
        }
        
        &__title {
            @include font(52, 500, 1);
        }
    }

    @include to(520px) {
        padding: 40px 0;
    
        &__title {
            @include font(42, 500, 1);
        }
    }
}