\:root
  --chevron-line-stroke: var(--gray)
  // --chevron-size: 12px
  --report-line-height: 24px
  --chevron-angle: 22deg
  --chevron-angle-right: -22deg

  --chevron-width: 30px
  --chevron-height: 38px

.faq
  display: flex
  background: white
  padding-top: 50px
  padding-bottom: 50px
  margin-bottom: 50px
  +to(992px)
    padding: 40px 0 0
  +to(520px)
    margin: 0px 0 25px
    padding: 40px 0 40px
  &__wrap
    padding: 0 50px
    +to(520px)
      padding: 0 25px
  &__picture
    position: absolute
    z-index: -1
    left: 5%
    top: 0
    width: 30%
    height: 98%
    max-height: 140vh
    user-select: none
    transform: rotate(-9deg)
    +to(1020px)
      left: 2%
      transform: rotate(-7deg)
    +to(992px)
      opacity: 0.1
      width: 100%
      top: 40px
      left: 100px
      height: 100vh
    img
      height: 100%
      object-fit: cover
      object-position: left
  &__title
    @include font(52)
    margin-bottom: 28px
    +to(768px)
      font-size: 42px
    +to(520px)
      font-size: 32px
      margin-bottom: 20px
  &__accordion-block
    display: flex
    min-height: 500px
    transition: .3s
    gap: 50px
    +to(768px)
      gap: 25px
      min-height: 400px
    +to(520px)
      flex-direction: column
  &__accordion
    width: calc(100% / 12 * 4)
    +to(520px)
      width: 100%
  &__more
    width: calc(100% / 12 * 8 - 25px)
    +to(768px)
      max-height: 300px
    +to(520px)
      width: 100%

.chevron
  width: var(--chevron-width)
  height: var(--chevron-height)
  // height: 38px
  // margin-top: calc((var(--report-line-height) - 20px) / 2)
  &__lines
    transition: transform 0.3s
    transform: translateY(var(--report-line-height))
  &__line
    stroke: var(--gray)
    stroke-width: var(--chevron-width)
    stroke-width: 5px
    stroke-linecap: round
    // stroke-linecap: square
    transform-origin: 50%
    transform: rotate(var(--chevron-angle))
    transition: transform 300ms, stroke 300ms
    &--right
      transform: rotate(var(--chevron-angle-right))

.accordion
  list-style: none

  &__item
    border-bottom: 1px solid rgba(0,0,0,.1)
    margin-bottom: 14px
    &:last-child
      border-bottom: none
    &.active
      .accordion
        &__content
          visibility: visible
          max-height: 1600px
          opacity: 1
      .chevron
        &__line
          &--right
            transform: rotate(var(--chevron-angle))
          &--left
            transform: rotate(var(--chevron-angle-right))
        &__lines
          transform: translateY(calc(var(--chevron-height) * -1))
          transform: translateY(-4px)
          // -4px
  &__header
    padding-top: 6px
    margin-bottom: 14px
    display: flex
    align-items: center
    justify-content: space-between
    cursor: pointer
    user-select: none
    &:hover
      .accordion
        &__title
          // color: var(--orange)
      .chevron
        &__line
          // stroke: var(--orange)
  &__title
    @include font(22)
    transition: .3s
    +to(768px)
      font-size: 18px
  & .expandable-details
    margin-left: 30px
  &__content
    padding: 0 0 16px 0
    visibility: hidden
    max-height: 0
    opacity: 0
    transition: .3s
    @include font(18, 400, 1.6)
    +to(768px)
      @include font(14, 400, 1.4)
  &__more
    display: none
  &__video
    width: 100%
    height: 100%
    object-fit: cover
  &__picture
    width: 100%
    height: 100%
    object-fit: cover
