.section-blog {
    padding: 160px 0 180px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(92, 500, 1);
        text-transform: uppercase;
        margin: 0 0 70px;
        transition: .3s;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 65px 40px;
    }

    &__block {
        grid-column: auto/span 1;

        &-img {
            max-width: 100%;
            width: 100%;
            aspect-ratio: 14 / 9;
            -webkit-aspect-ratio: 14 / 9;
            min-height: 0;
            margin-bottom: 24px;
            position: relative;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transform: scale(1);
                transition: .2s;
            }
        }

        &-category {
            color: var(--gray-700);
            @include font(14, 400, 1);
            text-transform: uppercase;
            margin-bottom: 14px;
        }

        &-title {
            @include font(20, 500);
            text-decoration: underline transparent;
            margin-bottom: 14px;
            transition: 0.2s;
        }

        &-subtitle {
            color: var(--gray-700);
            @include font(16, 400, 1.4);
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }


        &:hover {
            .section-blog__block-img {
                img {
                    transform: scale(1.1);
                }
            }

            .section-blog__block-title {
                text-decoration: underline;
            }
        }
    }

    &-navigation {
        width: 100%;
        padding-top: 70px;

        &__wrap {
            display: flex;
            flex-direction: column;
            gap: 50px;
            width: 100%;
        }
    }

    &-more {
        display: flex;
        justify-content: center;

        &__btn {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 14px 18px;
            overflow: hidden;
            @include font(12, 500, 1);
            letter-spacing: 0.1em;
            text-transform: uppercase;
            background-color: var(--black);
            color: var(--white);
            cursor: pointer;
            transition: 0.3s;
            position: relative;
            z-index: 1;

            span {
                z-index: 5;
            }

            &::before {
                content: '';
                position: absolute;
                z-index: 0;
                left: 0;
                top: 0;
                bottom: 0;
                width: 0%;
                background-color: var(--gray-200);
                transition: 0.3s;
            }

            &:hover {
                color: var(--black);

                &::before {
                    width: 100%;
                }
            }
        }
    }

    &-pagination {
        display: flex;
        justify-content: center;
        width: 100%;

        &__list {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 8px;

            li {
                a {
                    display: block;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 36px;
                    height: 36px;
                    background-color: var(--white);
                    border: 1px solid var(--gray-200);
                    padding: 2px;
                    @include font(15, 500, 1);
                    color: var(--black);
                    transition: .2s;

                    svg {
                        display: flex;
                        width: 15px;
                        height: 15px;
                        color: var(--black);
                        transition: .2s;
                    }

                    &.active {
                        color: var(--white);
                        background-color: var(--black);
                        border: 1px solid var(--black);
                    }

                    &:hover {
                        color: var(--white);
                        background-color: var(--black);
                        border: 1px solid var(--black);

                        svg {
                            color: var(--white);
                        }
                    }
                }
            }
        }
    }

    @include to(1200px) {
        padding: 125px 0 150px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(62, 500, 1);
            margin: 0 0 40px;
        }

        &__blocks {
            grid-gap: 45px 20px;
        }

        &__block {
            &-img {
                margin-bottom: 18px;
            }

            &-category {
                margin-bottom: 12px;
            }

            &-title {
                margin-bottom: 12px;
            }
        }
    }

    @include to(1024px) {
        &__blocks {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 50px 30px;
        }
    }

    @include to(950px) {
        &__blocks {
            grid-gap: 45px 20px;
        }
    }

    @include to(768px) {
        padding: 108px 0 125px;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 35px;
        }

        &__blocks {
            grid-gap: 34px 10px;
        }

        &__block {
            &-img {
                margin-bottom: 16px;
            }

            &-category {
                @include font(12, 400, 1);
                margin-bottom: 10px;
            }

            &-title {
                @include font(18, 500);
                margin-bottom: 10px;
            }

            &-subtitle {
                @include font(15, 400, 1.4);
            }
        }

        &-navigation {
            padding-top: 45px;

            &__wrap {
                gap: 35px;
            }
        }
    }

    @include to(620px) {
        &__blocks {
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 40px;
        }
    }

    @include to(520px) {
        padding: 95px 0 108px;

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 36px;
        }
    }
}