.calc-price-wrap {
    padding: 150px 0;
    position: relative;
    z-index: 5;
    background: #fff;
    font-weight: 500;
}

.calc-price-title {
    font-size: 82px;
    line-height: 1;
    text-align: center;
    margin-bottom: 60px;
}

.calc-price-subtitle {
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    color: #a6a6a6;
}

.calc-price-but-wrap {
    display: flex;
    justify-content: space-between;
    max-width: 650px;
    margin: 60px auto;
}

.calc-price-info {
    margin: 90px auto 0;
    max-width: 1100px;
    display: flex;
    justify-content: space-between;
}

.calc-price-info-options {
    flex-grow: 1;
    padding-right: 50px;
    padding-top: 30px;
    position: relative;
}

.target-scroll-but3.calc {
    position: absolute;
    left: 0;
    top: -120px;
}

.calc-price-info-options-area {
    margin-bottom: 45px;
}

.calc-price-info-options-area-title,
.calc-price-info-options-service-title {
    font-size: 36px;
    margin-bottom: 40px;
}

.calc-price-info-options-area-field {
    border-bottom: 1px solid #a6a6a6;
}

.calc-price-info-options-area-field input {
    width: 100%;
    padding-bottom: 10px;
    font-size: 16px;
}

.calc-price-info-options-service-list-e {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    border-bottom: 1px solid #a6a6a6;
}

.calc-price-info-options-service-list-e:last-child {
    margin-bottom: 0;
}

.calc-price-info-options-service-list-e-text-title {
    font-size: 18px;
    padding-bottom: 10px;
}

.calc-price-info-options-service-list-e-text-desc {
    font-weight: 400;
    padding-bottom: 10px;
    color: #a6a6a6;
}

.calc-price-info-options-service-list-e-check {
    width: 24px;
    height: 24px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    flex-shrink: 0;
}

.calc-price-info-options-service-list-e-check-tick {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border: 1px solid #a6a6a6;
    background-color: #fff;
    color: #fff;
}

.calc-price-info-options-service-list-e-check-tick.active {
    background: url(../img/sprites/multi.svg#icon-checkbox-check) no-repeat center center #000;
    background-size: 120%;
}

.calc-price-info-rezult {
    width: 350px;
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 3px 18px 0 rgba(0, 2, 4, 0.2);
}

.calc-price-info-options-rezult-title {
    font-size: 36px;
}

.calc-price-info-options-rezult-sum {
    font-size: 36px;
}

.calc-price-info-options-rezult-desc {
    padding-right: 10px;
}

.calc-price-info-options-rezult-but {
    width: 100% !important;
}

.calc-price-info-options-rezult-pic-wrap {
    margin-left: -30px;
}

.calc-price-info-options-rezult-pic-wrap img {
    width: 100%;
}

/*============================================================================*/

@media only screen and (max-width: 799px) {

    .calc-price-title {
        font-size: 62px;
    }

    .calc-price-but-wrap .calc,
    .calc-price-but-wrap .smeta {
        width: calc(50% - 10px);
        max-width: 300px;
    }

    .calc-price-info {
        max-width: 650px;
        flex-flow: column;
    }

    .calc-price-info-rezult {
        width: auto;
        padding: 30px;
    }

    .calc-price-info-options-rezult-title {
        margin-bottom: 20px;
    }

    .calc-price-info-options-rezult-sum {
        margin-bottom: 30px;
    }

    .calc-price-info-options-rezult-desc {
        padding-right: 0;
        margin-bottom: 30px;
    }

    .calc-price-info-options-rezult-desc br {
        display: none;
    }

    .calc-price-info-options-rezult-pic-wrap {
        display: none;
    }

    .calc-price-info-options {
        order: 2;
    }

    .calc-price-info-options {
        padding-right: 0;
        padding-top: 60px;
    }

}

@media only screen and (max-width: 599px) {
    .calc-price-title {
        font-size: 40px;
        margin-bottom: 30px;
    }

    .calc-price-subtitle {
        font-size: 16px;
    }

    .calc-price-subtitle br {
        display: none;
    }

    .calc-price-but-wrap {
        margin: 30px auto;
    }

    .calc-price-but-wrap .calc, .calc-price-but-wrap .smeta {
        height: 43px;
    }

    .calc-price-but-wrap .calc *, .calc-price-but-wrap .smeta *{
        line-height: 43px;
    }

    .calc-price-info {
        margin: 45px auto 0;
    }

    .calc-price-info-options-rezult-title {
        font-size: 30px;
    }

    .calc-price-info-options-rezult-sum {
        font-size: 30px;
    }

    .calc-price-info-options-rezult-but {
        height: 43px !important;
    }

    .calc-price-info-options-rezult-but * {
        line-height: 43px !important;
    }

    .calc-price-info-options-area-title, .calc-price-info-options-service-title {
        font-size: 30px;
        margin-bottom: 30px;
    }
}