.section-project-composition {
    background-color: #D8E1E6;
    overflow: hidden;
    position: relative;
    width: 100%;

    &__img {
        width: 100vw;
        height: auto;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, .16);
        }

        img {
            height: 100%;
            object-fit: contain;
        }
    }

    &__img-mobile {
        display: none;
    }

    &__wrap {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }

    &__blocks {
        width: 100%;
        height: 100%;
        position: relative;
    }

    &__block {
        width: 50%;

        &:nth-child(2) {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }

        &:nth-child(2) {
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1;
        }
    }

    &__head {
        max-width: 500px;
        width: 100%;
    }

    &__title {
        @include font(72, 500, 1);
        color: var(--white);
        margin: 0 0 20px;
        transition: .3s;
    }

    &__subtitle {
        @include font(20, 500);
        color: var(--white);
        margin-bottom: 30px;
        transition: .3s;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--white);
        color: var(--black);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            transition: 0.3s;
            background-color: var(--black);
        }

        &:hover {
            span {
                color: var(--white);
            }

            &::before {
                width: 100%;
            }
        }
    }

    &__block-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 40px;
        bottom: 0;
        z-index: 1;
        width: 49vw;
    }

    &__block-img {
        display: flex;
        align-items: center;
        position: absolute;
        top: 0;
        right: 40px;
        bottom: 0;
        z-index: 1;
        width: 49vw;
        flex-shrink: 0;

        img {
            width: 100%;
            height: auto;
            object-fit: contain;
        }
    }

    &__block-img-mobile {
        display: none;
    }

    @include to(1200px) {

        &__head {
            max-width: 405px;
        }

        &__title {
            @include font(62, 500, 1);
        }

        &__subtitle {
            @include font(18, 500);
        }

        &__block-info {
            top: 24px;
            left: 20px;
        }

        &__block-img {
            top: 26px;
            right: 20px;
        }

    }

    @include to(1090px) {
        &__head {
            max-width: 355px;
            margin-bottom: 28px;
        }

        &__title {
            @include font(52, 500, 1);
            margin: 0 0 18px;
        }

        &__subtitle {
            @include font(17, 500);
        }

        &__block-info {
            top: 57px;
        }

        &__block-img {
            top: 36px;
        }
    }

    @include to(950px) {
        &__img {
            display: none;
        }

        &__img-mobile {
            display: block;
        }

        &__block-info {
            text-align: center;
            justify-content: flex-start;
            align-items: center;
            top: 100px;
            left: initial;
            right: initial;
            width: 100%;
            padding: 0 20px;
        }

        &__head {
            margin: 0 auto 50px auto;
        }

        &__btn {
            margin: 0 auto;
        }

        &__block-img {
            display: none;
        }

        &__block-img-mobile {
            display: block;
            width: 70vw;

            img {
                object-fit: contain;
            }
        }
    }

    @include to(768px) {
        &__block-info {
            padding: 0 10px;
        }

        &__head {
            max-width: 295px;
        }

        &__title {
            @include font(42, 500, 1);
            margin: 0 0 20px;
        }

        &__subtitle {
            @include font(16, 500);
        }

        &__btn {
            width: 180px;
            height: 43px;
        }

    }

    @include to(670px) {
        &__block-info {
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            height: 100vh;
        }

        &__block-info-wrap {
            display: flex;
            flex-direction: column;
            // justify-content: space-between;
            height: 100%;
            padding: 108px 10px 50px 10px;
        }

        &__block-img-mobile {
            width: 100%;
        }

        &__img-mobile {
            height: 100vh;

            img {
                height: 100vh;
                object-fit: cover;
            }
        }
    }

    @include to(520px) {}
}