.section-separate-heading {
    padding: 90px 0;

    &__text {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(144, 500);
        text-transform: uppercase;
        margin-bottom: 12px;
    }

    &__subtitle {
        @include font(20, 500);
    }

    @include to(1200px) {
        padding: 60px 0;

        &__text {
            padding: 0 20px;
        }

        &__title {
            @include font(80, 500);
        }

        &__subtitle {
            max-width: 508px;
            width: 100%;
        }
    }

    @include to(768px) {
        padding: 50px 0;

        &__title {
            @include font(42, 500);
        }

        &__text {
            padding: 0 10px;
            margin-bottom: 10px;
        }
    }

    @include to(520px) {
        &__subtitle {
            @include font(16, 500);
        }
    }

    &--no-padding {
        padding-bottom: 0;
        @include to(1200px) {
            padding-bottom: 0;
        }
    
        @include to(768px) {
            padding-bottom: 0;
        }
    }

}