.vebinar-wrap {
    position: relative;
    z-index: 5;
    background: #fff;
    padding-top: 60px;
	padding-bottom: 60px;
	display: none;
}

#vebinar {
	position: absolute;
	top: 10px;
}

.vebinar {
	max-width: 1440px;
	max-width: 1400px;
	margin: 0 auto;
	padding-top: 30px;
	padding-bottom: 60px;
	border-top: 1px solid #a6a6a6;
	border-bottom: 1px solid #a6a6a6;
}

.vebinar-top {
	height: 875px;
	background: url(../images/vebinar/top-fon.png) no-repeat center top;
	padding: 40px 40px 0;
	position: relative;
	box-sizing: border-box;
	border-radius: 6px 6px 0 0;
}

.vebinar-top-left {
	position: relative;
	z-index: 2;
}

.vebinar-top-date {
	display: inline-table;
	padding: 0 22px;
	min-width: 445px;
	height: 60px;
	line-height: 60px;
	background: #FDF2FE;
    background:-webkit-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-o-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-ms-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-moz-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background: linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    border-radius: 100px;
    box-shadow: 0px 18px 48.06px 5.94px rgba(0, 0, 0, 0.04);
    font-size: 25px;
    font-weight: 700;
    box-sizing: border-box;
}

.vebinar-top-title {
	font-size: 50px;
	font-weight: 700;
	padding: 35px 0 35px;
}

.vebinar-top-desc-title {
	font-size: 25px;
	font-weight: 700;
	margin-bottom: 20px;
	text-transform: uppercase;
}

.vebinar-top-desc-cont-e {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
}

.vebinar-top-desc-cont-e:last-child {
	margin-bottom: 0;
}

.vebinar-top-desc-cont-e-num {
	flex-shrink: 0;
	width: 40px;
	height: 40px;
	line-height: 40px;
	border-radius: 100%;
	background: #fff;
	text-align: center;
	font-size: 18px;
}

.vebinar-top-desc-cont-e-text {
	font-size: 18px;
	padding-left: 30px;
}

.vebinar-top-pic-wrap {
	display: none;
}

.vebinar-bot {
	position: relative;
	padding-left: 40px;
	padding-right: 40px;
}

.vebinar-speaker {
	position: absolute;
	z-index: 2;
	right: 1px;
	top: -150px;
	width: 555px;
	height: 489px;
	background: url(../images/vebinar/speaker-fon.png) no-repeat center top;
	box-sizing: border-box;
	padding: 50px 79px 0;
}

.vebinar-speaker-status {
	font-size: 30px;
}

.vebinar-speaker-name {
	font-size: 30px;
	font-weight: 700;
}

.vebinar-speaker-sep {
	margin: 15px 0 25px;
	overflow: hidden;
	white-space: nowrap;
}

.vebinar-speaker-list-e {
	position: relative;
	padding-left: 25px;
	font-size: 18px;
	margin-bottom: 25px;
}

.vebinar-speaker-list-e:last-child {
	margin-bottom: 0px;
}

.vebinar-speaker-list-e:after {
	content:'';
	position: absolute;
	left: 0;
	top: 8px;
	width: 6px;
	height: 6px;
	border-radius: 100%;
	background: #000;
}

.verbinar-for-whom {
	margin-top:-14px;
	margin-bottom: 135px;
	position: relative;
	z-index: 3;
}

.verbinar-for-whom-title {
	font-size: 50px;
	font-weight: 700;
	margin-bottom: 70px;
}

.verbinar-for-whom-list {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	max-width: 760px;
}

.verbinar-for-whom-list-e {
	flex-shrink: 0;
	width: 50%;
	display: flex;
}

.verbinar-for-whom-list-e:nth-child(1),
.verbinar-for-whom-list-e:nth-child(2) {
	margin-bottom: 75px;
}

.verbinar-for-whom-list-e-num {
	flex-shrink: 0;
	width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 100%;
    background: #363738;
    text-align: center;
    font-size: 18px;
    color: #fff;
}

.verbinar-for-whom-list-e-text {
	padding-left: 20px;
	font-size: 18px;
}

.verbinar-record {
	position: relative;
}

.verbinar-record-pic-arrow {
	position: absolute;
    top: 33px;
    left: 514px;
}

.verbinar-record-title {
    font-size: 50px;
    font-weight: 700;
    margin-bottom: 70px;
}

.verbinar-record-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    max-width: 760px;
}

.verbinar-record-list-e {
    flex-shrink: 0;
    width: 50%;
    box-sizing: border-box;
}

.verbinar-record-list-e:nth-child(1), 
.verbinar-record-list-e:nth-child(2) {
    margin-bottom: 75px;
}

.verbinar-record-list-e:nth-child(2), 
.verbinar-record-list-e:nth-child(4) {
    padding-left: 60px;
}

.verbinar-record-list-e-title {
	font-size: 22px;
	margin-bottom: 10px;
}

.verbinar-record-list-e-desc {
	font-size: 20px;
	color: #0d0df0;
}

.verbinar-contact {
	position: absolute;
	z-index: 2;
	right: 1px;
	top: 261px;
	width: 555px;
	height: 638px;
	background: url(../images/vebinar/contact-fon.png) no-repeat center top;
	box-sizing: border-box;
	padding: 50px 79px 0;
}

.verbinar-contact-in {
	width: 100%;
	max-width: 334px;
	margin: 0 auto;
	text-align: center;
	padding-top: 20px;
}

.verbinar-contact-title {
	font-size: 25px;
	font-weight: 700;
	text-transform: uppercase;
	margin-bottom: 30px;
}

.verbinar-contact-form-field-wrap {
	margin-bottom: 24px;
}

.verbinar-contact-form-field-wrap input {
	box-sizing: border-box;
	width: 100%;
	height: 72px;
	padding: 0 30px;
	background: #fff;
	border-radius: 100px;
	font-size: 17px;
	text-align: center;
}

.verbinar-contact-form-but-wrap {
	margin-top: 47px;
}

.verbinar-contact-form-but-wrap button {
	box-sizing: border-box;
	width: 100%;
	height: 72px;
	padding: 0 30px;
	background: #0405F5;
	color: #fff;
	border-radius: 100px;
	font-size: 18px;
	text-align: center;
}

.vebinar-new-year-but {
	display: block;
	width: 280px;
	height: 64px;
	border-radius: 100px;
	background: url(../images/vebinar/vebinat-new-year-but.png) no-repeat center;
	box-shadow: 0px 18px 48.06px 5.94px rgba(0, 0, 0, 0.15);
	position: fixed;
    z-index: 10;
    bottom: 20px;
    right: 20px;
	display: none;
}

.vebinar-new-year-but img {
	position: absolute;
	left: 9px;
	bottom: 0;
}

.vebinar-new-year-but-text {
	padding-left: 79px;
	height: 64px;
	display: flex;
	align-items: center;
}


@media only screen and (max-width: 1250px) {	
	.verbinar-record-pic-arrow {
	    left: 450px;
	}

	.verbinar-for-whom-list {
	    max-width: 700px;
	}
	
	.verbinar-record-list-e:nth-child(2), 
	.verbinar-record-list-e:nth-child(4) {
    	padding-left: 0px;
	}	
}

@media only screen and (max-width: 1199px) {

.vebinar-top {
    height: auto;
    background-image: none;
    padding: 0;
}

.vebinar-top-date-wrap {
    text-align: center;
}

.vebinar-top-text-wrap {
	background: #EBEBEB;
	padding: 50px 20px 50px;
	margin-top: 40px;
	border-radius: 6px;
	text-align: center;
}

.vebinar-top-title {
	text-align: center;
	padding-top: 0;
	padding-bottom: 50px;
}

.vebinar-top-desc {
	display: inline-block;
}

.vebinar-top-desc-cont {
	text-align: left;
}

.vebinar-top-left {
	margin-bottom: 40px;
}

.vebinar-top-pic-wrap {
	display: block;
	text-align: center;
}

.vebinar-top-pic-wrap img {
	width: 100%;
	max-width: 400px;
}

.vebinar-speaker {
	position: static;
	background-image: none;
	width: auto;
	height: auto;
	padding: 30px 0;
	text-align: center;
}

.vebinar-speaker-in {
	display:inline-block;
	text-align: left;
}

.verbinar-contact {
	margin: 0 auto;
    position: static;
    max-width: 450px;
    width: 100%;
    height: auto;
    background-image: none;
    padding: 30px 30px 40px;
    background: #FDF2FE;
    background:-webkit-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-o-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-ms-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background:-moz-linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    background: linear-gradient(to right, #E5F2FE,#FDF2FE,#FDF2FA,#FEF1ED);
    box-shadow: 0px 18px 48.06px 5.94px rgba(0, 0, 0, 0.04);
    border-radius: 6px;
}

.verbinar-for-whom {
	text-align: center;
	margin-top: 60px;
	margin-bottom: 100px;
}

.verbinar-for-whom-list {
	display: inline-block;
	text-align: left;
	width: auto;
}

.verbinar-for-whom-list-e {
	width: auto;
	margin-bottom: 60px !important;
}

.verbinar-for-whom-list-e:last-child {
	margin-bottom: 0 !important;
}

.verbinar-record {
	margin-bottom: 100px;
}

.verbinar-record-pic-arrow {
	display: none;
}

.verbinar-record-title {
    text-align: center;
}

.verbinar-record-list {
    max-width: 500px;
    margin: 0 auto;
}

.verbinar-record-list-e:nth-child(1),
.verbinar-record-list-e:nth-child(3) {
	width: 64%;
}

.verbinar-record-list-e:nth-child(2),
.verbinar-record-list-e:nth-child(4) {
	width: 36%;
}
	
}


@media only screen and (max-width: 799px) {
	
.vebinar-in {

}

.vebinar-top {
    padding: 0 10px;
}

.vebinar-top-date {
    min-width: 100%;
    padding: 0 15px;
}
	
.vebinar-top-title {
    font-size: 32px;
    padding: 0px 0 40px;
}

.vebinar-top-title br {
	display: none;
}

.vebinar-top-desc-cont-e-text {
    font-size: 17px;
    padding-left: 15px;
}

.vebinar-top-desc-cont-e-text br {
	display: none;
}

.vebinar-top-pic-wrap img {
    max-width: 250px;
}

.vebinar-bot {
    padding-left: 10px;
    padding-right: 10px;
}

.vebinar-speaker-status {
    font-size: 22px;
}

.vebinar-speaker-name {
    font-size: 22px;
}

.vebinar-speaker-list-e {
    padding-left: 20px;
    font-size: 16px;
    margin-bottom: 10px;
}

.verbinar-for-whom {
    margin-top: 50px;
    margin-bottom: 90px;
}

.verbinar-for-whom-title {
    font-size: 32px;
    margin-bottom: 40px;
}

.verbinar-for-whom-list-e {
    margin-bottom: 30px !important;
}

.verbinar-record {
    margin-bottom: 80px;
}

.verbinar-record-title {
    font-size: 32px;
    margin-bottom: 40px;
}

.verbinar-record-list {
	padding-left: 10px;
}

.verbinar-record-list-e {
    width: calc(50% - 15px) !important;
}

.verbinar-record-list-e:nth-child(1), 
.verbinar-record-list-e:nth-child(2) {
    margin-bottom: 30px;
}

.verbinar-record-list-e-title {
    font-size: 18px;
}

.verbinar-record-list-e-desc {
    font-size: 18px;
}

.vebinar-new-year-but {
    transform: scale(0.8);
    transform-origin: right;
    bottom: 20px;
    right: 20px;
}
	
}	


@media only screen and (max-width: 599px) {
	.vebinar-top-date {
	    font-size: 18px;
	}
	
	.vebinar-top-text-wrap {
	    margin-top: 20px;
	}
	
	.vebinar-top-left {
	    margin-bottom: 20px;
	}
	
	.vebinar-speaker-status {
	    text-align: center;
	}
	
	.vebinar-speaker-status-defis {
		display: none;
	}
	
	.vebinar-speaker-name {
	    text-align: center;
	}
	
	.vebinar-speaker {
	    padding-bottom: 0;
	}
	
	.vebinar-speaker-sep {
	    margin: 15px 0 15px;
	}
	
	.verbinar-for-whom {
	    margin-bottom: 60px;
	}
	
	.verbinar-record {
		margin-bottom: 60px;
	}
	
	.vebinar-new-year-but {
	    bottom: 15px;
	}
	
	.verbinar-record-list-e-title {
		font-size: 16px;
	}
	
	.verbinar-record-list-e-desc {
	    font-size: 16px;
	}
	
}
