.promo-gallery
  background: white
  padding-top: 50px
  padding-bottom: 50px
  margin-bottom: 50px
  +to(520px)
    padding-right: 20px
    padding-left: 20px
  +to(520px)
    padding-right: 20px
    padding-left: 20px
  &__title
    // padding: 0 50px
    @include font(52)
    margin-bottom: 20px
    +to(768px)
      font-size: 42px
    +to(520px)
      font-size: 36px
  &__text
    margin-bottom: 60px
    max-width: 700px
    width: 100%
    @include font(20, 500)
    color: var(--gray-700)
  &__swiper, &__swiper-why-us
    user-select: none
    & .swiper-slide-active
      .promo-slide__info
        opacity: 1

    & .swiper-slide
      opacity: .3
    & .swiper-slide-active
      opacity: 1
  &__buttons
    display: flex
    flex-direction: row
    gap: 15px
    padding: 0 50px
    justify-content: flex-end
    position: absolute
    bottom: 0
    width: 100%
    z-index: 1
    +to(520px)
      margin-top: 25px
      padding: 0
    +to(520px)
      margin-top: 25px
      padding: 0

.promo-slide
  &__picture
    margin-bottom: 30px
    display: block
  &__info
    opacity: 0
    transition: .3s
  &__info-title
    @include font(22)
    margin-bottom: 10px
  &__info-text
    @include font(18, 400, 1.4)
    +to(768px)
      font-size: 16px
    +to(768px)
      font-size: 16px

  // transform: scale(0.6)

//     
.promo-gallery__buttons
  position: relative
  transition: .3s

.promo-gallery__buttons
  position: relative
  transition: .3s

.promo-gallery__button-prev, .promo-gallery__button-next
  background: rgba(#000, .2)
  background-image: url('../img/service-arrow.svg')
  background-size: 15px
  background-position: center center
  background-repeat: no-repeat
  border-radius: 20px
  display: block
  width: 36px
  height: 36px

  &:hover
    cursor: pointer
  &.swiper-button-disabled
    opacity: .3
    &:hover
      cursor: default

.promo-gallery__button-prev
.promo-gallery__button-next
  transform: scaleX(-1)
