.our-team-section {
    padding: 40px;
    background-color: rgb(255, 255, 255);
}

.our-team-wrap {
    display: grid;
    grid-template-columns: repeat(3, minmax(200px, 500px));
    gap: 50px;
}

.our-team-wrap figure {
    margin-bottom: 300px;
}

.our-team-wrap h4 {
    font-size: 52px;
    font-weight: 500;
    margin-bottom: 20px;
}

.our-team-wrap div p {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
}

.our-team-wrap img {
    width: 100%;
}

.our-team-wrap figcaption {
    font-size: 20px;
    font-weight: 500;
    padding-top: 20px;
}

.our-team-wrap span {
    color: #a6a6a6;
    display: block;
    padding-top: 15px;
    font-size: 16px;
}

.our-team-wrap span.descript {
    color: #000000;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.5;
}

@include to(1023px) {
    .our-team-section {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .our-team-section {
        padding-left: 10px;
        padding-right: 10px;
    }

    .our-team-wrap {
        grid-template-columns: 1fr;
        gap: 50px;
    }

    .our-team-wrap figure {
        margin-bottom: 0;
    }

    .our-team-wrap span {
        padding-top: 8px;
        font-size: 22px;
    }

    .our-team-wrap h4 {
        font-size: 40px;
    }
}