.section-planning-solution-life {
    padding: 100px 0 60px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        text-align: center;
        @include font(62, 500, 1);
        margin-bottom: 80px;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
    }

    &__block {
        grid-column: auto/span 1;
        max-width: 314px;
        width: 100%;
        opacity: 0;
        transform-style: preserve-3d;
        transform: perspective(1200px) rotateX(20deg) translateY(14%);

        &-img {
            margin-bottom: 20px;
        }

        &-text {
            @include font(18, 500, 1);
        }
    }

    @keyframes planningSolutionLifeAnimItems {
        0% {
            opacity: 0;
            transform-style: preserve-3d;
            transform: perspective(1200px) rotateX(20deg) translateY(14%);
        }

        100% {
            opacity: 1;
            transform-style: flat;
            transform: perspective(1200px) rotateX(0deg) translateY(0);
        }
    }

    &__wrap {

        &.active {
            .section-planning-solution-life__block {
                animation-name: planningSolutionLifeAnimItems;
                animation-duration: 0.45s;
                animation-fill-mode: forwards;
                animation-timing-function: linear;
            }

            .section-planning-solution-life__block:nth-child(1) {
                animation-delay: 0s;
            }

            .section-planning-solution-life__block:nth-child(2) {
                animation-delay: 0.45s;
            }

            .section-planning-solution-life__block:nth-child(3) {
                animation-delay: 0.9s;
            }

            .section-planning-solution-life__block:nth-child(4) {
                animation-delay: 1.3s;
            }
        }
    }

    @include to(1200px) {
        padding: 80px 0 50px;

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 50px;
        }
    }

    @include to(950px) {}

    @include to(768px) {
        padding: 60px 0 40px;

        &__wrap {
            padding: 0 10px;
        }

        &__blocks {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 33px 20px;
        }

        &__block {
            grid-column: auto/span 1;
            max-width: 100%;

            &-img {
                margin-bottom: 16px;

                img {
                    width: 100%;
                }
            }
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 40px;
        }
    }

    @include to(620px) {
        &__block {
            &-img {
                margin-bottom: 15px;
            }

            &-text {
                @include font(17, 500, 1);
            }
        }
    }

    @include to(520px) {
        padding: 45px 0 40px;

        &__blocks {
            grid-gap: 24px 10px;
        }

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 33px;
        }

        &__block {
            &-img {
                margin-bottom: 14px;
            }

            &-text {
                @include font(16, 500, 1);
            }
        }
    }


    @include to(420px) {
        &__title {
            br {
                display: none;
            }
        }
    }
}