.section-facade-design-price {
    padding: 100px 0 110px;
    background-color: var(--gray-section);

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        text-align: center;
        @include font(72, 500, 1);
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 80px;
    }

    &__blocks {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 25px;
    }

    &__block {
        flex: 1;
        display: flex;
        flex-direction: column;
        max-width: 440px;
        width: 100%;
        overflow: hidden;
        background-color: var(--white);
        border-radius: 20px;
        padding: 24px 20px 96px;
        border: 1px solid var(--white);
        position: relative;

        &-head {
            min-height: 146px;
            margin-bottom: 10px;
        }

        &-title {
            @include font(32, 500, 1);
            margin-bottom: 25px;
        }

        &-subtitle {
            @include font(16, 400);
            color: var(--gray-400);
        }

        &-value {
            @include font(28, 500, 1);
            margin: 20px 0 25px 0;
            padding: 22px 0;
            border-top: 1px solid var(--gray-300);
            border-bottom: 1px solid var(--gray-300);
        }

        &-list {

            &-title {
                @include font(18, 500);
                margin-bottom: 11px;
            }

            li {

                @include font(15, 500);
                padding: 5px 0 5px 24px;
                position: relative;

                &:before {
                    content: '';
                    display: flex;
                    width: 22px;
                    height: 16px;
                    background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat;
                    background-size: 30px;
                    position: absolute;
                    top: 8px;
                    left: -6px;
                }
            }
        }


        &-text {
            flex-shrink: 0;
            flex-direction: column;
            gap: 25px;
            @include font(15, 400, 1);

        }

        &-wrap {
            flex-shrink: 0;
            max-width: 100%;
            width: 100%;
            position: absolute;
            left: 0;
            left: 50%;
            bottom: 20px;
            padding: 0 20px;
            transform: translateX(-50%);
        }

        &-btn {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: 100%;
            width: 100%;
            height: 43px;
            overflow: hidden;
            @include font(12, 500, 1);
            letter-spacing: 0.1em;
            text-transform: uppercase;
            background-color: var(--black);
            color: var(--white);
            cursor: pointer;
            transition: 0.3s;
            position: relative;
            z-index: 1;

            span {
                z-index: 5;
            }

            &::before {
                content: '';
                position: absolute;
                z-index: 0;
                left: 0;
                top: 0;
                bottom: 0;
                width: 0%;
                background-color: var(--gray-200);
                transition: 0.3s;
            }

            &:hover {
                color: var(--black);

                &::before {
                    width: 100%;
                }
            }
        }
    }

    @include to(1200px) {
        padding: 80px 0 90px;

        &__wrap {
            padding: 0 20px;
        }

        &__blocks {
            gap: 15px;
        }

        &__block {
            padding: 24px 20px 87px;

            &-head {
                min-height: 154px;
            }

            &-title {
                @include font(28, 500, 1);
                margin-bottom: 20px;
            }

            &-value {
                @include font(24, 500, 1);
                margin: 0 0 20px 0;
                padding: 18px 0;
            }

        }

        &__title {
            @include font(62, 500, 1);
            margin-bottom: 70px;
        }
    }

    @include to(1040px) {

        &__blocks {
            gap: 15px;
        }

        &__block {
            padding: 13px 13px 72px;

            &-wrap {
                bottom: 16px;
                padding: 0 13px;
            }

            &-btn {
                height: 40px;
            }
        }
    }

    @include to(940px) {
        &__blocks {
            flex-direction: column;
            gap: 25px;
        }

        &__block {
            max-width: 100%;
            padding: 14px 14px 75px;

            &-head {
                min-height: 1px;
                margin-bottom: 20px;
            }

            &-value {
                margin: 0 0 16px 0;
            }

            &-list {
                &-title {
                    margin-bottom: 7px;
                }
            }

            &-wrap {
                padding: 0 14px;
            }
        }
    }

    @include to(920px) {
        padding: 70px 0 80px;

        &__title {
            @include font(52, 500, 1);
            margin-bottom: 60px;
        }
    }

    @include to(768px) {
        padding: 60px 0 70px;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 50px;
        }
    }

    @include to(520px) {
        padding: 50px 0 60px;

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 40px;
        }
    }

    @include to(490px) {
        &__title {

            br {
                display: none;
            }
        }
    }
}