.img-comparison-slider {
    position: relative;
    height: 750px;
    width: 100%;
    overflow: hidden;
    background-color: var(--white);

    &__images {
        height: 100%;
        width: 100%;
        display: flex;

        .img-comparison-slider__img-after {
            min-width: 90rem;
            width: 100%;
            height: 750px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            overflow: hidden;
        }

        .img-comparison-slider__img-before {
            position: absolute;
            height: 100%;
            width: 50%;
            z-index: 2;
            overflow: hidden;

            &-wrap {
                min-width: 90rem;
                width: 100%;
                height: 750px;
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center center;
                position: relative;
            }
        }
    }

    &__switch {
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 99;

        input {
            width: 100%;
            outline: none;
            background: none;
            -webkit-appearance: none;
        }

        input::-webkit-slider-thumb {
            height: 750px;
            width: 3px;
            background: none;
            -webkit-appearance: none;
            cursor: col-resize;
        }
    
        .img-comparison-slider__drag-line {
            width: 3px;
            height: 750px;
            position: absolute;
            left: 49.85%;
            pointer-events: none;
            z-index: 10;

            &::before,
            &::after {
                position: absolute;
                content: "";
                width: 100%;
                height: 47.6%;
                background-color: var(--white);
            }

            &::before {
                top: 0;
            }

            &::after {
                bottom: 0;
            }

            span {
                height: 42px;
                width: 42px;
                border: 3px solid #fff;
                background-color: rgba(115, 115, 115, 0.5);
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
                backdrop-filter: blur(12px);
                -webkit-backdrop-filter: blur(12px);
                position: absolute;
                top: 50%;
                left: 50%;
                border-radius: 50%;
                transform: translate(-50%, -50%);

                &::before,
                &::after {
                    position: absolute;
                    content: "";
                    top: 50%;
                    border: 10px solid transparent;
                    border-bottom-width: 0px;
                    border-right-width: 0px;
                    transform: translate(-50%, -50%) rotate(45deg);
                }

                &::before {
                    left: 40%;
                    border-left-color: #fff;
                }

                &::after {
                    left: 60%;
                    border-top-color: #fff;
                }
            }
        }
    }

    .img-comparison-slider-label {
        width: 100%;
        height: 100%;
        position: relative;

        &__wrap {
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            width: 94px;
            height: 42px;
            @include font(20, 500, 1);
            color: var(--white);
            background-color: rgba(10, 10, 10, 0.65);
            backdrop-filter: blur(5px);
            -webkit-backdrop-filter: blur(5px);
            border-radius: 4px;
            z-index: 1;

            &--after {
                bottom: 0;
                right: 0;
                margin-top: 10px;
                margin-right: 20px;
                margin-bottom: 20px;
            }

            &--before {
                bottom: 0;
                left: 0;
                margin-top: 10px;
                margin-left: 20px;
                margin-bottom: 20px;
            }
        }
    }

