.section-about-welcome
    min-height: 490px
    overflow: hidden
    &__wrap
        position: absolute
        padding-top: 12vh
        top: 0
        +to(799px)
            width: 100%
    &__picture-block
        +to(1023px)
            clip-path: polygon(10% 0%, 90% 10%, 100% 100%, 0% 80%)
            animation: about-welcome-animation-mob 3s forwards
        +to(799px)
            margin-top: 305px
        +to(520px)
            height: inherit
        animation: about-welcome-animation 3s forwards
    &__picture
        position: relative
        display: flex
        img
            margin-left: auto
            margin-right: auto
    &__head
        margin-bottom: 12px
        @include font(92)
        color: white
        text-transform: uppercase
        +to(799px)
            font-size: 40px

.body--advantages .section-about-welcome,
.body--team .section-about-welcome
    min-height: 470px
    +to(799px)
        min-height: 265px
.body--vacancies .section-about-welcome
    +to(799px)
        min-height: 265px
.body--cooperation .section-about-welcome
    min-height: 360px
    +to(799px)
        min-height: 215px
        

@keyframes about-welcome-animation
    from
        clip-path: polygon(25% 10%, 60% 20%, 65% 80%, 22% 60%)
    to
        clip-path: polygon(40% 5%, 75% 25%, 72% 95%, 25% 65%)
@keyframes about-welcome-animation-mob
    from
        clip-path: polygon(5% 16%, 70% 5%, 80% 80%, 5% 60%)
    to
        clip-path: polygon(10% 0%, 90% 10%, 100% 100%, 0% 80%)

.about-menu-header
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
.body--cooperation .about-menu-header
        align-items: flex-start
        margin-left: 5px
        +to(799px)
            margin-left: 0

.about-menu
    +to(799px)
        display: none
    &__list
        --underline-width:0
        --underline-offset-x:0
        position: relative
        display: inline-flex
        justify-content: space-between
        margin: 0
        padding: 0
        list-style: none
        width: 780px

        &::before
            content: ""
            display: block
            position: absolute
            top: 100%
            height: 4px
            width: var(--underline-width)
            transform: translateX(var(--underline-offset-x))
            background-color: #ffffff
            transition: transform 0.5s, width 0.5s
            border-radius: 4px
    &__link
        display: block
        padding: 0.5rem 0 0.5rem 0
        font-size: 18px
        color: #ffffff
        text-decoration: none
        font-weight: 500
        +to(1023px)
            font-size: 14px
            padding: 0.5rem 0.6rem

.about-menu-mob
    display: none
    margin-top: -12px
    color: rgb(255, 255, 255)
    font-size: 18px
    background-color: none
    width: 100%
    margin-top: 26px
    +to(799px)
        display: block
    li
        padding: 10px 0
        display: none
        a
            width: 100%
            display: inline-block
        &.curent_link
            display: block
            border-bottom: 1px solid rgb(255, 255, 255)
            padding: 10px 0
            a
                background-image: url(../img/about-menu-arr.png)
                background-position: right bottom
                background-repeat: no-repeat
                padding-right: 35px
                display: inline-block
                width: 100%
    &--visible
        background-image: linear-gradient(to bottom, transparent 0, transparent 44px, rgb(255,255,255) 44px, rgb(255,255,255) 41px 100%)
        color: rgb(0, 0, 0)
        z-index: 2000
        li
            display: block !important
            padding-left: 20px
            &.curent_link
                a
                    background-image: url(../img/about-menu-arr-black.png) !important
                    color: rgb(255, 255, 255)