.section-project-composition-info {
    padding: 100px 0 40px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
    }

    &__title {
        max-width: 910px;
        width: 100%;
        @include font(62, 500, 1);
        margin-bottom: 70px;
    }

    &__blocks {
        display: flex;
        justify-content: space-between;
    }

    &__block {
        max-width: 300px;
        width: 100%;

        &:nth-child(1) {
            margin-right: 20px;
        }

        &:nth-child(2) {
            margin-right: 25px;
            margin-left: 20px;
        }

        &:nth-child(3) {
            margin-left: 25px;
            margin-right: 20px;
        }

        &:nth-child(4) {
            max-width: 240px;
            margin-left: 20px;
        }
    }

    &__block-title {
        @include font(20, 500, 1);
        margin-bottom: 25px;
    }

    &__block-subtitle {
        @include font(16, 400, 1);
    }

    @include to(1200px) {
        padding: 80px 0 40px;

        &__wrap {
            padding: 0 20px;
        }
    }

    @include to(950px) {

        &__blocks {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 40px 90px;
        }

        &__block {
            max-width: 330px;
            width: 100%;
            grid-column: auto/span 1;

            &:nth-child(1) {
                margin-right: initial;
            }

            &:nth-child(2) {
                margin-right: initial;
                margin-left: initial;
            }

            &:nth-child(3) {
                margin-left: initial;
                margin-right: initial;
            }

            &:nth-child(4) {
                max-width: initial;
                margin-left: initial;
            }
        }

        &__block-title {
            @include font(20, 500, 1);
            margin-bottom: 20px;
        }
    }

    @include to(768px) {
        padding: 60px 0 40px;

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            max-width: 620px;
            @include font(42, 500, 1);
            margin-bottom: 40px;
        }

        &__blocks {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 30px 70px;
        }

        &__block {
            max-width: 270px;
        }

        &__block-title {
            @include font(18, 500, 1);
            margin-bottom: 17px;
        }

        &__block-subtitle {
            @include font(15, 400, 1);
        }
    }

    @include to(520px) {
        padding: 48px 0 30px;

        &__title {
            @include font(32, 500, 1);
            margin-bottom: 45px;
        }

        &__blocks {
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 35px 20px;
        }

        &__block-subtitle {}
    }
}