.save {
    padding: 90px 0 120px;

    &__title {
        @include font(62, 500);
        margin-bottom: 20px;
    }

    &__text {
        max-width: 580px;
        width: 100%;
        @include font(20, 500);
        color: var(--gray-700);
        margin-bottom: 60px;
    }

    &__block {
        width: 100%;
        position: relative;
    }

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 100%;
        width: 100%;
        height: auto;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__dots {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    &__dots-wrap {
        height: 100%;
        position: relative;
    }

    &__dot {
        width: 34px;
        height: 34px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;

        &:hover {
            z-index: 10;
        }
    }

    &__dot-wrap {
        position: relative;
        z-index: 1;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            z-index: -1;
            width: 28px;
            height: 28px;
            border-radius: 50%;
            animation-duration: 2.1s;
            animation-iteration-count: infinite;
        }
    }

    @keyframes pulseDotYourBudget {
        0% {
            scale: 1;
            background-color: rgba(#fff, 0);
        }

        90% {
            scale: 1.65;
            background-color: rgba(#fff, 1);
        }


        100% {
            background-color: rgba(#fff, 0);
            scale: 1;
            opacity: 0;
        }
    }


    &__dot-num {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 34px;
        border-radius: 40px;
        color: var(--white);
        background-color: #d40136;
        @include font(16, 500);
        cursor: default;
        transition: .2s;
    }

    &__card {
        width: 300px;
        height: auto;
        padding: 15px;
        background-color: var(--white);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        border-radius: 8px;
        overflow: hidden;
        position: absolute;
        top: 18px;
        left: 20px;
        z-index: -1;
        transition: .2s;
    }

    &__card-num {
        @include font(16, 500);
        margin-bottom: 8px;
    }

    &__card-title {
        @include font(18, 500);
        margin-bottom: 12px;
    }

    &__card-text {
        @include font(15, 400);
    }

    &__dot--1 {
        top: 9%;
        right: 30%;
    }

    &__dot--2 {
        top: initial;
        right: initial;
        left: 7%;
        bottom: 6%;

        .save__card {
            top: initial;
            bottom: 18px;
        }
    }

    &__dot--3 {
        top: 44%;
        right: 51%;
    }

    @include to(1200px) {
        padding: 60px 0 90px;
    }

    @include to(990px) {
        &__dot--1 {
            top: 9%;
            right: 34%;
        }

        &__dot--2 {
            top: initial;
            right: initial;
            left: 4%;
            bottom: 4%;
        }
    }

    @include to(910px) {
        &__dot--1 {
            top: 5%;
            right: 22%;

            .save__card {
                left: initial;
                right: 18px;
            }
        }
    }

    @include to(840px) {
        &__dot--3 {
            top: 44%;
            right: 49%;
        }
    }

    @include to(768px) {
        padding: 50px 0 80px;

        &__title {
            @include font(42, 500);
        }

        &__text {
            @include font(18, 500);
        }

        &__dot-wrap {
            &::after {
                animation-name: pulseDotYourBudget;
            }
        }

        &__dot {
            width: 28px;
            height: 28px;

            &:hover {
                .save__dot-num {
                    background-color: #d40136;
                    color: var(--white);
                }

                .save__card {
                    pointer-events: all;
                    opacity: 1;
                    z-index: 5;
                }
            }
        }

        &__dot-num {
            width: 28px;
            height: 28px;
            background-color: var(--white);
            color: var(--black);
            @include font(14, 500);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
        }

        &__card {
            opacity: 0;
            pointer-events: none;
        }

        &__dot--3 {
            .save__card {
                left: initial;
                right: 18px;
            }
        }
    }

    @include to(692px) {
        &__dot--3 {
            .save__card {
                top: initial;
                left: 50%;
                right: initial;
                bottom: 32px;
                transform: translateX(-50%);
            }
        }
    }

    @include to(520px) {
        padding: 50px 0 60px;
    
        &__title {
            @include font(32, 500);
            margin-bottom: 15px;
        }
        
        &__text {
            @include font(16, 500);
            margin-bottom: 30px;
        }
    
        &__dot {
            width: 25px;
            height: 25px;
        }

        &__dot-wrap {
            &::after {
                width: 25px;
                height: 25px;
            }
        }

        &__dot-num {
            width: 25px;
            height: 25px;
        }
    
        &__dot--1 {
            top: 6%;
            right: 27%;

            .save__card {
                right: -94px;
                top: 30px;
            }
        }

        &__dot--2 {
            .save__card {
                bottom: 30px;
                left: 0px;
            }
        }

        &__dot--3 {
            top: 43%;
            right: 50%;
        }

        &__card {
            width: 254px;
            padding: 12px;
        }

        &__card-num {
            @include font(14, 500);
            margin-bottom: 5px;
        }

        &__card-title {
            @include font(16, 500);
            margin-bottom: 10px;
        }

        &__card-text {
            @include font(14, 400);
        }
    }

    @include to(390px) {
        &__dot--1 {
            .save__card {
                right: -80px;
            }
        }

        &__dot--3 {
            top: 41%;
            right: initial;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    @include to(340px) {
        &__dot--1 {
            .save__card {
                right: -70px;
            }
        }
    }
}