.section-services
  background: white
  position: relative
  padding-top: 100px
  margin-bottom: 200px
  +to(768px)
    margin-bottom: 100px
  // +to(520px)
  //   margin-bottom: 500px
  &__wrap
  &__header
    margin-bottom: 82px
    +to(520px)
      margin-bottom: 62px
  &__title
    @include font(62, 500, 1)
    margin-bottom: 28px
    transition: .3s linear
    +to(768px)
      font-size: 52px
    +to(520px)
      font-size: 40px
      br
        display: none
  &__text
    @include font(20)
    width: calc(100% / 12 * 5)
    +to(768px)
      width: 100%
    +to(520px)
      font-size: 16px
      line-height: 1.4
  &__presentation

.presentation
  position: relative
  display: flex
  flex-direction: row
  gap: 40px
  +to(768px)
    gap: 20px
  +to(520px)
    flex-direction: column
  &__links-block
    width: calc(100% / 12 * 5)
    display: flex
    flex-direction: column
    gap: 20px
    +to(520px)
      width: 100%
  &__list
    list-style: none
    display: flex
    flex-direction: column
    gap: 20px
    counter-reset: li
    +to(520px)
      gap: 40px
  &__list-item
    +to(520px)
      position: relative
      &:before
        position: absolute
        top: 0
        right: 0
        counter-increment: li
        content: counter(li, decimal-leading-zero)
        color: #a6a6a6
        @include font(20)
      &:after
        content: ''
        position: absolute
        bottom: -20px
        left: 0
        right: 0
        height: 1px
        background: var(--gray-300)
      &:last-child
        &:after
          display: none
    &.active
      +to(520px)
        &:before
          color: black
  &__link
    position: relative
    text-decoration: none
    color: #a6a6a6
    opacity: 1
    transition: .3s ease
    @include font(36)
    +to(768px)
      font-size: 20px
    &::after
      content: ''
      background-image: url('../img/service-arrow.svg')
      background-size: contain
      background-repeat: no-repeat
      background-position: center
      width: 30px
      height: 22px
      display: inline-block
      opacity: 0
      transform: translateX(-7px)
      transform-origin: left
      transition: .3s ease
      +to(768px)
        width: 20px
        height: 14px
    &::before
      content: ''
      display: inline-block
      position: absolute
      bottom: -3px
      border-bottom: 3px solid black
      width: 100%
      transform-origin: left
      transform: scaleX(0)
      transition: .4s ease
    &.active
      color: black
      &::after
        opacity: 1
        transform: translateX(0px)
      &::before
        transform: scaleX(1)
  &__more
    @include font(14)
    width: fit-content
    display: inline-block
    text-transform: uppercase
    color: black
    position: relative
    +to(520px)
      margin: 20px 0 0 0
    &::after
      content: ''
      background-image: url('../img/service-arrow.svg')
      background-size: contain
      background-repeat: no-repeat
      background-position: center
      height: 12px
      width: 24px
      display: inline-block
      opacity: 1
      transform: translateX(0px)
      transform-origin: left
      transition: .3s ease
    &::before
      content: ''
      display: inline-block
      position: absolute
      bottom: -3px
      border-bottom: 3px solid black
      width: 100%
      transform: scaleX(1)
      transform-origin: right
      transition: .4s ease
    &:hover
      &::after
        transform: translateX(7px)
      &::before
        transform: scaleX(0)
  &__description
    width: calc(100% / 12 * 7)
    position: relative
    +to(520px)
      width: 100%

.presentation-info
  opacity: 0
  position: absolute
  top: 0
  right: 0
  transform: translateX(-8px)
  transition: .5s ease
  width: calc(100% / 12 * 7)
  max-height: 100%
  z-index: 1
  +to(520px)
    transform: translateX(0)
    position: relative
    width: 100%
    max-height: 0
    opacity: 0
    overflow: hidden
    animation: none
  &.active
    transform: translateX(0)
    opacity: 1
    z-index: 2
    +to(520px)
      display: block
      max-height: 1000px
      animation: presentationIn .3s forwards linear
  &__picture
    display: block
    img
      line-height: 0
    +to(520px)
      margin: 20px 0 0 0
  &__text
    font-size: 18px
    margin-top: 20px
    +to(768px)
      font-size: 16px
  &__bandage
    display: block
    background: #f5f5f7
    border-radius: 4px
    margin-bottom: 10px
    width: fit-content
    padding: 5px 15px
    @include font
    color: #a6a6a6

.fade, .fade2
  opacity: .1
  visibility: visible !important
  &--animated
    animation: fade .75s forwards linear

@keyframes fade
  0%
    opacity: .1
  100%
    opacity: 1

@keyframes presentationIn
  0%
    opacity: 0.8
    max-height: 1000px
    transform: translateY(5px)
  75%
    opacity: 0.95
    max-height: 1000px
    transform: translateY(0)
  100%
    opacity: 1
    max-height: 1000px
    transform: translateY(0)