.section-welcome
  // margin-bottom: 120px
  max-height: 170vh
  overflow: hidden
  +to(768px)
    height: 85vh
  &__wrap
    position: absolute
    padding-top: 23vh
    top: 0
  &__picture-block
    +to(520px)
      height: inherit
    .simpleParallax
      +to(520px)
        height: inherit
  &__picture
    position: relative
    display: flex
    +to(520px)
      height: inherit
    img
      height: inherit
      width: 100%
      object-fit: cover
    &::after
      content: ''
      display: block
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100vh
      // background: linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(255, 0, 0, 0) 60%)
      // +to(520px)
        // background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(255, 0, 0, 0) 44%)
  &__head
    @include font(92)
    color: white
    text-transform: uppercase
    +to(768px)
      font-size: 72px
    +to(600px)
      font-size: 62px
    +to(520px)
      font-size: 42px
  &__head-mask
    display: block
    overflow: hidden
  &__head-text
    display: block
    transform: translateY(160%) skewY(4deg)
    opacity: 0
    text-shadow: 0px 0px 1px rgba(0,0,0,.06)
    animation: head-show 2.2s cubic-bezier(0.25, 0, 0, 1) forwards
  & > .good-wrap
    box-shadow: 0px -40px 30px rgba(0,0,0,.25)

@keyframes head-show
  0%
  100%
    transform: translateY(0) skewY(0)
    opacity: 1
