.section-we-ideal {
    background-color: rgb(255, 255, 255);
    padding-bottom: 80px;
    padding: 40px;
}

.section-we-ideal h3 {
    font-size: 62px;
    font-weight: 500;
    padding-bottom: 120px;
}

.section-we-ideal .wrap_block_text {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 80px;
    justify-content: space-between;
    gap: 25px;
}

.section-we-ideal .wrap_block_text div {
    max-width: 400px;
    flex: 1 1 250px;
}

.section-we-ideal .wrap_block_text h4 {
    display: block;
    font-size: 20px;
    font-weight: 500;
    padding-bottom: 24px;
}

.section-we-ideal .wrap_block_text p {
    font-size: 16px;
    font-weight: 400;
}

@include to(1023px) {
    .section-we-ideal {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .section-we-ideal {
        padding-left: 10px;
        padding-right: 10px;
    }

    .section-we-ideal h3 {
        font-size: 40px;
        padding-bottom: 50px;
    }

    .section-we-ideal .wrap_block_text h4 {
        font-size: 28px;
        padding-bottom: 15px;
    }

    .section-we-ideal .wrap_block_text p {
        font-size: 17px;
    }

    .section-we-ideal .wrap_block_text {
        padding-bottom: 30px;
    }

    .section-we-ideal {
        padding-bottom: 40px;
    }
}