.section-block-seo {
    height: auto;
    position: relative;

    &__items {
        display: flex;
    }

    &__item {
        width: 50%;
        min-height: 800px;

        &--img {
            overflow: hidden;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            background-image: url(../img/bg-show-blur-1.jpg);
            padding: 60px 8% 60px 8%;
        }

        &--text {
            display: flex;
            flex-direction: column;
            background-color: var(--gray-section);
            padding: 60px 8% 60px 8%;
        }
    }

    &__img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
    }


    &__title {
        @include font(45, 500);
        color: var(--white);
    }

    &__subtitle {
        @include font(22, 500);
        margin-bottom: 15px;
    }

    &__text {
        @include font(15, 400);

        p {
            margin: 15px 0;
        }

        a {
            text-decoration: underline;
            transition: .2s;

            &:hover {
                color: var(--gray-700);
            }
        }
    }

    @include to(1200px) {

        &__item {
            min-height: 500px;

            &--img {
                padding: 50px 5% 50px 5%;
            }

            &--text {
                padding: 50px 5% 50px 5%;
            }
        }
    }

    @include to(768px) {
        &__items {
            flex-direction: column;
        }

        &__item {
            width: 100%;

            &--img {
                min-height: 500px;
                padding: 40px 10px 40px 10px;
            }

            &--text {
                padding: 7% 5% 5% 5%;
            }
        }

        &__title {
            @include font(42, 500);
        }

        &__subtitle {
            @include font(20, 500);
            margin-bottom: 8px;
        }
    }

    @include to(520px) {
        &__title {
            @include font(32, 500);
        }

        &__item {
            &--img {
                min-height: 400px;
            }
        }

    }
}