.section-planning-solution {
    overflow: hidden;
    position: relative;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-image: url(../img/section-planning-solution-bg.webp);


    &__wrap {
        display: flex;
        align-items: center;
        max-width: 100%;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        padding-top: 20px;
        padding-left: 40px;
        padding-right: 40px;
        padding-bottom: 20px;
        backdrop-filter: blur(20px);
        -webkit-backdrop-filter: blur(20px);
        background-color: rgba(0, 0, 0, 0.25);
    }

    &__blocks {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 70px;
    }
    
    &__title {
        @include font(60, 500, 1.2);
        color: var(--white);
        margin: 0 0 30px 0;
    }

    &__subtitle {
        @include font(18, 500);
        color: var(--white);
        margin-bottom: 30px;
    }

    &__block-img {
        img {
            width: 52vw;
            object-fit: contain;
        }
    }

    @include to(1200px) {
        &__wrap {
            padding-left: 20px;
            padding-right: 20px;
        }

        &__blocks {
            gap: 40px;
        }

        &__title {
            @include font(52, 500, 1);
            margin: 0 0 25px 0;
        }

        &__subtitle {
            @include font(17, 500);
            margin-bottom: 25px;
        }
    }

    @include to(1024px) {

        &__title {
            @include font(42, 500, 1);
            margin: 0 0 25px 0;
        }

        &__subtitle {
            @include font(16, 500);
            margin-bottom: 25px;
        }

    }

    @include to(980px) {
        &__wrap {
            align-items: flex-start;
            padding-top: 106px;
            padding-left: 10px;
            padding-right: 10px;
        }

        &__blocks {
            flex-direction: column;
            justify-content: flex-start;
            gap: 50px;
        }

        &__block {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }

        &__block-img {
            img {
                width: 75svw;
            }
        }
    }
    
    @include to-h(568px) {
        &__wrap {
            padding-top: 85px;
        }
    
        &__blocks {
            gap: 30px;
        }

    }

    @include to(380px) {
        br {
            display: none;
        }
    }
}