.design-portfolio {
    overflow: hidden;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 120px 40px 120px;
    }

    &__head {
        margin-bottom: 115px;
        transition: 0.3s;
    }

    &__head-wrap {
        position: relative;
    }

    &__title {
        height: 140px;
        @include font(62, 500, 1);
        color: var(--gray-300);
        background-color: var(--white);

        &--paint-black {
            color: var(--black);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            overflow: hidden;
            white-space: nowrap;
            transition: .4s;
        }
    }

    &__block {
        padding-bottom: 120px;
        border-bottom: 1px solid var(--gray-300);

        &:not(:last-child) {
            margin-bottom: 90px;
        }
    }
}

.design-portfolio-gallery {
    width: 100%;
    height: 150vh;
    background-color: var(--black);
    overflow: hidden;
    position: relative;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        width: 100%;
        height: 6%;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.95) 0, rgba(255, 0, 0, 0) 88%);
        pointer-events: none;
    }

    &__bg-color-intro {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
        pointer-events: none;
        background-color: rgba(0, 0, 0, 0.5);
        transition: 1s;
    }

    &__wrap {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
    }

    &__inner {
        width: 100%;
        height: 100%;
        position: relative;
    }

    &__blocks {
        width: auto;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        gap: 35px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        transition: 1.8s;
    }

    &__block {
        &:nth-child(1) {
            transition: 4s;
        }

        &:nth-child(2) {
            transition: 1.4s;
        }

        &:nth-child(3) {
            transition: 3s;
        }

        &:nth-child(4) {
            transition: 0.8s;
        }

        &:nth-child(5) {
            transition: 1.4s;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 30px;
        transition: 1s;
    }

    &__item {
        width: 40vw;
        transition: .3s;
    }

    &__img {
        img {
            border-radius: 2px;
            width: 100%;
            height: auto;
            object-fit: contain;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        padding: 20px;
        cursor: default;
    }

    &__title {
        @include font(140, 500, 1);
        color: var(--white);
        margin: initial;
        transition: .3s;
    }
}

@include to(1536px) {

    .design-portfolio-gallery {
        &__blocks {
            gap: 25px;
        }

        &__items {
            gap: 23px;
        }
    }
}

@include to(1200px) {
    .design-portfolio {
        &__wrap {
            padding: 100px 20px 100px;
        }

        &__head {
            margin-bottom: 80px;
        }

        &__block {
            padding-bottom: 70px;

            &:not(:last-child) {
                margin-bottom: 70px;
            }
        }
    }

    .design-portfolio-gallery {
        height: 130svh;

        &__title {
            @include font(80, 500, 1);
        }
    }
}

@include to(1024px) {
    .design-portfolio-gallery {
        &__item {
            width: 54vw;
        }
    }
}

@include to(990px) {
    .design-portfolio-gallery {
        height: 100svh;

        &__blocks {
            gap: 18px;
        }

        &__items {
            gap: 16px;
        }
    }
}

@include to(768px) {
