        }

        &__head {
            margin-bottom: 60px;
        }

        &__title {
            height: 95px;
            @include font(42, 500, 1);
        }

        &__block {
            padding-bottom: 50px;

            &:not(:last-child) {
                margin-bottom: 50px;
            }
        }
    }

    .design-portfolio-gallery {
        &__title {
            @include font(65, 500, 1);
        }

        &__blocks {
            gap: 12px;
        }

        &__items {
            gap: 11px;
        }
    }
}

@include to(520px) {
    .design-portfolio {
        &__wrap {
            padding: 60px 10px 60px;
        }

        &__head {
            margin-bottom: 50px;
        }

        &__title {
            height: 72px;
            @include font(32, 500, 1);
        }

        &__block {
            padding-bottom: 40px;

            &:not(:last-child) {
                margin-bottom: 40px;
            }
        }
    }

    .design-portfolio-gallery {
        &__title {
            @include font(50, 500, 1);
        }

        &__blocks {
            gap: 7px;
        }

        &__items {
            gap: 6px;
        }
    }
}

@include to(450px) {
    .design-portfolio-gallery {
        &__item {
            width: 64vw;
        }
    }
}

@include to(414px) {
    .design-portfolio-gallery {
        &__title {
            @include font(42, 500, 1);
        }
    }
}