
.under-base-wrap
  // height: 100vh

.under-base-mark-for-scroll
  position: absolute
  top: 0
  left: 0

.under-base-fixed
  position: relative
  z-index: 1
  left: 0
  right: 0
  top: 0
  bottom: 0
  background: #000
  display: flex
  justify-content: center
  align-items: center
  height: 60vh

.under-base
  width: 100%
  color: #fff
  +to(520px)
    padding: 0 10px
  &-title
    @include font(92, 500, 1)
    margin: 20px 0 40px 65px
    text-transform: uppercase
    +to(520px)
      margin-left: 0
      text-align: center
  &-subtitle
    @include font(22, 500, 1)
    margin-left: 65px
    +to(520px)
      margin-left: 0
      text-align: center

@media only screen and (max-width: 1250px)
  .under-base-title,
  .under-base-subtitle
    margin-left: 30px

@media only screen and (max-width: 1023px)
  .under-base-title
    font-size: 52px

@media only screen and (max-width: 799px)
  .under-base-title
    font-size: 36px

.plan-work-wrap
  position: relative
  background: #fff
  &-in

.plan-work
  padding-top: 120px
  border-left: 3px solid #ccc
  position: relative
  margin-left: 70px

  &:after
    content: ''
    position: absolute
    z-index: 5
    bottom: -2px
    left: -5px
    width: 7px
    height: 400px
    background: #fff

  &-scale
    position: absolute
    z-index: 5
    top: 0
    left: -3px
    width: 3px
    height: 0px
    background: #000

  &-e-wrap
    position: relative

  &-e
    display: flex
    justify-content: space-between
    padding-bottom: 160px

  &-e-wrap:last-child &-e
    padding-bottom: 0px

  &-e-stage
    width: calc(100% - 750px)
    padding-right: 60px
    padding-left: 70px
    position: relative

  &-e-stage-days
    position: absolute
    left: -42px
    top: 53px
    transform-origin: 0 0
    transform: rotate(-90deg) translatex(-50%)
    @include font(22, 700, 1)

  &-e-stage-separate
    width: 35px
    height: 2px
    background: #ccc
    position: absolute
    left: 0
    top: 54px

  &-e-stage-separate:after
    content: ''
    position: absolute
    top: 0
    left: 0
    bottom: 0
    width: 0%
    background: #000

  &-e-stage-separate.active:after
    width: 100%

  &-e-stage-name
    @include font(52, 500, 1)

  &-e-stage-desc
    opacity: 0.3
    line-height: 1.4

  &-e-stage-desc-text
    margin: 30px 0 15px
    @include font(17)
    color: #6e6e6e

  &-e-stage-desc.active
    opacity: 1

  &-e-pic-wrap
    direction: rtl

  &-e-pic-wrap img
    width: 0
    height: 500px
    opacity: 0.3
    transition: 300ms

  &-e-pic-wrap.active img
    opacity: 1
    width: 100%

  &-e-wrap.e5
    padding-bottom: 120px

  &-e-wrap.e6
    padding-bottom: 60px

  &-e-want-start
    opacity: 0.3

  &-e-want-start.active
    opacity: 1

  &-e-want-start-desc
    margin-bottom: 20px

  &-e-stage
    .plan-work-e-pic-wrap
      display: none
      padding-left: 0

@media only screen and (max-width: 1399px)
  .plan-work-e-stage-name
    font-size: 45px
    line-height: 1

@media only screen and (max-width: 1250px)
  .plan-work
    margin-left: 30px

    &-e-stage-name

    &-e-stage
      width: 40%
      position: relative
      padding-right: 30px
      padding-left: 60px
      position: relative

    &-e-pic-wrap
      flex-shrink: 0
      width: 60%
      direction: rtl

@media only screen and (max-width: 1023px)
  .plan-work-e
    display: block
    padding-bottom: 90px

  .plan-work-e-stage,
  .plan-work-e-pic-wrap
    width: auto

  .plan-work-e-stage-name br
    display: none

  .plan-work-e-pic-wrap
    direction: ltr
    padding-left: 60px
    margin-top: 45px

    & img
      width: 100% !important
      height: auto !important
  
  .plan-work-e-pic-wrap
    display: none
  .plan-work-e-stage
    .plan-work-e-pic-wrap
      display: block

@media only screen and (max-width: 799px)
  .plan-work
    margin-left: 40px

    &-e-stage-name
      font-size: 42px

@media only screen and (max-width: 520px)
  .plan-work
    padding-top: 60px
    margin-left: 30px
    margin-bottom: 60px

    &-e
      padding-bottom: 60px

    &-e-wrap.e5
      padding-bottom: 5px

    &-e-stage-days
      font-size: 17px
      left: -32px

    &-e-stage-name
      font-size: 28px

    &-e-stage
      padding-right: 45px
      padding-left: 30px

    &-e-stage-separate
      width: 15px

    &-e-pic-wrap
      padding-left: 30px
      margin-top: 30px

    &-e-pic-wrap img
      opacity: 1
      transition: 0ms

    &-e-stage-desc
      opacity: 1
