.project-wrap {
    position: relative;
    z-index: 5;
    padding-bottom: 120px;
    background: #fff;
}

.project {
    margin: 0 auto;
}

.project-item.e1 {
    margin-bottom: 120px;
}

.project-item.e2 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.project-slider {
    width: 100%;
    max-width: 1090px;
}

.project-info {
    padding-top: 60px;
}

.project-info.e1 {
    max-width: 1090px;
    margin: 0 auto;
    padding-left: 60px;
}

.project-info.e2 {
    width: 100%;
    max-width: 1090px;
}

.project-info-title {
    font-size: 40px;
}

.project-info-list {
    margin: 20px 0 40px;
    padding-left: 33px
}

.project-info-list li {
    list-style-type: decimal-leading-zero;
    list-style-position: outside;
    padding-left: 10px;
}

.project-info-column {
    column-count: 2;
    column-gap: 30px;
    max-width: 1000px;
    margin: 0 auto;
    font-size: 16px;
}



.we-care-wrap {
    height: 500px;
}

.we-care-fixed {
    position: fixed;
    z-index: 2;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.we-care {
    width: 100%;
    max-width: 1200px;
    padding: 200px 0;
    color: #fff;
}

.we-care-title {
    font-size: 60px;
    margin: 20px 0 40px;
}

/*======================================================*/


.bot-wrap {
    position: relative;
    z-index: 5;
    padding-bottom: 120px;
    background: #fff;
}

.bot {
    width: 1280px;
    margin: 0 auto;
}