.section-about-gallery {
    height: 600px;
    padding-right: 40px;
    padding-left: 40px;
    overflow: hidden;
    &__inner {
        width: 100%;
        display: flex;
        overflow: hidden;
    }
    .swiper-slide {
        width: fit-content;
    }
    img {
        max-width: none;
    }
}

@include to(1023px) {
    .section-about-gallery {
        padding-left: 20px;
        padding-right: 20px;
    }
}

@include to(799px) {
    .section-about-gallery {
        padding-left: 10px;
        padding-right: 10px;
    }
}