.section-main-direction {
    padding: 100px 0 120px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        @include font(92, 500, 1);
        margin-bottom: 20px;
        transition: .3s
    }

    &__subtitle {
        width: 50%;
        @include font(20, 500);
        transition: .3s
    }

    &__head {
        margin-bottom: 80px;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(12, 1fr);
        gap: 45px;
        transition: .3s
    }

    &__block {
        grid-column: auto/span 6;
        margin-bottom: 52px;
    }

    &__block-img {
        margin-bottom: 24px;

        img {
            width: 100%;
            height: auto;
            object-fit: contain;
        }
    }

    &__block-title {
        @include font(36, 500);
        margin-bottom: 16px;
        transition: .3s
    }

    &__block-subtitle {
        @include font(18, 400);
        transition: .3s
    }

    @include to(1200px) {
        padding: 60px 0 70px;

        &__wrap {
            padding: 0 20px;
        }

        &__head {
            margin-bottom: 60px;
        }

        &__title {
            @include font(62, 500);
        }

        &__subtitle {
            max-width: 500px;
            width: 100%;
            @include font(16, 500);
        }

        &__blocks {
            gap: 25px;
        }

        &__block {
            margin-bottom: 34px;
        }
    }

    @include to(992px) {
        &__blocks {
            gap: 20px;
        }

        &__block {
            margin-bottom: 24px;
        }

        &__block-img {
            margin-bottom: 16px;
        }

        &__block-title {
            @include font(32, 500);
            margin-bottom: 8px;
        }

        &__block-subtitle {
            @include font(16, 400);
        }
    }

    @include to(768px) {
        padding: 50px 0 65px;

        &__wrap {
            padding: 0 10px;
        }

        &__head {
            margin-bottom: 42px;
        }

        &__title {
            @include font(42, 500);
            margin-bottom: 16px;
        }

        &__blocks {
            gap: 10px;
        }

        &__block-img {
            margin-bottom: 12px;
        }

        &__block-title {
            @include font(25, 500);
            margin-bottom: 6px;
        }

        &__block-subtitle {
            @include font(16, 400);
        }
    }

    @include to(520px) {
        &__title {
            @include font(32, 500);
        }

        &__subtitle {
            @include font(16, 400);
        }

        &__block {
            grid-column: auto/span 12;
        }
    }
}
