.header {
    position: absolute;
    z-index: 1003;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
    padding-top: 10px;
    z-index: 10;
}

.header-e {
    display: flex;
    align-items: center;
}

.header-e {
    &.e1 {
        width: 170px;
    }

    & .logo {
        // img {
        //     width: 100%;
        // }

        svg {
            display: flex;
            color: var(--white);
            width: 100%;
            aspect-ratio: 170 / 57;
        }
    }

    &.e2 {
        li {
            margin-right: 40px;
        }

        li:last-child {
            margin-right: 0;
        }
    }
}

.header-tel-time {
    margin-right: 40px;
}

.header {
    &.fixed {
        position: fixed;
        padding-top: 10px;
        padding-bottom: 10px;
        background: #000;
    }
}

.header-hamburget {
    display: none;
}

.header-time {
    font-size: 8px;
    text-transform: none;
    margin-top: 3px;
}

.header-white-fon {
    .header:not(.fixed) {
        * {
            color: var(--black);
        }
        
        .header-hamburget-line {
            border-top: 2px solid var(--black);
        }
    
        .but1-text {
            color: var(--white);
        }
    
        .but1 {
            background: var(--black);
    
            &:hover {
                .but1-text {
                    color: var(--black);
                }
            }
        }
    }
}

