.section-project {
    padding: 192px 0 120px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__head {
        width: 100%;
        height: 84dvh;
        overflow: hidden;
        position: relative;
        margin-bottom: 10px;
    }

    &__img {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
        transform: scale(1);
        transition: .7s;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center bottom;
            transform: scale(1);
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            z-index: 1;
            width: 100%;
            height: 28%;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.72) 0, rgba(255, 0, 0, 0) 100%);
            transition: .3s;
        }
    }

    .js-scroll-modify-color {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-color: rgba(black, 0.05);
        transition: 1s;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
    }

    &__blocks {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: repeat(20, 1fr);
        grid-gap: 10px;
        padding: 30px 32px;
    }

    &__block {
        &:nth-child(2) {
            grid-column: auto/span 10;
        }

        &:nth-child(1),
        &:nth-child(3) {
            grid-column: auto/span 5;
            margin-top: 2px;
        }

        &:nth-child(3) {
            margin-left: auto;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        transition: .3s;

        &-circle {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            backdrop-filter: blur(12px);
            -webkit-backdrop-filter: blur(12px);
            background-color: rgba(white, .25);
            border: 1px solid rgba(white, .6);

            svg {
                width: 16px;
                height: 16px;
                scale: 1;
                transition: .3s;
            }
        }

        &-title {
            width: 100%;
            height: 22px;
            overflow: hidden;

            span {
                display: block;
                @include font(14, 500, 1);
                text-transform: uppercase;
                color: var(--white);
                margin-left: 12px;
                transition: .3s;

                &:nth-child(1) {
                    margin-bottom: 4px;
                }
            }
        }

        &-wrap {
            transform: translateY(18%);
            transition: .3s;
        }

        &:hover {
            .section-project__btn-circle {
                svg {
                    scale: 0.88;
                }
            }

            .section-project__btn-wrap {
                transform: translateY(-39%);
            }
        }
    }

    &__block-title {
        text-align: center;
        @include font(45, 500, 1);
        letter-spacing: 0.3rem;
        text-transform: uppercase;
        color: var(--white);
        transition: .3s;
    }

    &__breadcrumbs {
        text-align: center;
        width: auto;
        height: auto;
        padding: 10px 20px;
        border-radius: 50px;
        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px);
        background-color: rgba(white, .25);
        border: 1px solid rgba(white, .6);

        span {
            display: inline-block;
            @include font(12, 500);
            text-transform: uppercase;
            color: var(--white);
            border-bottom: 1px solid transparent;
            word-wrap: break-word;
            word-break: break-all;
            transition: .15s;

            a {
                display: block;
            }

            &:not(:first-child) {
                margin-left: 10px;
                position: relative;

                &::before {
                    content: '';
                    background-image: url(../img/sprites/multi.svg#icon-minus-sm);
                    background-size: 6px;
                    background-position: center center;
                    background-repeat: no-repeat;
                    position: absolute;
                    width: 6px;
                    height: 12px;
                    color: white;
                    top: 0;
                    left: -10px;
                    z-index: 1;
                }
            }

            &:not(:last-child) {
                &:hover {
                    border-bottom: 1px solid var(--white);
                }
            }
        }
    }

    &__more {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 32px 32px 62px;
    }

    &__more-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px);
        background-color: rgba(white, .2);
        transition: 0.3s;
        cursor: pointer;

        svg {
            width: 16px;
            height: 16px;
            scale: 1;
            transition: .3s;
        }

        &:hover {
            .section-project-more__wrap {
                border-color: var(--white);
            }
        }
    }

    .section-project-more__wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        overflow: hidden;
        border-radius: 50px;
        border-style: solid;
        border-width: 1px;
        border-color: transparent;
        transition: .3s;
    }

    .section-project-more__anim {
        height: 60px;
        transform: translateY(-19%);
        animation-name: animArrowDownMoreBtn;
        animation-duration: 1.3s;
        animation-iteration-count: infinite;
        transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
    }

    .section-project-more__icon {
        display: block;

        svg {
            display: flex;
            width: 22px;
            height: 22px;
        }

        &:nth-child(1) {
            margin-bottom: 7px;
        }
    }

    @keyframes animArrowDownMoreBtn {
        0% {
            transform: translateY(-19%);
        }

        90% {
            transform: translateY(29%);
        }

        100% {
            transform: translateY(29%);
        }
    }

    &__description {
        padding: 80px 0 80px;
    }

    &__title {
        @include font(62, 500, 1);
        margin: 0 0 48px;
        transition: .3s;
    }

    &__tags {
        margin-bottom: 45px;
    }

    &__tags-list {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        gap: 13px 8px;

        li {
            a {
                display: block;
                color: var(--black);
                @include font(15, 500);
                padding: 4px 11px;
                border-radius: 50px;
                border: 1px solid var(--black);
            }
        }
    }

    &__text {
        max-width: 700px;
        width: 100%;
        margin-bottom: 50px;

        p {
            @include font(17, 400);
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__list-item {
        display: flex;
        align-items: center;
        @include font(17, 400);
    }

    &__list-type {
        max-width: 140px;
        width: 100%;
        color: var(--gray-800);
    }


    &__list-detail {
        margin-left: 25px;
    }

    &__preview {
        display: flex;
        flex-direction: column;
        gap: 16px; 
        &-line {
            width: 100%;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 16px 16px; 
            height: 900px;
            @include to(768px) {
                height: 600px;
            }
            img {
                object-fit: cover;
                width: 100%;
                height: inherit;
            }
            &--2 {
                img {
                    width: calc(100% / 2 - (16px / 2));
                    @include to(768px) {
                        width: 100%;
                    }
                }
            }
            &--3 {
                img {
                    width: calc(100% / 3 - (16px * 2 / 3));
                    @include to(768px) {
                        width: 100%;
                    }
                }
            }
            &--4 {
                img {
                    width: calc(100% / 4 - (16px * 3 / 4));
                    @include to(992px) {
                        width: calc(100% / 2 - (16px / 2));
                    }
                    @include to(768px) {
                        width: 100%;
                    }
                }
            }
        }
    }

    @include to(1200px) {
        padding: 162px 0 100px;

        &__blocks {
            grid-template-columns: repeat(18, 1fr);
            padding: 21px 20px;
        }

        &__block {
            &:nth-child(2) {
                grid-column: auto/span 8;
            }

            &:nth-child(1),
            &:nth-child(3) {
                grid-column: auto/span 5;
            }
        }

        &__img {
            &::before {
                height: 24%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0, rgba(255, 0, 0, 0) 99%);

            }
        }

        &__breadcrumbs {
            padding: 9px 18px;
        }

        &__more {
            padding: 20px 20px 42px;
        }

        &__more-btn {
            width: 56px;
            height: 56px;
        }

        &__wrap {
            padding: 0 20px;
        }

        &__title {
            margin-bottom: 42px;
        }

        &__tags {
            margin-bottom: 42px;
        }

        &__text {
            margin-bottom: 45px;
        }

        &__description {
            padding: 60px 0 70px;
        }
    }

    @include to(1194px) {
        &__blocks {
            grid-template-columns: repeat(10, 1fr);
        }

        &__block {
            &:nth-child(2) {
                grid-column: auto/span 4;
            }

            &:nth-child(1),
            &:nth-child(3) {
                grid-column: auto/span 3;
            }
        }
    }

    @include to(1094px) {
        &__blocks {
            grid-template-columns: repeat(13, 1fr);
        }

        &__block {
            &:nth-child(2) {
                grid-column: auto/span 5;
            }

            &:nth-child(1),
            &:nth-child(3) {
                grid-column: auto/span 4;
            }
        }

        &__block-title {
            @include font(42, 500, 1);
        }
    }

    @include to(768px) {
        padding: 110px 0 40px;

        &__head {
            height: 70svh;
            margin-bottom: 0;
        }

        &__blocks {
            grid-template-rows: initial;
            grid-template-columns: initial;
            grid-gap: initial;
            display: flex;
            flex-direction: column;
            padding: 12px 10px;
        }

        &__block {
            &:nth-child(1),
            &:nth-child(3) {
                margin-top: initial;
            }

            &:nth-child(2) {
                order: 2;
            }

            &:nth-child(3) {
                margin-right: auto;
                order: 1;
            }
        }

        &__btn {
            display: none;
        }

        &__img {
            &::before {
                height: 29%;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0, rgba(255, 0, 0, 0) 96%);
            }
        }

        &__breadcrumbs {
            padding: 5px;
            -webkit-backdrop-filter: initial;
            backdrop-filter: initial;
            background-color: transparent;
            border: initial;
            margin-bottom: 10px;
            
            span {
                @include font(12, 400);
            }
        }

        &__more {
            bottom: 0;
            padding: 10px 10px 28px;
        }

        &__more-btn {
            width: 50px;
            height: 50px;

            svg {
                width: 14px;
                height: 14px;
            }
        }

        .section-project-more__wrap {
            width: 35px;
            height: 35px;
        }

        &__block-title {
            @include font(36, 500, 1);
        }

        &__wrap {
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500, 1);
            margin-bottom: 34px;
        }

        &__tags {
            margin-bottom: 32px;
        }

        &__text {
            margin-bottom: 34px;
        }

        &__list {
            gap: 6px;
        }

        &__list-item {
            @include font(16, 400);
        }

        &__list-detail {
            margin-left: 22px;
        }

        &__description {
            padding: 40px 0 54px;
        }
    }

    @include to(520px) {
        &__title {
            @include font(32, 500, 1);
            margin-bottom: 24px;
        }

        &__tags {
            margin-bottom: 24px;
        }

        &__tags-list {
            gap: 10px 8px;

            li {
                a {
                    @include font(12, 500);
                }
            }
        }

        &__text {
            margin-bottom: 30px;
        }

        &__list-detail {
            margin-left: 14px;
        }

        &__description {
            padding: 30px 0 42px;
        }

    }
}