.price-project-wrap
  padding-top: 120px
  position: relative
  background: #fff

.price-project

  &-title
    @include font(92,500,1)
    text-transform: uppercase
    margin-bottom: 60px

  &-cont
    display: flex
    justify-content: space-between

  &-cont-left
    width: 490px

  &-cont-right
    width: calc(100% - 610px)

  &-cont-plate
    width: 100%
    padding: 50px 60px
    border-radius: 20px
    border: 1px solid #000

  &-cont-plate-title
    @include font(40, 500, 1)
    margin-bottom: 20px
    text-align: center

  &-cont-plate-day
    @include font(15)
    text-align: center
    margin-bottom: 30px
    color: #6e6e6e

  &-cont-plate-sep.e1
    border-top: 1px solid #a6a6a6
    margin: 0 auto
    max-width: 300px

  &-cont-plate-sep.e2
    border-top: 1px solid #a6a6a6
    margin: 0 auto 30px
    max-width: 50px

  &-cont-plate-desc
    text-align: center
    margin: 30px 0

  &-cont-plate-desc-name
    font-weight: 500
    color: #6e6e6e
    margin-bottom: 10px

  &-cont-plate-desc-sum
    @include font(52)

  &-cont-plate-cont
    margin: 60px 0
    font-size: 17px

  &-cont-plate-cont-title
    @include font(20)
    margin-bottom: 15px

  &-cont-plate-cont-item
    padding: 5px 0 5px 24px
    position: relative
    @include font(16)

  &-cont-plate-cont-item:before
    content: ''
    display: flex
    width: 22px
    height: 16px
    background: url(../img/sprites/multi.svg#icon-price-list-mark) no-repeat
    background-size: 30px
    position: absolute
    top: 11px
    left: -6px

  &-cont-plate-but
    width: 100%
    height: 43px
    line-height: 43px
    background-color: #121315
    position: relative
    cursor: pointer

  &-cont-plate-but:before
    content: ''
    position: absolute
    z-index: 1
    left: 0
    top: 0
    bottom: 0
    width: 0%
    transition: 300ms
    background: #ddd

  &-cont-plate-but:hover:before
    width: 100%

  &-cont-plate-but-text
    position: absolute
    z-index: 2
    top: 0
    bottom: 0
    left: 0
    right: 0
    color: #fff
    text-align: center
    font-size: 12px
    font-weight: 500
    letter-spacing: 0.1em
    text-transform: uppercase
    transition: 300ms

  &-cont-plate-but-text:hover
    color: #000

  &-cont-more-title
    @include font(36, 500, 1)
    padding-top: 52px
    margin-bottom: 89px

  &-cont-more-item-wrap
    padding-top: 25px
    border-top: 1px solid #a6a6a6

  &-cont-more-item
    padding-bottom: 25px
    border-bottom: 1px solid #a6a6a6
    margin-bottom: 30px

  &-cont-more-item:last-child
    margin-bottom: 0

  &-cont-more-item-title
    @include font(18)
    position: relative
    cursor: pointer
    padding-right: 40px

  &-cont-more-item-title:after
    content: '+'
    position: absolute
    top: -1px
    right: 10px
    font-size: 22px

  &-cont-more-item-title.active:after
    content: '-'

  &-cont-more-item-desc
    margin-top: 20px
    line-height: 1.4
    display: none

  &-cont-more-item-desc ul li
    font-weight: 500
    margin-bottom: 10px
    position: relative
    padding-left: 16px

  &-cont-more-item-desc ul li:before
    content: ''
    width: 6px
    height: 6px
    border-radius: 100%
    background: #000
    position: absolute
    top: 8px
    left: 0

  &-cont-more-item-desc ul li:last-child
    margin-bottom: 0

@media only screen and (max-width: 1399px)
  .price-project-cont-right
    width: calc(100% - 550px)

@media only screen and (max-width: 1023px)
  .price-project-title
    font-size: 52px
    text-align: center

  .price-project-cont
    display: block

    &-left
      width: 100%
      max-width: 490px
      margin: 0 auto

    &-right
      width: auto

    &-more-title
      padding-top: 90px
      margin-bottom: 45px

@media only screen and (max-width: 799px)
  .price-project-title
    font-size: 42px

  .price-project-cont-more-title
    padding-top: 60px

@media only screen and (max-width: 599px)
  .price-project-wrap
    padding-top: 60px

  .price-project-title
    font-size: 40px
    margin-bottom: 45px

  .price-project-cont-plate
    width: 100%
    padding: 30px 30px
    border-radius: 20px
    border: 1px solid #000

    &-cont
      margin: 60px 0 10px
