.section-start {
    padding: 90px 0 90px;

    &__wrap {
        max-width: 1480px;
        width: 100%;
        margin: 0 auto;
        padding: 0 40px;
    }

    &__title {
        max-width: 936px;
        width: 100%;
        @include font(62, 500);
        margin-bottom: 34px;
        transition: 0.3s;
    }

    &__form {
        position: relative;
        max-width: 650px;
        width: 100%;
        margin-bottom: 22px;
    }

    &__input {
        width: 100%;
        border-radius: 90px;
        border: 1px solid var(--gray-300);
        padding: 20px 30px;
        @include font(18, 400);

        &::placeholder {
            color: var(--gray-400);
        }
    }

    &__form-wrap {
        position: absolute;
        top: 50%;
        right: 6px;
        transform: translateY(-50%);
        z-index: 1;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        height: 52px;
        border-radius: 90px;
        overflow: hidden;
        @include font(12, 500, 1);
        letter-spacing: 0.1em;
        text-transform: uppercase;
        background-color: var(--black);
        color: var(--white);
        cursor: pointer;
        transition: 0.3s;
        position: relative;
        z-index: 1;

        span {
            z-index: 5;
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 0;
            left: 0;
            top: 0;
            bottom: 0;
            width: 0%;
            background-color: var(--gray-200);
            transition: 0.3s;
        }

        &:hover {
            color: var(--black);


            &::before {
                width: 100%;
            }
        }
    }

    &__stages {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        &-item {
            display: flex;
            align-items: center;
            @include font(15, 400);
            padding: 0 14px;
            margin-bottom: 14px;

            svg {
                width: 12px;
                height: 12px;
                color: var(--black);
                margin-right: 5px;
            }

            &:not(:last-child) {
                border-right: 1px solid var(--gray-300);
            }
        }
    }

    @include to(1060px) {
        padding: 60px 0 60px;
        
        &__wrap { 
            padding: 0 20px;
        }

        &__title {
            @include font(62, 500);
        }
    }

    @include to(768px) {
        padding: 50px 0 50px;
        
        &__wrap { 
            padding: 0 10px;
        }

        &__title {
            @include font(42, 500);
        }
    }

    @include to(670px) {
        &__title {
            max-width: 564px;
            @include font(32, 500);
        }

        &__stages {
            display: none;
        }
    }

    @include to(500px) {
        &__input {
            padding: 16px 20px;
        }

        &__btn {
            width: 184px;
            height: 46px;
        }

        &__form-wrap {
            right: 5px;
        }
    }

    @include to (415px) {
        &__input {
            padding: 17px 16px;
            @include font(16, 400);
        }
    }

    @include to (386px) {
        &__btn {
            width: 140px;
            padding: 0 5px;
        }

        &__form-wrap {
            right: 4px;
        }
    }
}